/*
// .th-widget-posts
*/

.th-widget-posts {}
.th-widget-posts__list {
    list-style: none;
    padding: 0;
    margin: 0;
}
.th-widget-posts__item {
    display: flex;

    & + & {
        margin-top: 1rem;
    }
}
.th-widget-posts__image {
    flex-shrink: 0;
    position: relative;

    @include th-overlay-image-link;
    @include direction {
        #{$margin-inline-end}: 14px;
    }

    img {
        display: block;
        border-radius: 1.5px;
        width: 70px;
        height: 70px;
    }
}
.th-widget-posts__info {
    padding-top: 5px;
}
.th-widget-posts__name {
    $local-line-height: 19px;

    font-size: 15px;
    line-height: $local-line-height;
    overflow: hidden;
    max-height: $local-line-height * 2;

    a {
        color: inherit;
        transition: color .12s;
    }
    a:hover {
        @include th-var(color, --th-link-color, $link-color);
    }
}
.th-widget-posts__date {
    font-size: 13px;
    color: map_get($card-scheme, muted);
    margin-top: 3px;

    @include th-decor-line(16px);
}
