/*
// .th-layout--page--blog
*/

.th-layout--page--blog {
    .th-layout__body {
        display: flex;
        justify-content: center;
    }

    .th-layout__item--content {
        flex-grow: 1;
    }

    .th-layout__item--sidebar {
        width: 332px;
        flex-shrink: 0;
    }

    .th-layout__item + .th-layout__item {
        @include direction {
            #{$margin-inline-start}: 48px;
        }
    }
}

.th-layout--variant--blog-classic {
    .th-posts-view {
        --th-posts-view--gutter: 40px;
        --th-posts-view--columns: 1;
    }
    .th-layout__item--content {
        //max-width: 730px;
        max-width: 970px;
    }
}

.th-layout--variant--blog-list {
    .th-posts-view {
        --th-posts-view--gutter: 28px;
        --th-posts-view--columns: 1;
    }
    .th-layout__item--content {
        max-width: 970px;
    }
}

.th-layout--variant--blog-grid {
    .th-posts-view {
        --th-posts-view--columns: 1;
        --th-posts-view--gutter: 28px;
    }

    @include media-breakpoint-up(md) {
        .th-posts-view {
            --th-posts-view--columns: 2;
        }
    }
    @include media-breakpoint-up(lg) {
        &:not(.th-layout--has-sidebar) {
            .th-posts-view {
                --th-posts-view--columns: 3;
            }
        }
    }
}

.th-layout--page--blog {
    @include media-breakpoint-only(lg) {
        .th-layout__item--sidebar {
            width: 290px;
        }
        .th-layout__item + .th-layout__item {
            @include direction {
                #{$margin-inline-start}: 36px;
            }
        }
    }
    @include media-breakpoint-down(md) {
        .th-layout__body {
            flex-direction: column;
        }
        .th-layout__item--sidebar {
            width: 100%;
            margin-top: 48px;
            order: 1;
        }
        .th-layout__item + .th-layout__item {
            @include direction {
                #{$margin-inline-start}: 0;
            }
        }
    }
}

/*
// .th-layout--page--post
*/

.th-layout--page--post {
    .th-layout__body {
        display: flex;
        justify-content: center;
        position: relative;
    }

    .th-layout__item {
        flex-basis: 0;
        min-width: 0;
    }

    .th-layout__item--content {
        flex-grow: 1;
        max-width: 760px;
    }

    .th-layout__item--sidebar {
        flex-basis: 332px;
        width: 332px;
        flex-shrink: 0;
    }

    .th-layout__item + .th-layout__item {
        @include direction {
            #{$margin-inline-start}: 48px;
        }
    }

    @include media-breakpoint-only(lg) {
        .th-layout__item--sidebar {
            flex-basis: 290px;
            width: 290px;
        }
        .th-layout__item + .th-layout__item {
            @include direction {
                #{$margin-inline-start}: 36px;
            }
        }
    }
    @include media-breakpoint-down(md) {
        .th-layout__body {
            flex-direction: column;
        }
        .th-layout__item--sidebar {
            flex-basis: 100%;
            width: 100%;
            margin-top: 20px;
            order: 1;
        }
        .th-layout__item + .th-layout__item {
            @include direction {
                #{$margin-inline-start}: 0;
            }
        }
    }
}

/*
// .th-layout--page--shop
*/

.th-layout--page--shop {
    .th-layout__body {
        display: flex;
        justify-content: center;
        position: relative;
    }

    .th-layout__item--content {
        flex-grow: 1;
        flex-basis: 0;
    }

    .th-layout__item--sidebar {
        width: 270px;
        flex-shrink: 0;
    }

    .th-layout__item + .th-layout__item {
        @include direction {
            #{$margin-inline-start}: 40px;
        }
    }

    @include media-breakpoint-between(lg, xl) {
        .th-layout__item--sidebar {
            width: 260px;
        }
        .th-layout__item + .th-layout__item {
            @include direction {
                #{$margin-inline-start}: 32px;
            }
        }
    }
    @include media-breakpoint-down(md) {
        .th-layout__body {
            flex-direction: column;
        }
        .th-layout__item--sidebar {
            width: 100%;
            margin: 0;
            order: 1;
        }
        .th-layout__item + .th-layout__item {
            @include direction {
                #{$margin-inline-start}: 0;
            }
        }
    }
}

/*
// .th-layout--page--product
*/

.th-layout--page--product {
    --th-sidebar-width: 0px;
    --th-sidebar-gutter: 0px;

    .th-layout__body {
        display: flex;
        justify-content: center;
        position: relative;
    }
    .th-layout__item--content {
        flex-grow: 1;
        flex-basis: 0;
        max-width: calc(100% - var(--th-sidebar-width) - var(--th-sidebar-gutter));
    }
    .th-layout__item--sidebar {
        width: var(--th-sidebar-width);
        flex-shrink: 0;
    }
    .th-layout__item + .th-layout__item {
        @include direction {
            #{$margin-inline-start}: var(--th-sidebar-gutter);
        }
    }

    &.th-layout--has-sidebar {
        @include media-breakpoint-up(xxl) {
            --th-sidebar-width: 270px;
            --th-sidebar-gutter: 40px;
        }
        @include media-breakpoint-between(lg, xl) {
            --th-sidebar-width: 260px;
            --th-sidebar-gutter: 32px;
        }

        @include media-breakpoint-down(md) {
            .th-layout__body {
                flex-direction: column;
            }
            .th-layout__item--sidebar {
                width: 100%;
                order: 1;
                margin-top: 40px;
            }
        }
    }
}


