/*
// .th-widget-aboutus
*/

.th-widget-aboutus {
    text-align: center;
    margin-top: -4px;

    .th-widget-aboutus__title {
        font-size: 24px;
        font-weight: 500;
        margin: 12px 0 16px;
    }
    .th-widget-aboutus__text {
        font-size: 15px;
        line-height: 26px;
        max-width: 260px;
        margin: 0 auto;
    }
    .th-widget-aboutus__socials {
        position: relative;
        margin-top: 41px;

        &:before {
            content: '';
            position: absolute;
            display: block;
            top: -21px;
            left: calc(50% - 28px);
            height: 1px;
            width: 56px;
            opacity: .2;
            background-color: #6c757d;
        }

        ul {
            justify-content: center;
        }
    }
    .th-widget-aboutus__socials-caption {
        font-size: 14px;
        color: #6c757d;
        margin-top: 16px;
        margin-bottom: 8px;
    }
}
