/*
// .th-site-footer
*/

.th-site-footer {
    position: relative;
    background-color: map_get($footer-scheme, main);
    color: map_get($footer-scheme, muted);
}
.th-site-footer__decor {
    position: absolute;
    bottom: calc(100% - 1px);

    @include th-decor-style(map_get($footer-scheme, main));
}
.th-site-footer__widgets {
    padding: 56px 0 50px;

    .th-row {
        margin-top: -44px;
    }
    .th-row > * {
        margin-top: 44px;
    }
}
.th-site-footer__widget-title {
    font-size: 20px;
    color: map_get($footer-scheme, opposite);
    margin-bottom: 26px;
    line-height: 1.2;

    @include scheme-font-weight($footer-scheme, opposite, $font-weight-medium);
}
.th-site-footer__bottom {
    background-color: map_get($footer-copyright-scheme, main);
    font-size: 14px;
    color: map_get($footer-copyright-scheme, muted);

    @include scheme-font-weight($footer-copyright-scheme, muted, $font-weight-normal);

    a {
        color: map_get($footer-copyright-scheme, opposite);

        @include scheme-font-weight($footer-copyright-scheme, opposite, $font-weight-medium);
    }
}
.th-site-footer__bottom-row {
    display: flex;
    height: 72px;
    justify-content: space-between;
    align-items: center;
}


@include media-breakpoint-between(xs, lg) {
    .widget_redparts_sputnik_footer_contacts .th-site-footer__widget-title {
        text-align: center;
        font-size: 24px;
        margin-bottom: 12px;
    }
}
@include media-breakpoint-down(sm) {
    .th-site-footer__decor {
        display: none;
    }
    .th-site-footer__widgets {
        padding: 40px 0 36px;
    }
    .th-site-footer__bottom-row {
        height: auto;
        flex-direction: column;
        padding: 20px 0 24px;
    }
    .th-site-footer__payments {
        margin-top: 12px;
    }

    .widget_redparts_sputnik_footer_newsletter {
        .th-site-footer__widget-title {
            text-align: center;
            font-size: 24px;
            margin-bottom: 16px;
        }
    }
}
