/*
// .widget_tag_cloud
*/

.widget_tag_cloud {
    $local-gutter: 4px;

    .tagcloud {
        display: flex;
        flex-wrap: wrap;
        font-weight: $font-weight-medium;
        margin: -($local-gutter / 2);
    }
    .tag-cloud-link {
        display: block;
        margin: ($local-gutter / 2);
        background: $tag-normal-bg-color;
        padding: 4px 11px;
        color: inherit;
        border-radius: 1.5px;
        transition: background .12s;
        font-size: 13px !important;

        &:hover {
            color: inherit;
            background: $tag-hover-bg-color;
        }
        &:active {
            transition-duration: 0s;
            background: $tag-active-bg-color;
        }
    }

    .th-site-footer & {
        .tag-cloud-link {
            color: #ababab;
            background: #404040;

            &:hover {
                background: #4c4c4c;
            }
            &:active {
                background: #404040;
            }
        }
    }
}
