/*
// .th-teammate
*/

.th-teammate {
    @include th-card;

    padding: 24px;
    border-radius: 2px;
}
.th-teammate__avatar {
    position: relative;

    img {
        border-radius: 2px;
    }
}
.th-teammate__info {
    margin-top: 20px;
}
.th-teammate__name {
    font-size: 17px;
    font-weight: $font-weight-medium;
}
.th-teammate__position {
    position: absolute;
    font-size: 12px;
    line-height: 20px;
    bottom: 10px;
    padding: 1px 7px;
    border-radius: 2px;
    z-index: 0;

    @include th-var(color, --th-theme-scheme-opposite, map_get($theme-scheme, opposite));
    @include direction {
        #{$inset-inline-end}: -8px;
    }

    &:before {
        $local-expand: 4px;

        content: '';
        z-index: -1;
        position: absolute;
        left: -$local-expand; // SKIP-RTL
        right: -$local-expand; // SKIP-RTL
        top: 0;
        bottom: 0;
        border-radius: 2.5px;
        transform-origin: center;

        @include th-var(background-color, --th-theme-scheme-main, map_get($theme-scheme, main));
        @include direction {
            transform: skewX(#{-20deg * $transform-direction});
        }
    }
}
.th-teammate__description {
    margin-top: 4px;
    font-size: 14px;
    color: map_get($card-scheme, opposite-alt);
}
.th-teammate__contacts {
    margin-top: 16px;

    ul {
        list-style: none;
        padding: 0;
        margin: -8px 0 0;
        display: flex;
        flex-wrap: wrap;

        @include direction {
            #{$margin-inline-start}: -8px;
        }
    }
    li {
        margin: 8px 0 0;

        @include direction {
            #{$margin-inline-start}: 8px;
        }
    }
    a {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 12px;
        width: 28px;
        height: 28px;
        border-radius: 100%;
        color: rgba(#262626, .8);
        background: rgba(#000, .06);
        transition:
            color .12s,
            background-color .12s;

        &:hover {
            background: rgba(#000, .1);
            //color: #fff;
            //background: rgba(#262626, .8);
        }
    }
}
