/*
// .widget_products
*/

.widget_products {
    $local-image-size: 60px;
    $local-image-expand: 2px;

    ul {
        list-style: none;
        padding: 0;
        margin: 0;
    }
    li {
        margin: 0;
        padding: $local-image-expand 0;

        &:after {
            display: block;
            content: '';
            width: 100%;
            clear: both;
        }
    }
    li + li {
        margin-top: 14px;
    }

    img {
        margin: (-$local-image-expand) 0;
        width: $local-image-size;
        height: auto;
        border-radius: 2px;
        box-shadow: 0 0 0 1px rgba(#000, .08);

        @include direction {
            float: $inline-start;
        }
    }

    a {
        color: inherit;
        transition: color .12s;
    }
    a:hover {
        @include th-var(color, --th-link-color, $link-color);
    }

    a > span {
        $local-line-height: 17px;

        font-size: 14px;
        line-height: $local-line-height;
        max-height: $local-line-height * 2;
        overflow: hidden;
    }
    li > span {
        padding-top: 2px;
        font-size: 14px;
        font-weight: $font-weight-medium;
        margin-bottom: -1px;
    }

    .star-rating {
        margin-top: 3px;
    }

    a > span,
    li > span,
    .star-rating {
        display: block;

        @include direction {
            #{$margin-inline-start}: ($local-image-size + 14px);
        }
    }
}
