/*
// .widget_media_gallery
*/

.widget_media_gallery {
    $local-gutter: 8px;

    .gallery {
        margin: -$local-gutter / 2;
        display: flex;
        flex-wrap: wrap;

        img {
            max-width: 100%;
            height: auto;
            border-radius: 2px;
        }
    }
    .gallery-item {
        margin: $local-gutter / 2;
    }

    @for $i from 1 through 9 {
        .gallery-columns-#{$i} {
            .gallery-item {
                width: calc((100% - #{$local-gutter * $i}) / #{$i});
            }
            .gallery-item:nth-child(n) {
                margin-left: $local-gutter / 2; // SKIP-RTL
            }
            .gallery-item:nth-child(#{$i}n + 1) {
                margin-left: $local-gutter / 2; // SKIP-RTL
            }
            .gallery-item:nth-child(n+#{$i + 1}) {
                margin-top: $local-gutter / 2;
            }
        }
    }

    @include media-breakpoint-down(md) {
        @for $i from 1 through 9 {
            .gallery-columns-#{$i} {
                .gallery-item {
                    margin: $local-gutter / 2;
                    width: calc((100% - #{$local-gutter * 2}) / 2);
                }
                .gallery-item:nth-child(n) {
                    margin-left: $local-gutter / 2; // SKIP-RTL
                }
                .gallery-item:nth-child(2n-1) {
                    margin-left: $local-gutter / 2; // SKIP-RTL
                }
                .gallery-item:nth-child(n+3) {
                    margin-top: $local-gutter / 2;
                }
            }
        }
    }
    @include media-breakpoint-down(sm) {
        @for $i from 1 through 9 {
            .gallery-columns-#{$i} {
                .gallery-item {
                    margin: $local-gutter / 2;
                    width: 100%;
                }
                .gallery-item:nth-child(n) {
                    margin-left: $local-gutter / 2; // SKIP-RTL
                }
                .gallery-item:nth-child(n+1) {
                    margin-top: $local-gutter / 2;
                }
            }
        }
    }
}
