/*
// .tippy
*/

.tippy {}
.tippy-box {
    background: $tooltip-bg-color;
    color: $tooltip-font-color;
    border-radius: 2px;
    font-size: 14px;
    line-height: 1.25;
}
.tippy-content {
    padding: 6px 10px;
}
.tippy-arrow {
    width: 12px;
    height: 12px;
    color: $tooltip-bg-color;
}

.tippy-box[data-placement^=top] > .tippy-arrow:before {
    bottom: -5px;
    border-width: 6px 6px 0;
}
.tippy-box[data-placement^=bottom] > .tippy-arrow:before {
    top: -5px;
    border-width: 0 6px 6px;
}
.tippy-box[data-placement^=left] > .tippy-arrow:before { // SKIP-RTL
    right: -5px; // SKIP-RTL
    border-width: 6px 0 6px 6px;
}
.tippy-box[data-placement^=right] > .tippy-arrow:before { // SKIP-RTL
    left: -5px; // SKIP-RTL
    border-width: 6px 6px 6px 0;
}
