/*
// #customer_login
*/

// layout
.th-login-form--columns--one,
.th-login-form--columns--two .u-column1,
.th-login-form--columns--two .u-column2 {
    @include th-card;

    flex-basis: 0;
    flex-grow: 1;
    max-width: none;
    padding: 2rem;
}
.th-login-form--columns--one {
    max-width: 410px;
    margin: 0 auto;
}

@include media-breakpoint-only(lg) {
    .th-login-form--columns--two .u-column1,
    .th-login-form--columns--two .u-column2 {
        max-width: none;
    }
}
@include media-breakpoint-up(lg) {
    .th-login-form--columns--two .u-column1,
    .th-login-form--columns--two .u-column2 {
        max-width: 410px;
    }
    .th-login-form--columns--two {
        .u-column2 {
            @include direction {
                #{$margin-inline-start}: 3.5rem;
            }
        }
    }
}
@include media-breakpoint-only(md) {
    .th-login-form--columns--two .u-column1,
    .th-login-form--columns--two .u-column2 {
        padding: 1.5rem;
    }
    .th-login-form--columns--two {
        .u-column2 {
            @include direction {
                #{$margin-inline-start}: 1.5rem;
            }
        }
    }
}
@include media-breakpoint-up(md) {
    .th-login-form--columns--two {
        .u-columns {
            display: flex;
            width: 100%;
            justify-content: center;
        }
    }
}
@include media-breakpoint-down(sm) {
    .th-login-form {
        .u-column2 {
            margin-top: 1.5rem;
        }
    }
}
@media (max-width: 419px) {
    .th-login-form--columns--one,
    .th-login-form--columns--two .u-column1,
    .th-login-form--columns--two .u-column2 {
        padding: 1.5rem;
    }
}

.th-confirmation {
    @include th-card;

    margin: 0 auto;
    padding: 2rem;

    p:not([class]) {
        font-size: 15px;
    }

    & > :last-child {
        margin-bottom: 0;
    }
}
@media (max-width: 419px) {
    .th-confirmation {
        padding: 1.5rem;
    }
}

.th-form-card {
    @include th-card;

    margin: 0 auto;
    max-width: 410px;
    padding: 2rem;

    p:not([class]) {
        font-size: 15px;
    }

    button[type="submit"] {
        margin-top: 1rem;
    }
}
@media (max-width: 419px) {
    .th-form-card {
        padding: 1.5rem;
    }
}

.woocommerce-ResetPassword {
    .form-row-last {
        @include direction {
            #{$margin-inline-start}: 0;
        }
    }
}

.th-login-form,
.th-form-card, {
    width: 100%;

    h2 {
        font-size: 24px;
        font-weight: 500;
        line-height: 1.2;
        padding-bottom: 8px;
        border-bottom: 1px solid #ebebeb;
        margin-bottom: 1.5rem;
        margin-top: -4px;
    }

    .woocommerce-privacy-policy-text {
        font-size: 13px;
        color: #6c757d;
        padding: .125rem 0 .5rem;
    }

    .form-row {
        width: 100%;
    }
    .woocommerce-form-login__rememberme {
        display: block;
        margin-bottom: 0;

        input {
            position: relative;
            top: -2px;
        }
        input + span {
            @include direction {
                #{$margin-inline-start}: 4px;
            }
        }
    }
    .woocommerce-form-login__submit {
        margin-top: 1rem;
    }
    .lost_password {
        font-size: 13px;
        margin-top: 1.5rem;
        width: 100%;

        a,
        a:hover {
            color: #6c757d;
        }
        a:hover{
            text-decoration: underline;
        }
    }
}
