/*
// .widget_price_filter
*/

$local-bar-height: 6px;
$local-handle-size: 16px;
$local-handle-inner-size: 6px;

.widget_price_filter {
    .ui-slider-horizontal {
        z-index: 0;
        position: relative;
        text-align: left; // SKIP-RTL
        height: $local-bar-height;
        border-radius: ($local-bar-height / 2);
        margin: (($local-handle-size - $local-bar-height) / 2) 0;
        background: $nouislider-bg-color;
    }
    .ui-slider .ui-slider-handle {
        position: absolute;
        z-index: 2;
        width: $local-handle-size;
        height: $local-handle-size;
        border-radius: ($local-handle-size / 2);
        cursor: ew-resize;
        outline: none;
        box-sizing: border-box;
        opacity: 1;
        margin-top: (-$local-handle-size / 2 + $local-bar-height / 2);
        transition: box-shadow .1s;

        @include th-var(background, --th-theme-scheme-main, $nouislider-border-color);

        &:after {
            display: block;
            content: '';
            position: absolute;
            width: $local-handle-inner-size;
            height: $local-handle-inner-size;
            margin: ($local-handle-size - $local-handle-inner-size) / 2;
            border-radius: $local-handle-inner-size / 2;
            transition: transform .2s;

            @include th-var(background, --th-theme-scheme-opposite, $nouislider-handle-color);
        }
    }
    .ui-slider .ui-slider-handle:last-child {
        margin-left: -1em; // SKIP-RTL
    }
    .ui-slider .ui-slider-handle:hover,
    .ui-slider .ui-slider-handle.ui-state-active {
        &:before {
            display: block;
            content: '';
            position: absolute;
            left: -3px; // SKIP-RTL
            right: -3px; // SKIP-RTL
            top: -3px;
            bottom: -3px;
            border-radius: 1000px;
            opacity: .2;
            z-index: -1;
            pointer-events: none;

            @include th-var(background, --th-theme-scheme-main, $nouislider-border-color);
        }
    }
    .ui-slider .ui-slider-range {
        position: absolute;
        z-index: 1;
        display: block;
        border: 0;

        &::before {
            position: absolute;
            display: block;
            content: '';
            height: 100%;
            left: ($local-handle-size / 2); // SKIP-RTL
            right: ($local-handle-size / 2); // SKIP-RTL

            @include th-var(background, --th-theme-scheme-main, $nouislider-connect-color);
        }
    }
    .ui-slider-horizontal .ui-slider-range {
        height: 100%;
    }

    input[type="text"] {
        $local-gutter: 6px;

        flex-basis: calc(50% - #{$local-gutter / 2});
        width: calc(50% - #{$local-gutter / 2});
        flex-grow: 1;

        @include input-sm;

        &:first-child {
            #{$margin-inline-end}: $local-gutter;
        }
    }

    .ui-slider + .price_slider_amount {
        margin-top: 12px;
    }
    .price_slider_amount {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        font-size: 14px;
        line-height: 18px;

        .clear {
            display: none;
        }
    }
    .price_label {
        padding: 3px 0 0;
        margin-top: 6px;
        order: -1;

        @include direction {
            #{$margin-inline-end}: 12px;
        }
    }
    .button {
        margin-top: 6px;

        @include th-button--size--small;
        @include th-button--style--secondary;
    }
    .ui-slider + .price_slider_amount .button {
        @include th-button--size--extra-small;
    }
}
