/*
// .th-modal
*/

$local-transition-duration: .3s;
$local-transition-timing-function: ease-out;

.th-modal {
    display: none;
}
.th-modal--size--md {
    .th-modal__wrapper {
        max-width: 500px;
    }
}
.th-modal--display {
    display: block;
}
.th-modal--open {
    .th-modal__backdrop {
        opacity: 1;
    }
    .th-modal__window {
        opacity: 1;
        transform: none;
    }
}
.th-modal__backdrop {
    z-index: 1040;
    position: fixed;
    background: rgba(#333, .8);
    top: 0;
    left: 0; // SKIP-RTL
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity $local-transition-duration $local-transition-timing-function;
}
.th-modal__container {
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 1050;
    position: fixed;
    top: 0;
    left: 0;// SKIP-RTL
    width: 100%;
    height: 100%;
}
.th-modal__wrapper {
    --th-modal-margin-t: 1.75rem;
    --th-modal-margin-b: 1.75rem;

    max-width: 800px;
    min-height: calc(100% - var(--th-modal-margin-t) - var(--th-modal-margin-b));
    margin: var(--th-modal-margin-t) auto var(--th-modal-margin-b);
    display: flex;
    align-items: center;

    .admin-bar & {
        --th-modal-margin-t: 3.75rem;
    }
}
.th-modal__window {
    position: relative;
    background: #fff;
    border-radius: 2px;
    box-shadow: 0 1px 10px rgba(#000, .1);
    flex-grow: 1;
    opacity: 0;
    transform: translateY(-100px);
    transition:
        opacity $local-transition-duration $local-transition-timing-function,
        transform $local-transition-duration $local-transition-timing-function;
}
.th-modal__close {
    @include th-modal-close-button;
}
