@mixin wordpress-pagination-base {
    .page-numbers:not(.dots) {
        font-size: 15px;
        line-height: 18px;
        border-radius: 2px;
        padding: 6px 10px;
        transition: background .15s;
        cursor: pointer;
        user-select: none;
        margin: 3px;

        @include define-button-state($pagination-scheme, normal);

        &:hover {
            @include define-button-state($pagination-scheme, hover);
        }
        &:active {
            transition-duration: 0s;

            @include define-button-state($pagination-scheme, active);
        }
        &:focus {
            box-shadow: none;
        }
    }
    .page-numbers:not(.dots).current {
        @include th-var(background-color, --th-theme-scheme-main, map_get($pagination-scheme, current-bg-color));
        @include th-var(color, --th-theme-scheme-opposite, map_get($pagination-scheme, current-font-color));

        cursor: default;
    }
    .page-numbers.prev,
    .page-numbers.next {
        display: flex;
        align-items: center;

        svg {
            display: block;
            margin: 3px 0 4px;
            fill: currentColor;
            opacity: .8;

            @include direction {
                transform: scaleX($transform-direction); // SKIP-RTL
            }

        }
    }
    .page-numbers.prev svg {
        @include direction {
            #{$margin-inline-start}: -1px;
            #{$margin-inline-end}: 1px;
        }
    }
    .page-numbers.next svg {
        @include direction {
            #{$margin-inline-start}: 1px;
            #{$margin-inline-end}: -1px;
        }
    }
    .page-numbers.dots {
        $local-vertical: true;
        $local-size: 2px;
        $local-gutter: 3px;

        position: relative;
        width: $local-size;
        height: $local-size;
        border-radius: ($local-size / 2);
        background: $pagination-dots-color;
        align-self: center;
        color: transparent;
        pointer-events: none;

        @if($local-vertical) {
            margin: 0 (4px + 3px);
        } @else {
            margin: 0 #{$local-size + $local-gutter + 4px};
        }

        &:before,
        &:after {
            display: block;
            content: '';
            position: absolute;
            width: inherit;
            height: inherit;
            border-radius: inherit;
            background: inherit;
        }
        &:before {
            @if($local-vertical) {
                top: -($local-size + $local-gutter);
            } @else {
                left: -($local-size + $local-gutter); // SKIP-RTL
            }
        }
        &:after {
            @if($local-vertical) {
                bottom: -($local-size + $local-gutter);
            } @else {
                top: 0;
                right: -($local-size + $local-gutter); // SKIP-RTL
            }
        }
    }

    @include media-breakpoint-down(xs) {
        .page-numbers:not(.dots) {
            padding: 5px 9px;
        }
    }
}

@mixin wordpress-pagination() {
    .nav-links {
        display: flex;
        justify-content: center;
        width: 100%;
        margin: -3px;
    }

    @include wordpress-pagination-base;
}
