/*
// .th-products-view
*/

.th-products-view {}
.th-products-view__options {
    @include th-card;

    margin-bottom: 20px;
}
.th-products-view__pagination {
    @include th-card;

    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
    padding: .75rem 1rem;
}
.th-products-view__pagination-legend {
    font-size: .9375rem;
    padding: 0 .375rem;

    p {
        margin: 0;
    }
}


@include media-breakpoint-down(sm) {
    .th-products-view__pagination {
        padding-top: 1rem;
        flex-direction: column;
    }
    .th-products-view__pagination-legend {
        padding-top: 9px;
    }
}
