/*
// .woocommerce-tabs
*/

.woocommerce-tabs {
    --th-woocommerce-tabs-padding: 48px;

    .wc-tabs {
        @include th-reset-list;

        display: flex;
        padding: 0;
        margin: 0 0 -1px;
        list-style: none;
        overflow-x: auto;

        li {
            @include th-reset-list-item;

            position: relative;
            white-space: nowrap;

            a {
                font-size: 16px;
                font-weight: $font-weight-medium;
                color: inherit;
                display: flex;
                padding: 14px 18px 13px;
                transition:
                    background .12s,
                    box-shadow .12s;

                &:hover {
                    background: $product-tabs-hover-bg-color;
                    box-shadow: 0 -1px $product-tabs-hover-border-color inset;
                }
                &:active {
                    background: $product-tabs-active-bg-color;
                    box-shadow: 0 -1px $product-tabs-active-border-color inset;
                }
            }
            &:first-child {
                @include direction {
                    #{$margin-inline-start}: auto;
                }
            }
            &:last-child {
                @include direction {
                    #{$margin-inline-end}: auto;
                }
            }
        }

        .active {
            a {
                //noinspection CssOverwrittenProperties
                &,
                &:hover,
                &:active {
                    background: transparent;
                    box-shadow: 0 -2px $product-tabs-current-border-color inset;
                    box-shadow: 0 -2px var(--th-theme-scheme-main) inset;
                }
            }
        }
    }
    .wc-tab {
        border-top: 1px solid map_get($card-scheme, divider);
        padding: var(--th-woocommerce-tabs-padding);
    }
}
.woocommerce-Tabs-panel--description {
    & > *:last-child {
        margin-bottom: 0;
    }
}
.woocommerce-Tabs-panel--description > h2:first-child,
.woocommerce-Tabs-panel--additional_information > h2:first-child {
    display: none;

    & + * {
        margin-top: 0;
    }
}


.woocommerce-product-attributes {
    @include th-reset-table;

    width: 100%;
    //border-bottom: 1px solid #ebebeb;
    font-size: 15px;
    margin: -8px 0;

    a:hover {
        text-decoration: underline;
    }
}
.woocommerce-product-attributes-item__label,
.woocommerce-product-attributes-item__value {
    @include th-reset-table-cell;

    padding: 8px 0;
    border-top: 1px solid #ebebeb;

}
.woocommerce-product-attributes-item__label {
    width: 240px;
    font-weight: $font-weight-medium;

    @include direction {
        #{$padding-inline-end}: 30px;
    }
}
.woocommerce-product-attributes-item__value {
    :last-child {
        margin-bottom: 0;
    }
}
.woocommerce-product-attributes-item {
    &:first-child {
        th,
        td {
            padding-top: 0;
            border-top: none;
        }
    }
    &:last-child {
        th,
        td {
            padding-bottom: 0;
            border-bottom: none;
        }
    }
}
