/*
// .widget_layered_nav
*/

$local-margin: 8px;

.widget_layered_nav {
    .woocommerce-widget-layered-nav-list {
        list-style: none;
        padding: 0;
        margin: (-$local-margin) 0;
    }
    .woocommerce-widget-layered-nav-list__item {
        margin: $local-margin 0;
        font-size: 14px;
        line-height: 16px;
        display: flex;

        a,
        span {
            display: block;
        }

        a {
            padding: 1px 0;
            position: relative;
            color: inherit;
            flex-grow: 1;

            @include direction {
                #{$padding-inline-start}: (16px + 7px);
            }

            @include th-fake-checkbox;
        }

        span {
            padding-top: 3px;
            font-size: 12px;
            line-height: 12px;
            color: map_get($card-scheme, opposite-alt);

            @include direction {
                #{$margin-inline-start}: 12px;
            }
        }
    }
    .woocommerce-widget-layered-nav-list__item--chosen a {
        @include th-fake-checkbox--checked;
    }
}
