/*
// .comment-form
*/

.comment-form {
    display: flex;
    flex-wrap: wrap;
    font-size: 15px;
    margin-left: -10px; // SKIP-RTL

    p {
        @include th-reset-paragraph;

        margin-bottom: 1rem;
    }

    & > *,
    & > p {
        margin-left: 10px; // SKIP-RTL
    }

    .comment-notes,
    .logged-in-as {
        width: 100%;
        margin-top: -1rem;
    }

    .comment-form-comment,
    .comment-form-author,
    .comment-form-email,
    .comment-form-url {
        width: 100%;

        label {
            margin-bottom: .25rem;
            font-size: 15px;
        }
    }

    .comment-form-author,
    .comment-form-email,
    .comment-form-url {
        width: calc((100% - 10px * 3) / 3);
        min-width: 160px;
        flex-grow: 1;
    }
    .comment-form-email,
    .comment-form-url {
        @include direction {
            #{$margin-inline-start}: 10px;
        }
    }

    .comment-form-comment textarea,
    .comment-form-author input,
    .comment-form-email input,
    .comment-form-url input {
        width: 100%;
    }

    .comment-form-cookies-consent {
        margin-bottom: 0;
        font-size: 15px;
        line-height: 22px;

        input {
            position: relative;
            top: -1px;
        }
    }

    .form-submit {
        width: 100%;
        margin-top: 1.25rem;
        margin-bottom: 0;
    }

}
.comment-list {
    .comment-respond {
        box-shadow: none;
    }
    .comment-reply-title {
        font-size: 18px;
        padding-bottom: 6px;
        margin-bottom: 32px;
        position: relative;

        @include direction {
            #{$padding-inline-end}: 64px;
        }

        a {
            position: absolute;
            top: calc(50% - 12px - 3px);

            @include direction {
                #{$inset-inline-end}: 0;
            }
        }
    }

    @include media-breakpoint-up(sm) {
        .comment-respond {
            --th-post-card-padding: #{28px + 3px};

            background: map_get($child-card-scheme, main);
            margin-top: -5px;
            border-bottom-left-radius: 5px; // SKIP-RTL
            border-bottom-right-radius: 5px; // SKIP-RTL
            position: relative;
            z-index: 0;

            &:before {
                position: absolute;
                content: '';
                left: 3px; // SKIP-RTL
                right: 3px; // SKIP-RTL
                top: 3px;
                bottom: 3px;
                border-radius: 5px 5px 3px 3px;
                z-index: -1;
                background: #fff;
            }
        }
    }
    @include media-breakpoint-down(xs) {
        .comment-respond {
            --th-post-card-padding: 0;

            margin: -4px 0 0;
            padding: 22px 0;
            border-top: 3px solid map_get($child-card-scheme, main);

            @include direction {
                #{$border-inline-start}: 3px solid map_get($child-card-scheme, main);
                #{$padding-inline-start}: 24px;
            }
        }
        .comment-reply-title {
            font-size: 16px;
        }
    }
    @media (max-width: 419px) {
        .children .comment-respond {
            @include direction {
                #{$margin-inline-start}: -(12px + 1px + 20px);
                #{$border-start-start-radius}: 24px;
            }
        }
    }
}
