/*
// .th-vehicle-vin
*/

.th-vehicle-vin {}
.th-vehicle-vin__field {
    position: relative;
}
.th-vehicle-vin__loader {
    $local-size: 18px;
    $local-input-height: 36px;

    @include th-loader($local-size, 2px, #000);
    @include direction {
        #{$inset-inline-start}: auto;
        #{$inset-inline-end}: ($local-input-height - $local-size) / 2;
    }

    pointer-events: none;
    opacity: 0;
    transition: opacity .2s ease-in-out;
}
.th-vehicle-vin__alert {
    &:empty {
        display: none;
    }
    &:before {
        display: block;
        content: '';
        height: .5rem;
    }

    .th-alert {
        margin: 0;
    }
}
.th-vehicle-vin--loading {
    .th-vehicle-vin__loader {
        pointer-events: all;
        opacity: 1;
    }
}
