/*
// .th-filter-vehicle
*/

.th-filter-vehicle {}
.th-filter-vehicle__empty {
    font-size: 15px;
    line-height: 19px;
}
.th-filter-vehicle__list {
    margin: 0;
    padding: 0;
    list-style: none;
}
.th-filter-vehicle__item {
    @include th-reset-list-item;
}
.th-filter-vehicle__item-label {
    padding: 1px 0;
    cursor: pointer;
    display: flex;
    margin: 0 0 8px;
}
.th-filter-vehicle__item-input {
    flex-shrink: 0;

    @include direction {
        #{$margin-inline-end}: 7px;
    }
}
.th-filter-vehicle__item-title {
    padding: 1px 0;
    font-size: 14px;
    line-height: 16px;
    flex-grow: 1;
    transition: color .2s;
}
.th-filter-vehicle__item-counter {
    padding-top: 3px;
    font-size: 12px;
    line-height: 12px;
    color: map_get($card-scheme, opposite-alt);

    @include direction {
        #{$margin-inline-start}: 12px;
    }
}
.th-filter-vehicle__item--disabled {
    .th-filter-vehicle__item-label {
        cursor: default;
    }
    .th-filter-vehicle__item-title {
        color: map_get($card-scheme, muted);
    }
}
.th-filter-vehicle__vehicle {
    @include th-reset-list-item;

    border: 1px solid #ebebeb;
    border-radius: 2px;
    padding: 6px 9px;
    line-height: 1.375;
}
.th-filter-vehicle__vehicle-title {
    font-size: 14px;
    font-weight: $font-weight-medium;
}
.th-filter-vehicle__vehicle-subtitle {
    font-size: 13px;
    color: map_get($card-scheme, muted);
}
.th-filter-vehicle__button {
    padding-top: 14px;
}
