@mixin post-card-sticky() {
    border-radius: 2px;

    @include th-var(background, --th-dark-scheme-main, map_get($dark-scheme, main-color));
    @include th-var(color, --th-dark-scheme-opposite, map_get($dark-scheme, opposite-color));

    .th-post-card__meta {
        @include th-var(color, --th-dark-scheme-muted, map_get($dark-scheme, opposite-alt));
    }
    .th-post-card__more {
        .th-button {
            @include th-var(background-color, --th-btn-context-normal-bg-color, map_get($btn-dark-scheme, normal-bg-color));
            @include th-var(color, --th-btn-context-normal-font-color, map_get($btn-dark-scheme, normal-font-color));

            &:hover {
                @include th-var(background-color, --th-btn-context-hover-bg-color, map_get($btn-dark-scheme, hover-bg-color));
                @include th-var(color, --th-btn-context-hover-font-color, map_get($btn-dark-scheme, hover-font-color));
            }
            &:active {
                @include th-var(background-color, --th-btn-context-active-bg-color, map_get($btn-dark-scheme, active-bg-color));
                @include th-var(color, --th-btn-context-active-font-color, map_get($btn-dark-scheme, active-font-color));
            }
        }
    }
    .th-post-card__category a {
        @include th-var(color, --th-accent-scheme-main, map_get($accent-scheme, main-color));
    }
}
@mixin post-card-base() {
    .th-post-card__meta {
        font-size: 14px;
        line-height: 21px;
        color: map_get($card-scheme, opposite-alt);
        width: 100%;

        a {
            color: inherit;
        }
        a:hover {
            text-decoration: underline;
        }

        svg {
            fill: currentColor;
            opacity: .75;
        }
    }
    .th-post-card__meta-list {
        display: flex;
        flex-wrap: wrap;
        margin-left: -16px;
        margin-top: -2px;
    }
    .th-post-card__meta-item {
        display: flex;
        align-items: center;
        margin-left: 16px;
        margin-top: 2px;
    }
    .th-post-card__meta-icon {
        @include direction {
            #{$margin-inline-end}: 7px;
        }
    }
    .th-post-card__meta-value {
        padding-top: 1px;
    }
    .th-post-card__meta-item--author {
        img {
            display: block;
            margin: -1px 0;
            border-radius: 9px;
        }
    }
    .th-post-card__meta-item--date {
        svg {
            position: relative;
            top: -1px;
        }
    }
    .th-post-card__more {
        .th-button {
            display: inline-flex;
            align-items: center;
        }

        svg {
            opacity: .9;
            position: relative;
            top: -1;

            @include direction {
                #{$margin-inline-start}: 8px;
                #{$margin-inline-end}: -1px;
                transform: scaleX(#{1 * $transform-direction});
            }
        }
    }
}
@mixin post-card-grid-base() {
    @include th-card;

    .th-post-card__image {
        img {
            width: 100%;
            max-width: 100%;
            height: auto;
        }
    }
    .th-post-card__content {
        position: relative;
    }
    .th-post-card__title {
        word-wrap: break-word;

        h2 {
            margin: 0;
        }

        a {
            color: inherit;
        }
        a:hover {
            text-decoration: underline;
        }
    }
    &:not(.has-post-thumbnail)  .th-post-card__category {
        margin-top: -2px;
        margin-bottom: 8px;
    }
    &.has-post-thumbnail .th-post-card__category {
        position: absolute;
        bottom: calc(100% - 1px);
        z-index: 0;

        @include direction {
            #{$inset-inline-start}: 0;
        }

        &:before,
        &:after {
            position: absolute;
            display: block;
            content: '';
            top: 0;
            background: map_get($card-scheme, main);
            z-index: -1;
            height: 100%;
        }

        &:before {
            width: 100%;

            @include direction {
                #{$inset-inline-start}: 0;
                #{$border-start-end-radius}: 2px;
                transform: skewX(#{30deg * $transform-direction}); // SKIP-RTL
                transform-origin: $inline-start top;
            }
        }
        &:after {
            width: 50px;

            @include direction {
                #{$inset-inline-start}: 0;
            }
        }
    }


    .th-post-card__category {
        font-weight: $font-weight-medium;
        font-size: 14px;

        a {
            @include th-var(color, --th-theme-scheme-main, $link-color);
        }
        a:hover {
            text-decoration: underline;
        }
    }
    .th-post-card__excerpt {
        overflow: hidden;
        line-height: 1.625;

        & > :first-child {
            margin-top: 0;
        }
        & > :last-child {
            margin-bottom: 0;
        }
    }
}
@mixin post-card-grid-lg() {
    .th-post-card__content {
        padding: 1.875rem 2.1875rem 2.1875rem;
    }
    .th-post-card__title {
        h2 {
            font-size: 26px;
            font-weight: $font-weight-medium;
            line-height: 34px;
        }
    }
    &.has-post-thumbnail .th-post-card__category {
        height: 26px;
        padding: 1.125rem 2.1875rem 0;
    }
    .th-post-card__meta {
        margin: 10px 0 20px;
    }
    .th-post-card__excerpt {
        //font-size: 15px;
        //color: #6c757d;

        &,
        & p {
            line-height: 1.7;
        }
    }
    .th-post-card__more {
        margin-top: 26px;

        .th-button {
            padding: 9.5px 18px;
        }
    }
}
@mixin post-card-grid-md() {
    $local-padding-x: 1.675rem;

    .th-post-card__content {
        padding: 1.675rem $local-padding-x 1.75rem;
    }
    .th-post-card__title {
        h2 {
            font-size: 20px;
            line-height: 28px;
        }
    }
    &.has-post-thumbnail .th-post-card__category {
        height: 26px;
        padding: 1.125rem $local-padding-x 0;
    }
    .th-post-card__meta {
        margin: 9px 0 12px;
    }
    .th-post-card__excerpt {
        $local-font-size: 16px;
        $local-line-height: 1.625;

        line-height: $local-line-height;
        font-size: $local-font-size;
        max-height: ($local-font-size * $local-line-height * 3);
    }
    .th-post-card__more {
        margin-top: 18px;
    }
}
@mixin post-card-grid-sm() {
    $local-padding-x: 1.25rem;

    .th-post-card__category {
        font-size: 13px;
    }
    .th-post-card__title {
        h2 {
            font-size: 18px;
            line-height: 24px;
        }
    }
    .th-post-card__excerpt {
        $local-font-size: 15px;
        $local-line-height: 1.625;
        $local-lines: 3;

        line-height: $local-line-height;
        font-size: $local-font-size;
        max-height: ($local-font-size * $local-line-height * $local-lines);
    }
    &.has-post-thumbnail .th-post-card__category {
        height: 22px;
        padding: .875rem $local-padding-x 0;
    }
    .th-post-card__content {
        display: flex;
        flex-direction: column;
        padding: 1.5rem $local-padding-x 1.125rem;
    }
    .th-post-card__meta {
        margin: 12px 0;
    }
    .th-post-card__more {
        display: none;
    }
}
@mixin post-card-grid-xs() {
    $local-padding-x: 1.25rem;

    .th-post-card__category {
        font-size: 13px;
    }
    .th-post-card__title {
        h2 {
            font-size: 15px;
            line-height: 20px;
            overflow: hidden;
            max-height: (20px * 2);
        }
    }
    &.has-post-thumbnail .th-post-card__category {
        height: 22px;
        padding: .75rem $local-padding-x 0;
    }
    .th-post-card__content {
        display: flex;
        flex-direction: column;
        padding: 1.125rem $local-padding-x 1rem;
    }
    .th-post-card__meta {
        margin: 8px 0 0;
    }
    .th-post-card__meta-item--comments {
        display: none;
    }
    .th-post-card__excerpt,
    .th-post-card__more {
        display: none;
    }
    &:not(.has-post-thumbnail) {
        .th-post-card__meta {
            margin: 10px 0 11px;
        }
        .th-post-card__excerpt {
            display: block;
            order: 2;
            margin-top: 8px;
            font-size: 15px;
            line-height: 24px;
            max-height: (24px * 5);

            p {
                line-height: inherit;
            }
        }
    }
}


@mixin post-card-list-base() {
