        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        align-content: flex-start;
        flex-basis: 0;
        width: 0;
        flex-grow: 1;
    }
    .th-post-card__title {
        word-wrap: break-word;
        width: 100%;

        h2 {
            margin-bottom: 0;
        }
        a {
            color: inherit;
        }
        a:hover {
            text-decoration: underline;
        }
    }
    .th-post-card__category {
        font-size: 14px;
        font-weight: $font-weight-medium;

