/*
// .th-product
*/

.th-product {}
.th-product__header {
    h1 {
        font-size: 26px;
        font-weight: $font-weight-bold;
        line-height: 32px;
        margin: 0;
    }
}
.th-product__body {
    position: relative;
}
@include th-product__tabs {
    @include th-card;
}
.th-product__subtitle {
    padding-top: 8px;
    display: flex;
    align-items: center;
}
@include th-product__rating {
    display: flex;
    color: map_get($card-scheme, opposite-alt);
    font-size: 14px;
    line-height: 1;

    a {
        color: inherit;
    }
    a:hover {
        text-decoration: underline;
    }
}
@include th-product__rating-stars {
    @include direction {
        #{$margin-inline-end}: 8px;
    }
}
@include th-product__fit--after-rating {
    .th-status-badge {
        @include direction {
            #{$margin-inline-start}: 12px;
        }
    }
}
.th-product__summary {
    --th-product__summary--padding-y: 28px;
    --th-product__summary--padding-x: 28px;

    @include media-breakpoint-down(xl) {
        --th-product__summary--padding-x: 20px;
    }

    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: flex-start;
    padding: var(--th-product__summary--padding-y) var(--th-product__summary--padding-x);
}
%th-product__price--primary {
    margin-bottom: 0;
    line-height: 1;
    font-size: 30px;
    font-weight: $font-weight-bold;
    letter-spacing: -.04em;
    padding: 2px 0;

    @include direction {
        #{$margin-inline-end}: auto;
    }

    .woocs_price_code {
        display: flex;
        flex-direction: column;
    }
    del {
        order: -1;
        font-size: 16px;
        font-weight: $font-weight-normal;
        margin-bottom: 5px;
    }
}
@include th-product__price--primary {
    @include th-reset-paragraph;

    @extend %th-product__price--primary;

    @include direction {
        #{$margin-inline-end}: auto;
    }

    .product-type-variable &,
    .product-type-grouped & {
        .woocs_price_code {
            display: block;
        }
    }
}
.th-product__summary > .price,
.th-product__stock {
    margin-bottom: 20px;
}

.th-product__meta {
    width: 100%;
    margin: 0 0 20px;

    table {
        @include th-reset-table;

        width: 100%;
        font-size: 14px;
    }
    th,
    td {
        @include th-reset-table-cell;

        padding: 3px 0;
    }
    th {
        font-weight: $font-weight-medium;

        @include direction {
            #{$padding-inline-end}: 12px;
        }
    }
    tr + tr > * {
        border-top: 1px solid map_get($card-scheme, divider);
    }

    a {
        @include th-var(color, --th-link-color, $link-color);
    }
    a:hover {
        text-decoration: underline;
    }
}

@include th-product__variations {
    @include th-reset-table;

    width: calc(100% + var(--th-product__summary--padding-x) * 2);
    margin-left: calc(var(--th-product__summary--padding-x) * -1); // SKIP-RTL
    margin-right: calc(var(--th-product__summary--padding-x) * -1); // SKIP-RTL
    padding: 16px var(--th-product__summary--padding-x) 18px;

    background: #f9f9f9;
    border-top: 1px solid #ebebeb;
    border-bottom: 1px solid #ebebeb;
    margin-bottom: 24px;

    th,
    td {
        @include th-reset-table-cell;
    }

    &,
    tbody,
    tr,
    td {
        display: block;
    }
    tr + tr {
        margin-top: 12px;
    }
    td {
        padding: 0;
    }

    .label label {
        display: block;
        text-transform: uppercase;
        font-size: 11px;
        font-weight: $font-weight-medium;
        color: #6c7177;
        margin-bottom: 4px;
    }

    .value {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin-bottom: -10px;

        @include direction {
            #{$margin-inline-end}: -12px;
        }

        & > * {
            margin-bottom: 10px;

            @include direction {
                #{$margin-inline-end}: 12px;
            }
        }
    }

    .reset_variations {
        visibility: visible !important;
        display: block;
        height: 18px;
        font-size: 11px;
        font-weight: $font-weight-medium;
        line-height: 1;
        text-transform: uppercase;
        position: relative;
        padding-top: 4px;
        border-radius: 1.5px;
        color: #999;
        transition:
            background-color .12s ease-in-out,
            color .12s ease-in-out;

        &::before {
            opacity: .3;
            top: 4px;
            transition:
                opacity .12s ease-in-out;

            @include svg-pseudo-element(10px);
            @include cross-10(#000);

            @include direction {
                #{$inset-inline-start}: 5px;
            }
        }

        @include direction {
            #{$padding-inline-start}: 18px;
            #{$padding-inline-end}: 6px;
        }

        &:hover {
            background: #f0f0f0;
            color: #808080;

            &::before {
                opacity: .4;
            }
        }
    }
}
.th-input-radio-label,
.th-input-radio-color {
    & + select {
        display: none;
    }
}
.th-reset-variations {
    overflow: hidden;
    transition:
        height .2s ease-in-out,
        opacity .2s ease-in-out;
}
.th-reset-variations--hidden {
    opacity: 0;
    height: 0;
    pointer-events: none;
}
.single_variation_wrap {
    margin-top: -4px;
}
.woocommerce-variation-add-to-cart {
    margin-top: 4px;
}
.woocommerce-variation {
    $local-expand: 4px;

    display: flex;
    flex-wrap: wrap;
    align-items: center;

    &:after {
        content: '';
        display: block;
        width: 100%;
        height: (22px - $local-expand * 2);
    }

    &:empty:after {
        height: 0;
    }
}
.woocommerce-variation-description {
    width: 100%;
    font-size: 15px;
    line-height: 1.4;
    margin-bottom: 14px;
    padding-top: 2px;

    & > :last-child {
        margin-bottom: 0;
    }

    &:empty {
        display: none;
    }
}
.woocommerce-variation-price {
    @extend %th-product__price--primary;
}

.woocommerce-grouped-product-list {
    @include th-reset-table;

    width: calc(100% + var(--th-product__summary--padding-x) * 2);
    margin-left: calc(var(--th-product__summary--padding-x) * -1); // SKIP-RTL
    margin-right: calc(var(--th-product__summary--padding-x) * -1); // SKIP-RTL
    //

    //background: #f9f9f9;
    border-top: 1px solid #ebebeb;
    margin-bottom: 24px;

    label {
        margin-bottom: 0;
    }

    th,
    td {
        @include th-reset-table-cell;

        padding: 0;
    }

    &,
    tbody,
    tr,
    th,
    td {
        display: block;
    }

    tr {
        padding: 14px var(--th-product__summary--padding-x) 20px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        border-bottom: 1px solid #ebebeb;
    }

    .woocommerce-grouped-product-list-item__redparts_image {
        display: none;
    }

    .woocommerce-grouped-product-list-item__label {
        width: 100%;
        margin-top: -1px;

        a {
            color: inherit;
        }
    }

    .woocommerce-grouped-product-list-item__quantity {
        @include direction {
            #{$margin-inline-end}: 14px;
        }
    }
    .woocommerce-grouped-product-list-item__quantity,
    .woocommerce-grouped-product-list-item__price {
        margin-top: 8px;
    }
    .woocommerce-grouped-product-list-item__price {
        font-size: 14px;
        font-weight: 500;
    }

    .button {
        @include th-button--size--small;
    }
    .th-icon-cart,
    .th-icon-cart-added {
        display: none;
    }

    .th-input-number__input {
        min-width: 80px;
    }
    .th-product__summary .cart &  .th-quantity {
        width: 80px;
        margin-bottom: 0;

        @include direction {
            #{$margin-inline-end}: 0;
        }

        input {
            @include input-sm;
        }
    }
    .th-status-badge {
        @include direction {
            #{$margin-inline-start}: 10px;
        }
    }
}





.th-product__summary .single_variation_wrap {
    width: 100%;
}

@include th-product__form {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
@include th-product__actions-container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;

    & > .th-quantity {
        width: 100px;

        @include direction {
            #{$margin-inline-end}: 8px;
        }

        input {
            @include input-lg;
        }
    }
    button[type="submit"] {
        @include th-button--size--large;

        flex-grow: 1;

        @include media-breakpoint-up(xl) {
            .product-type-external &,
            .product-type-grouped & {
                width: 100%;
            }
            .th-product--layout--quickview.product-type-external &,
            .th-product--layout--quickview.product-type-grouped & {
                width: auto;
            }
        }
    }
}
@include th-product__secondary-actions {
    margin-top: 14px;
    margin-bottom: -8px;
    width: 50%;
    font-size: 13px;
    line-height: 16px;
    background: map_get($wishlist-button-scheme, normal-bg-color);
    color: map_get($wishlist-button-scheme, normal-font-color);
    fill: map_get($wishlist-button-scheme, normal-icon-color);
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    padding-top: 7px;
    padding-bottom: 5px;
    border-radius: 2px;
    transition:
        background .15s,
        fill .15s;

    @include direction {
        #{$padding-inline-start}: 9px;
        #{$padding-inline-end}: 10px;
    }

    svg {
        position: relative;
        top: -1px;
        display: block;

        @include direction {
            #{$margin-inline-end}: 8px;
        }
    }

    &:focus {
        outline: none;
    }
    &:hover {
        background: map_get($wishlist-button-scheme, hover-bg-color);
        fill: map_get($wishlist-button-scheme, hover-icon-color);
    }
    &:active {
        background: map_get($wishlist-button-scheme, active-bg-color);
        fill: map_get($wishlist-button-scheme, active-icon-color);
    }

    &__icon {
        order: -1;
    }

    &--loading {
        cursor: default;

        &,
        &:hover,
        &:active {
            background: map_get($wishlist-button-scheme, normal-bg-color);
            fill: transparent;
        }

        &:before {
            @include th-loader(16px, 2px, map_get($btn-muted-scheme, loader-color));

            position: static;
            order: -2;

            @include direction {
                #{$margin-inline-end}: -16px;
            }
        }
    }
}
.th-product__tags {
    a {
        padding: 2px 8px;
    }
}
.th-product__tags,
.th-product__share-buttons {
    margin-top: 24px;
    padding-top: 24px;

    width: calc(100% + var(--th-product__summary--padding-x) * 2);
    margin-left: calc(var(--th-product__summary--padding-x) * -1); // SKIP-RTL
    margin-right: calc(var(--th-product__summary--padding-x) * -1); // SKIP-RTL
    padding-left: var(--th-product__summary--padding-x); // SKIP-RTL
    padding-right: var(--th-product__summary--padding-x); // SKIP-RTL

    border-top: 1px solid map_get($card-scheme, divider);

    &:last-child {
        margin-bottom: calc(24px - var(--th-product__summary--padding-y));
    }
}
.th-product__tags + .th-product__share-buttons {
    margin-top: 18px;
    border-top: none;
    padding-top: 0;
}



.th-product__badge {
    top: -7px;
    position: absolute;

    @include direction {
        #{$inset-inline-start}: 22px;
    }

    .th-tag-badge {
        display: block;
    }
}
.th-product__tags + .th-product__share-links {
    margin-top: 18px;
}
.th-product__widgets {
    .th-sidebar__widget {
        margin-top: 30px;
    }
}


@import 'th-product--layout--sidebar';
@import 'th-product--layout--full';
@import 'th-product--layout--quickview';
