/*
// .th-input-radio-color
*/

$local-field-height: 30px;
$local-item-size: 28px;
$local-border-width: 3px;
$local-white-border-width: 2px;
$local-item-inner-size: 14px;
$local-item-margin: 2px;

.th-input-radio-color {
    display: block;
}
.th-input-radio-color__list {
    padding: #{($local-field-height - $local-item-size) / 2} 0;
    display: flex;
    margin: -$local-item-margin;
}
.th-input-radio-color__item {
    position: relative;
    width: $local-item-size;
    height: $local-item-size;
    display: block;
    margin: $local-item-margin;
    cursor: pointer;
}
.th-input-radio-color__input {
    pointer-events: none;
    opacity: 0;
    position: absolute;
}
.th-input-radio-color__circle {
    position: absolute;
    width: $local-item-size;
    height: $local-item-size;
    display: block;
    border-radius: $local-item-size / 2;
    background: currentColor;
    transform: scale(#{round(($local-item-size * .75) / 2) / $local-item-size * 2});
    transition: transform .2s;

    &:before,
    &:after {
        display: block;
        position: absolute;
        content: '';
        border-radius: 1000px;
    }
    &:before {
        left: $local-border-width; // SKIP-RTL
        top: $local-border-width;
        width: ($local-item-size - $local-border-width * 2);
        height: ($local-item-size - $local-border-width * 2);
        background: #fff;
        transform: scale(0);
        transition: transform .2s;
    }
    &:after {
        left: ($local-item-size - $local-item-inner-size) / 2; // SKIP-RTL
        top: ($local-item-size - $local-item-inner-size) / 2;
        width: $local-item-inner-size;
        height: $local-item-inner-size;
        background: currentColor;
        transform: scale(0);
        transition: transform .2s;
    }

    .th-input-radio-color__item:hover .th-input-radio-color__input:enabled:not(:checked) ~ & {
        transform: scale(#{round(($local-item-size * .875) / 2) / $local-item-size * 2});
    }
}
.th-input-radio-color__stick {
    position: absolute;
    width: $local-item-size;
    height: $local-item-size;
    display: block;
    border-radius: $local-item-size / 2;
    opacity: .4;
    transform: scale(0);
    transition:
        transform .2s,
        opacity .2s;

    &:before {
        display: block;
        position: absolute;
        content: '';
        transform: scale(1.33) rotateZ(45deg);
        left: $local-item-size / 2 - 1px; // SKIP-RTL
        top: -1px;
        width: 2px;
        height: calc(100% + 2px);
        background: rgba(#000, .3);
    }
}
.th-input-radio-color__item input:enabled:checked {
    & ~ .th-input-radio-color__circle,
    & ~ .th-input-radio-color__circle:before,
    & ~ .th-input-radio-color__circle:after {
        transform: scale(1);
    }
}
.th-input-radio-color__item--disabled {
    cursor: default;

    .th-input-radio-color__stick {
        opacity: 1;
        transform: scale(.75);
    }
}
.th-input-radio-color__item--white {
    .th-input-radio-color__circle {
        background: #fff;
        box-shadow: inset 0 0 0 $local-white-border-width #ccc;

        &:after {
            box-shadow: inset 0 0 0 $local-white-border-width #ccc;
        }
    }
}
