/*
// .widget_categories
// .widget_pages
// .widget_nav_menu
// .widget_meta
// .widget_archive
// .widget_product_categories
*/

@mixin local-layout($gutter) {
    ul:not(.children):not(.sub-menu) {
        margin-top: -$gutter;
        margin-bottom: -$gutter;
    }
    li {
        padding-top: $gutter;
        padding-bottom: $gutter;
    }
    ul:not(.children) > li {
        &:before {
            top: ((20px + $gutter * 2 - 4px) / 2);
        }
    }
    li > ul {
        margin-top: $gutter;
        margin-bottom: -$gutter;
    }
}

.widget_categories,
.widget_pages,
.widget_nav_menu,
.widget_meta,
.widget_archive,
.widget_product_categories {
    ul {
        @include th-reset-list;

        padding: 0;
        margin: 0;
        list-style: none;
        line-height: 20px;
        color: map-get($card-scheme, muted);
    }
    li {
        @include th-reset-list-item;

        position: relative;
        font-size: 12px;
        line-height: 20px;

        @include direction {
            #{$padding-inline-start}: 16px;
        }
    }
    ul:not(.children) > li {
        &:before {
            position: absolute;
            display: block;
            content: '';
            width: 4px;
            height: 4px;
            background: currentColor;
            opacity: .4;

            @include direction {
                #{$inset-inline-start}: 0;
            }
        }
    }
    a {
        font-size: 15px;
        color: map-get($card-scheme, opposite);
        transition: color .12s;
        vertical-align: bottom;

        &:hover {
            @include th-var(color, --th-link-color, $link-color);
        }
    }
    .current-cat > a {
        font-weight: $font-weight-medium;
    }

    @include local-layout($gutter: 7px);

    & .th-filter__container {
        @include local-layout($gutter: 2px);
    }

    .th-site-footer & {
        a {
            @include th-var(color, --th-footer-muted-color, map_get($footer-scheme, muted));
        }
        a:hover {
            @include th-var(color, --th-footer-font-color, map_get($footer-scheme, opposite));
        }
    }
}
