/*
// .woocommerce-order-downloads
*/

.woocommerce-order-downloads {
    @include th-card;

    .woocommerce-order-downloads__title {
        padding: .875rem 1.25rem .5rem;
        font-size: 22px;
        margin-bottom: 0;
    }

    table {
        @include th-reset-table;

        margin: 0;
        width: 100%;
        font-size: 15px;
    }
    th,
    td {
        @include th-reset-table-cell;
    }

    .button {
        @include th-button--size--extra-small;
        @include th-button--style--secondary;
    }
}

@include media-breakpoint-up(md) {
    .woocommerce-order-downloads {
        th,
        td {
            padding: .875rem .75rem;

            &:first-child {
                @include direction {
                    #{$padding-inline-start}: 1.25rem;
                }
            }
            &:last-child {
                @include direction {
                    #{$padding-inline-end}: 1.25rem;
                }
            }
        }
        th {
            padding-top: .75rem;
            padding-bottom: .75rem;
            text-transform: uppercase;
            font-size: 13px;
        }

        thead + tbody tr:first-child,
        tr + tr {
            th,
            td {
                border-top: 1px solid map-get($card-scheme, divider);
            }
        }

        .button {
            margin: -1px 0;
        }
    }
}

@include media-breakpoint-down(sm) {
    .woocommerce-order-downloads {
        .woocommerce-order-downloads__title {
            padding-bottom: 0;
        }

        thead {
            display: none;
        }
        tbody,
        tr,
        th,
        td {
            display: block;
        }

        tr {
            padding: .625rem 0;
        }
        tr + tr {
            border-top: 1px solid map-get($card-scheme, divider);
        }

        td {
            display: flex;
            align-items: center;
            padding: .125rem 1.25rem;

            &:before {
                flex-shrink: 0;
                width: 50%;
                content: attr(data-title);
                vertical-align: middle;
                color: #6c757d;

                @include direction {
                    #{$padding-inline-end}: 1.5rem;
                }
            }
        }
    }
}
