/*
// .th-post
*/

$local-padding: 48px;

.th-post {}
.th-post__body {
    padding: $local-padding;

    &:empty {
        padding-top: $local-padding - 36px;
    }

    &:after {
        display: block;
        content: '';
        clear: both;
    }

    img {
        max-width: 100%;
        height: auto;
    }
}

// pagination
.th-post__pagination {
    border-top: 1px solid map_get($card-scheme, divider);
    margin: -16px $local-padding 0;
    padding-top: 20px;
    padding-bottom: $local-padding;
}
.th-post__pagination-title,
.th-page-pagination__title {
    font-size: 12px;
    color: map_get($card-scheme, muted);
    text-transform: uppercase;
    margin-bottom: 5px;
}
.th-post__pagination-list,
.th-page-pagination__list {
    display: flex;
    font-size: 15px;
    margin: -3px;

    .post-page-numbers {
        margin: 3px;
        padding: 2px 8px;
        display: block;
        border-radius: 1.5px;
        transition: background .2s;

        @include define-button-state($pagination-scheme, normal);

        &:hover {
            @include define-button-state($pagination-scheme, hover);
        }
        &:active {
            transition-duration: 0s;

            @include define-button-state($pagination-scheme, active);
        }
    }
    .post-page-numbers.current {
        cursor: default;

        &,
        &:hover,
        &:active {
            @include th-var(background-color, --th-theme-scheme-main, map_get($pagination-scheme, current-bg-color));
            @include th-var(color, --th-theme-scheme-opposite, map_get($pagination-scheme, current-font-color));
        }
    }
}

// footer
.th-post__footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 0 $local-padding $local-padding;
    margin-top: -12px;
}
.th-post__tags {
    @include direction {
        #{$margin-inline-end}: 32px;
    }
}
.th-post__share-links {
    padding-bottom: 1px;
}

// author
.th-post__author {
    display: flex;
    align-items: center;
    margin: -24px $local-padding 0;
    padding: 24px 0 $local-padding;
    border-top: 1px solid map_get($card-scheme, divider);

    img {
        display: block;
    }

    a {
        color: inherit;
        transition: color .12s ease-in-out;
    }
    a:hover {
        @include th-var(color, --th-link-color, $link-color);
    }
}
.th-post__author-avatar {
    overflow: hidden;
    flex-shrink: 0;
    border-radius: 2px;
    position: relative;

    @include th-overlay-image-link;
    @include direction {
        #{$margin-inline-end}: 20px;
    }

    img {
        width: 70px;
        height: 70px;
    }
}
.th-post__author-name {
    font-weight: $font-weight-medium;
}
.th-post__author-about {
    font-size: 15px;
    margin-top: 4px;
}
.th-page-content {
    &:after {
        display: block;
        content: '';
        clear: both;
    }
}
.th-page-pagination {
    border-top: 1px solid map_get($card-scheme, divider);
    margin: 20px 0 0;
    padding-top: 20px;
}
.th-page-pagination__title {
    font-size: 12px;
    color: map_get($card-scheme, muted);
    text-transform: uppercase;
    margin-bottom: 5px;
}
.th-page-comments {
    margin-top: 32px;
}


@include media-breakpoint-down(sm) {
    $local-padding: 32px;

    .th-post__body {
        padding: $local-padding;
    }
    .th-post__pagination {
        margin: 0 $local-padding 0;
    }
    .th-post__pagination {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .th-post__footer {
        flex-direction: column;
        align-items: center;
        padding: 0 $local-padding 24px;
    }
    .th-post__tags {
        @include direction {
            #{$margin-inline-end}: 0;
        }

        .th-tags__list {
            justify-content: center;
        }
    }
    .th-post__share-links {
        margin-top: 20px;
        padding-bottom: 0;
    }
    .th-post__author {
        margin: 0 $local-padding 0;
        padding: $local-padding 0 $local-padding;
        flex-direction: column;
    }
    .th-post__author-avatar {
        border-radius: 50%;

        @include direction {
            #{$margin-inline-end}: 0;
        }
    }
    .th-post__author-info {
        text-align: center;
        margin-top: 16px;
    }
    .th-post__author-about {
        margin-top: 6px;
    }
}
@include media-breakpoint-down(xs) {
    $local-padding: 24px;

    .th-post__body {
        padding: $local-padding;
    }
    .th-post__pagination {
        margin: 0 $local-padding 0;
    }
    .th-post__author {
        margin: 0 $local-padding 0;
        padding: $local-padding 0 $local-padding;
    }
}
