/*
// .woocommerce-orders-table
*/

.woocommerce-orders-table {
    @include th-reset-table;

    margin: 0;
    width: 100%;
    font-size: 15px;

    .button {
        @include th-button--size--extra-small;
        @include th-button--style--secondary;
    }

    th,
    td {
        @include th-reset-table-cell;
    }
}

@include media-breakpoint-up(md) {
    .woocommerce-orders-table {
        @include th-card;

        th,
        td {
            padding: .875rem .75rem;

            &:first-child {
                @include direction {
                    #{$padding-inline-start}: 1.25rem;
                }
            }
            &:last-child {
                @include direction {
                    #{$padding-inline-end}: 1.25rem;
                }
            }
        }
        th {
            padding-top: .75rem;
            padding-bottom: .75rem;
            text-transform: uppercase;
            font-size: 13px;
        }

        thead + tbody tr:first-child,
        tr + tr {
            th,
            td {
                border-top: 1px solid map-get($card-scheme, divider);
            }
        }

        .button {
            margin: -1px 0;
        }
    }
}
@include media-breakpoint-down(sm) {
    .woocommerce-orders-table {
        thead {
            display: none;
        }
        tbody,
        tr,
        th,
        td {
            display: block;
        }

        tr {
            @include th-card;

            padding: .75rem 0;
        }
        tr + tr {
            margin-top: .75rem;
        }

        td {
            display: flex;
            align-items: center;
            padding: .125rem 1.25rem;

            &:before {
                flex-shrink: 0;
                width: 50%;
                content: attr(data-title);
                vertical-align: middle;
                color: #6c757d;

                @include direction {
                    #{$padding-inline-end}: 1.5rem;
                }
            }
        }
    }
}
