@mixin th-owl-carousel {
    .owl-stage-outer {
        margin: -10px;
        padding: 10px;
    }
    .owl-dots {
        margin-top: 18px;
        display: flex;
        justify-content: center;
    }
    .owl-carousel button.owl-dot {
        &:before {
            display: block;
            content: '';
            width: 10px;
            height: 10px;
            border-radius: 5px;
            background: $body-carousel-dot-normal-color;
            transition: background .2s;
            margin: 6px;
        }
        &:focus {
            outline: none;
        }
        &:hover:before {
            background: $body-carousel-dot-hover-color;
        }
        &.active:before {
            @include th-var(background, --th-theme-scheme-main, $body-carousel-dot-active-color);
        }
    }
}
