/*
// .widget_search
*/

$local-height: 52px;
$local-line-height: 21px;

.widget_search {
    padding: 0;

    .th-search__body {
        display: flex;
        position: relative;
        z-index: 0;
    }
    .th-search__input {
        flex-grow: 1;
        appearance: none;
        border: none;
        background: none;
        padding: (($local-height - $local-line-height) / 2) 24px;
        font-family: inherit;
        font-size: 16px;
        line-height: $local-line-height;
        height: auto;
        min-height: auto;

        &:focus {
            outline: none;
        }

        &::placeholder {
            color: #6c757d;
        }
    }
    .th-search__button {
        flex-shrink: 0;
        border: none;
        background: none;
        display: flex;
        justify-content: center;
        align-items: center;
        color: rgba(#6c757d, .7);
        fill: currentColor;
        width: $local-height;
        height: $local-height;
        padding: 0;

        &:focus {
            outline: none;
        }
        &:hover {
            background: $widget-search-button-hover-bg-color;
        }
        &:active {
            background: $widget-search-button-active-bg-color;
        }
    }
    .th-search__button-title {
        display: none;
    }
    .th-search__box {
        position: absolute;
        display: block;
        content: '';
        z-index: -1;
        left: 0; // SKIP-RTL
        right: 0; // SKIP-RTL
        width: 100%;
        height: 100%;
        border-radius: 1px;
        transition: box-shadow .2s;
    }

    .th-search__body:hover .th-search__box {
        box-shadow: $widget-search-input-hover-shadow;
    }
    .th-search__input:focus ~ .th-search__box {
        box-shadow: $widget-search-input-focus-shadow;
    }


    .th-site-footer & {
        padding: 0 15px;

        .th-search__input {
            flex-shrink: 1;
            border-radius: 2px;
            font-size: 15px;
            height: 38px;
            padding: 0 12px;
            flex-basis: 0;
            min-width: 0;
            flex-grow: 1;
            font-family: inherit;
            border-width: 2px;
            border-style: solid;
            border-color: transparent;
            background-clip: padding-box;
            transition:
                border .2s,
                background .2s;

            @include define-input-scheme($form-control-dark-scheme, normal);

            &::placeholder {
                transition: color .2s;
            }
            &:hover {
                @include define-input-scheme($form-control-dark-scheme, hover);
            }
            &:focus {
                outline: none;

                @include define-input-scheme($form-control-dark-scheme, focus);
            }
        }
        .th-search__button {
            width: auto;
            height: 38px;
            flex-shrink: 0;
            border-radius: 2px;
            border: none;
            padding: 0 20px;
            font-family: inherit;
            font-size: 15px;
            transition:
                background .2s,
                color .2s;

            @include direction {
                #{$margin-inline-start}: 8px;
            }

            @include th-var(background-color, --th-btn-context-normal-bg-color, map_get($btn-dark-scheme, normal-bg-color));
            @include th-var(color, --th-btn-context-normal-font-color, map_get($btn-dark-scheme, normal-font-color));

            &:hover {
                @include th-var(background-color, --th-btn-context-hover-bg-color, map_get($btn-dark-scheme, hover-bg-color));
                @include th-var(color, --th-btn-context-hover-font-color, map_get($btn-dark-scheme, hover-font-color));
            }
            &:active {
                @include th-var(background-color, --th-btn-context-active-bg-color, map_get($btn-dark-scheme, active-bg-color));
                @include th-var(color, --th-btn-context-active-font-color, map_get($btn-dark-scheme, active-font-color));
            }

            &:active {
                transition-duration: .1s,  .1s;
            }
            &:focus {
                outline: none;
            }
        }
        .th-search__button-icon {
            display: none;
        }
        .th-search__button-title {
            display: block;
        }
        .th-search__box {
            display: none;
        }
    }
}
