/*
// .widget_rating_filter
*/

$local-margin: 8px;

.widget_rating_filter {
    ul {
        list-style: none;
        padding: 0;
        margin: (-$local-margin) 0;
    }
    li {
        margin: $local-margin 0;
        font-size: 14px;
        line-height: 16px;
        display: flex;
    }

    a {
        position: relative;
        flex-grow: 1;
        display: flex;
        justify-content: space-between;
        padding: 3px 0 1px;
        font-size: 12px;
        line-height: 12px;
        color: map_get($card-scheme, opposite-alt);

        @include direction {
            #{$padding-inline-start}: (16px + 7px);
        }

        @include th-fake-checkbox;
    }

    .star-rating {
        position: relative;
        top: -1px;
        display: block;
        flex-grow: 0;
    }

    .chosen a {
        @include th-fake-checkbox--checked;
    }
}
