/*
// .th-block-reviews
*/

$local-padding: 32px;
$local-avatar-size: 80px;
$local-avatar-expand: 16px + $local-padding;
$local-avatar-padding: 24px;

.th-block-reviews {}
.th-block-reviews__header {
    margin-bottom: 40px;
}
.th-block-reviews__title {
    font-size: 32px;
    font-weight: $font-weight-bold;
    text-align: center;
}
.th-block-reviews__subtitle {
    color: map_get($card-scheme, opposite-alt);
    text-align: center;
    font-size: 16px;
    margin-top: 4px;
}
.th-block-reviews__list {
    @include th-owl-carousel;

    .owl-dots {
        margin-top: 32px;
    }
    .owl-stage {
        display: flex;
    }
    .owl-item {
        flex-shrink: 0;
        display: flex;
    }
}
.th-block-reviews__item {
    @include th-card;

    margin: ($local-avatar-expand - $local-padding + $local-avatar-padding) auto 0;
    padding: $local-padding;
    border-radius: 2px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    flex-grow: 1;
}
.th-block-reviews__item-avatar {
    width: $local-avatar-size;
    flex-shrink: 0;
    position: relative;
    z-index: 0;
    margin-top: -$local-avatar-expand;

    img {
        max-width: 100%;
    }
}
.th-block-reviews__item-avatar-image {
    overflow: hidden;
    border-radius: 100%;
}
.th-block-reviews__item-avatar-shadow {
    position: absolute;
    z-index: -1;
    left: -($local-avatar-padding + 10px);
    right: -($local-avatar-padding + 10px);
    top: -($local-avatar-padding + 10px);
    height: $local-avatar-expand - $local-padding + $local-avatar-padding + 10px;
    pointer-events: none;
    overflow: hidden;

    &:before {
        @include th-card;

        content: '';
        display: block;
        padding-bottom: calc(100% - 20px);
        border-radius: 100%;
        margin: 10px;
    }
}
.th-block-reviews__item-author {
    margin-top: 20px;
    margin-bottom: 20px;
}
.th-block-reviews__item-author-name {
    font-size: 17px;
    font-weight: $font-weight-medium;
}
.th-block-reviews__item-author-position {
    font-size: 14px;
    margin-top: 2px;
    color: map_get($card-scheme, opposite-alt);
}
.th-block-reviews__item-text {
    font-size: 15px;
    line-height: 28px;
    color: map_get($card-scheme, opposite-alt);
    flex-grow: 1;
}
.th-block-reviews__item-rating {
    margin-top: 28px;
    padding-bottom: 4px;
}
