/*
// .th-wishlist
*/

$local-row-padding-x: 28px;

.th-wishlist {
    max-width: 1110px;
    margin: 0 auto;
}
.th-wishlist__table {
    @include th-reset-table;

    width: 100%;
    border-spacing: 0;
}
.th-wishlist__column {
    @include th-reset-table-cell;
}
// columns
.th-wishlist__column--head {
    font-size: 13px;
    text-transform: uppercase;
}
// product
.th-wishlist__product-name {
    line-height: 20px;

    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        @include th-var(color, --th-link-color, $link-color);
    }
}
.th-wishlist__product-rating {
    display: flex;
    margin-top: 6px;
}
.th-wishlist__product-rating-title {
    font-size: 13px;
    line-height: 1;
    color: map_get($card-scheme, opposite-alt);

    @include direction {
        #{$margin-inline-start}: 8px;
    }
}
.th-wishlist__column--button {
    .button,
    .added_to_cart {
        @include th-button--size--small;

        svg {
            display: none;
        }
    }
    .button.added {
        display: none;
    }
    .button.loading {
        position: relative;
        color: transparent;
        cursor: default;

        @include th-var(background, --th-btn-primary-normal-bg-color, map_get($addtocart-button-scheme, normal-bg-color));

        &:after {
            @include th-loader(18px, 2px, map_get($addtocart-button-scheme, normal-font-color));
        }
    }
}
.th-wishlist__remove {
    display: flex;
    justify-content: center;
    align-items: center;
}
.th-wishlist__dash {
    display: block;
    height: 1px;
    width: 16px;
    margin: 0 auto;
    background: #999;
    opacity: .5;
}


// table state
@include media-breakpoint-up(md) {
    .th-wishlist {
        @include th-card;
    }
    .th-wishlist__column {
        padding: 14px 16px;

        &:first-child {
            @include direction {
                #{$padding-inline-start}: $local-row-padding-x;
            }
        }
        &:last-child {
            @include direction {
                #{$padding-inline-end}: $local-row-padding-x;
            }
        }
    }

    // columns
    .th-wishlist__column--image {
        text-align: center;
        width: 1px;

        img {
            width: 80px;
            height: auto;
        }
    }
    .th-wishlist__column--body {
        border-top: 1px solid map_get($card-scheme, divider);
    }
    .th-wishlist__column--stock {
        text-align: center;
        width: 1px;
        white-space: nowrap;
    }
    .th-wishlist__column--price {
        white-space: nowrap;
        width: 140px;

        @include direction {
            text-align: $inline-end;
        }
    }
    .th-wishlist__column--button {
        white-space: nowrap;
        width: 200px;

        @include direction {
            text-align: $inline-end;
        }
    }
    .th-wishlist__column--remove {
        width: 1px;
        white-space: nowrap;

        @include direction {
            #{$padding-inline-start}: 0;
        }
    }

    // remove
    .th-wishlist__remove {
        @include direction {
            #{$margin-inline-end}: -12px;
        }
    }
}
@include media-breakpoint-between(md, lg) {
    .th-wishlist__column--button {
        width: 1px;
    }
}
@include media-breakpoint-only(md) {
    .th-wishlist__column {
        &:first-child {
            @include direction {
                #{$padding-inline-start}: 16px;
            }
        }
        &:last-child {
            @include direction {
                #{$padding-inline-end}: 16px;
            }
        }
    }

    .th-wishlist__column--product,
    .th-wishlist__column--stock,
    .th-wishlist__column--price,
    .th-wishlist__column--button {
        @include direction {
            #{$padding-inline-start}: 0;
        }
    }
    .th-wishlist__column--price {
        width: 1px;
    }

    .th-wishlist__remove {
        margin-top: 0;
        margin-bottom: 0;

        @include direction {
            #{$margin-inline-start}: -8px;
            #{$margin-inline-end}: -8px;
        }
    }
}


// grid state
$local-gutter: 16px;

@include media-breakpoint-down(sm) {
    .th-wishlist__table {
        display: block;
    }
    .th-wishlist__head {
        display: none;
    }
    .th-wishlist__body {
        display: flex;
        flex-wrap: wrap;
        margin: -($local-gutter / 2);
    }
    .th-wishlist__row,
    .th-wishlist__column {
        display: block;
    }
    .th-wishlist__column {
        padding: 0;
    }
    .th-wishlist__row {
        @include th-card;

        position: relative;
        margin: ($local-gutter / 2);
        width: calc(50% - #{$local-gutter});
        text-align: center;
        padding: 16px;
    }
    .wishlist__row--body {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .th-wishlist__product-rating {
        display: flex;
        justify-content: center;
    }

    .th-wishlist__column--stock {
        display: flex;
        justify-content: center;
    }
    .th-wishlist__column--image {
        padding-bottom: 12px;

        img {
            max-width: 220px;
            width: 100%;
            height: auto;
            margin: 0 auto;
        }
    }
    .th-wishlist__column--stock {
        margin-top: 16px;
        display: none;
    }
    .th-wishlist__column--price {
        margin-top: 8px;
        font-weight: $font-weight-medium;
    }
    .th-wishlist__column--button {
        padding: 20px 0 12px;
        margin-top: auto;
    }

    .th-wishlist__remove {
        position: absolute;
        top: 0;

        @include direction {
            #{$inset-inline-end}: 0;
        }
    }
}
@media (max-width: 474px){
    .th-wishlist__row {
        width: calc(100% - #{$local-gutter});
    }
}
