@mixin th-decor-style($bg, $shadow: none) {
    .th-decor__start,
    .th-decor__end,
    .th-decor__center {
        background: $bg;
    }
    .th-decor__start,
    .th-decor__end {
        box-shadow: $shadow;
    }
}
@mixin th-decor-bottom-height($height, $expand) {
    height: $height + $expand;

    .th-decor__start,
    .th-decor__end {
        height: $height;
    }
}
@mixin th-decor-bottom-radius($radius) {
    .th-decor__start {
        border-top-right-radius: $radius; // SKIP-RTL
    }
    .th-decor__end {
        border-top-left-radius: $radius; // SKIP-RTL
    }
}
@mixin th-decor-bottom-skew($angel) {
    .th-decor__start {
        transform: skewX($angel); // SKIP-RTL
    }
    .th-decor__end {
        transform: skewX(-$angel); // SKIP-RTL
    }
}
@mixin th-decor-center-size($height, $expand, $angel) {
    $padding-x-base: $height * tan($angel);

    .th-decor__body {
        height: $height + $expand;
        margin: 0 (-$expand) (-$expand);
    }
    .th-decor__start,
    .th-decor__end {
        height: $height;
        width: calc(50% - #{$expand + $padding-x-base});
    }
    .th-decor__start {
        left: $expand; // SKIP-RTL
        transform: skewX($angel); // SKIP-RTL
    }
    .th-decor__end {
        right: $expand; // SKIP-RTL
        transform: skewX(-$angel); // SKIP-RTL
    }
    .th-decor__center {
        height: $height;
        width: #{$padding-x-base * 2 + 2px};
    }
}
@mixin th-decor-center-radius($radius) {
    .th-decor__start {
        border-bottom-left-radius: $radius; // SKIP-RTL
    }
    .th-decor__end {
        border-bottom-right-radius: $radius; // SKIP-RTL
    }
}
