/*
// .th-block-products-columns
*/

.th-block-products-columns {}
.th-block-products-columns__title {
    border-bottom: 2px solid map_get($body-scheme, divider);
    margin-bottom: 20px;
    padding-bottom: 8px;
    font-size: 20px;
    font-weight: $font-weight-bold;
}
.th-block-products-columns__list {
    padding: 0;
    margin: 0;
}
.th-block-products-columns__list-item {
    @include th-reset-list-item;

    & + & {
        margin-top: 16px;
    }

    .th-product-card {
        @include product-card-horizontal-base;
    }
}


@include media-breakpoint-up(xxl) {
    .th-block-products-columns__list-item {
        .th-product-card {
            @include product-card-horizontal-base-md;
        }
    }
}
@include media-breakpoint-down(lg) {
    .th-block-products-columns {
        display: none;
    }
}
