@mixin th-product__tabs {
    .woocommerce-tabs {
        @content;
    }
}
@mixin th-product__rating {
    .woocommerce-product-rating {
        @content;
    }
}
@mixin th-product__rating-stars {
    .woocommerce-product-rating .star-rating {
        @content;
    }
}
@mixin th-product__fit--after-rating {
    .th-product__fit {
        .woocommerce-product-rating + & {
            @content;
        }
    }
}
@mixin th-product__price--primary {
    .th-product__summary > .price {
        @content;
    }
}
@mixin th-product__variations {
    .th-product__summary .variations {
        @content;
    }
}
@mixin th-product__form {
    .th-product__summary .cart {
        @content;
    }
}
@mixin th-product__form--after-primary-price {
    .th-product__summary > .price + .cart {
        @content;
    }
}
@mixin th-product__actions-container {
    .th-product__summary .woocommerce-variation-add-to-cart,
    .th-product__summary .cart:not(.variations_form) {
        @content;
    }
}
@mixin th-product__secondary-actions {
    .th-product__summary .th-wishlist-add,
    .th-product__summary .th-compare-add {
        @content;
    }
}


@mixin th-product--mobile {
    @include th-product__variations {
        width: 100%;
        margin-left: 0; // SKIP-RTL
        margin-right: 0; // SKIP-RTL
        padding: 0;

        background: transparent;
        border: none;
        margin-bottom: 12px;
    }
    @include th-product__form {
        margin-top: 14px;
    }
    @include th-product__actions-container {
        margin-top: 10px;
        margin-bottom: -8px;

        @include direction {
            #{$margin-inline-end}: -8px;
        }

        button[type="submit"] {
            flex-grow: 0;
        }

        & > .th-quantity,
        button[type="submit"] {
            @include direction {
                #{$margin-inline-end}: 8px;
            }

            margin-bottom: 8px;
        }
    }
    @include th-product__form--after-primary-price {
        margin-top: 0;
    }
    @include th-product__secondary-actions {
        width: 44px;
        height: 42px;
        margin-top: 0;
        margin-bottom: 8px;
        padding: 0;

        @include direction {
            #{$margin-inline-end}: 8px;
        }

        svg {
            top: 0;

            @include direction {
                #{$margin-inline-end}: 0;
            }
        }

        &__label {
            display: none;
        }
    }
    .th-product__tags,
    .th-product__share-buttons {
        margin-top: 20px;
        padding-top: 0;
        width: 100%;
        margin-left: 0; // SKIP-RTL
        margin-right: 0; // SKIP-RTL
        padding-left: 0; // SKIP-RTL
        padding-right: 0; // SKIP-RTL
        border-top: none;
    }
}
