/*
// .th-footer-newsletter
*/

.th-footer-newsletter {
    .th-subscribe-form {
        display: flex;
        flex-wrap: wrap;

        &:after {
            display: block;
            content: '';
            flex-shrink: 0;
            width: calc(100% - 380px);
        }
    }
    .th-subscribe-form__text {
        font-size: 15px;
        line-height: 22px;
        margin-bottom: 14px;
        width: 100%;
        order: -2;
    }
    .th-subscribe-form__field {
        flex-shrink: 1;
        border-radius: 2px;
        font-size: 15px;
        height: 38px;
        padding: 0 12px;
        flex-basis: 0;
        min-width: 0;
        flex-grow: 1;
        font-family: inherit;
        border-width: 2px;
        border-style: solid;
        border-color: transparent;
        background-clip: padding-box;
        transition:
            border .2s,
            background .2s;

        @include define-input-scheme($form-control-dark-scheme, normal);

        &::placeholder {
            transition: color .2s;
        }
        &:hover {
            @include define-input-scheme($form-control-dark-scheme, hover);
        }
        &:focus {
            outline: none;

            @include define-input-scheme($form-control-dark-scheme, focus);
        }
    }

    .th-subscribe-form__button {
        flex-shrink: 0;
        border-radius: 2px;
        border: none;
        padding: 0 20px;
        font-family: inherit;
        font-size: 15px;
        transition:
            background .2s,
            color .2s;

        @include direction {
            #{$margin-inline-start}: 8px;
        }

        @include th-var(background-color, --th-btn-context-normal-bg-color, map_get($btn-dark-scheme, normal-bg-color));
        @include th-var(color, --th-btn-context-normal-font-color, map_get($btn-dark-scheme, normal-font-color));

        &:hover {
            @include th-var(background-color, --th-btn-context-hover-bg-color, map_get($btn-dark-scheme, hover-bg-color));
            @include th-var(color, --th-btn-context-hover-font-color, map_get($btn-dark-scheme, hover-font-color));
        }
        &:active {
            @include th-var(background-color, --th-btn-context-active-bg-color, map_get($btn-dark-scheme, active-bg-color));
            @include th-var(color, --th-btn-context-active-font-color, map_get($btn-dark-scheme, active-font-color));
        }

        &:active {
            transition-duration: .1s,  .1s;
        }
        &:focus {
            outline: none;
        }
    }

    .mc4wp-response {
        margin-top: .5rem;
        font-size: 15px;
        line-height: 22px;
    }
    .mc4wp-alert > :last-child {
        margin-bottom: 0;
    }
}


.th-footer-newsletter__text {
    font-size: 15px;
    line-height: 22px;
    margin-top: 20px;
    margin-bottom: 0;
}
.th-footer-newsletter__social-links {
    margin-top: 8px;

    .menu .menu-item a {
        background: #4c4c4c;

        &:hover {
            background: #555555;
            opacity: 1;
        }
        &:active {
            background: #4c4c4c;
        }
    }
}



@include media-breakpoint-down(sm) {
    .th-footer-newsletter {
        text-align: center;

        .th-subscribe-form {
            justify-content: center;

            &:before {
                display: block;
                content: '';
                flex-shrink: 0;
            }

            &:before,
            &:after {
                width: calc((100% - 380px) / 2);
            }
        }
    }
    .th-footer-newsletter__text--social {
        margin-top: 24px;
    }
    .th-footer-newsletter__social-links {
        margin-top: 12px;
        display: flex;
        justify-content: center;
    }
}
