@mixin th-tag-badge-size($height, $padding-x, $angel) {
    $tan: tan($angel);

    $padding-x-skew: ceil($height * $tan);
    $padding-x-total: $padding-x-skew + $padding-x;

    height: $height;
    padding: (($height - 10px) / 2) ($padding-x-total) 0;

    &:before {
        left: $padding-x-skew / 2; // SKIP-RTL
        right: $padding-x-skew / 2; // SKIP-RTL

        @include direction {
            transform: skewX(#{$angel * $transform-direction}); // SKIP-RTL
        }
    }
}
