/*
// .woocommerce-customer-details
*/

.woocommerce-customer-details {
    position: relative;
    z-index: 0;

    .woocommerce-columns {
        --th-grid-gutter: 1.5rem;
        --th-grid-columns: 2;

        display: flex;
        flex-wrap: wrap;
        margin-top: calc(var(--th-grid-gutter) * -1);
        margin-left: calc(var(--th-grid-gutter) * -1); // SKIP-RTL
    }
    .woocommerce-column {
        flex: 0 0 calc(100% / var(--th-grid-columns) - var(--th-grid-gutter));
        max-width: calc(100% / var(--th-grid-columns) - var(--th-grid-gutter));
        padding: 0;
        position: relative;
        margin-top: var(--th-grid-gutter);
        margin-left: var(--th-grid-gutter); // SKIP-RTL
    }

    h2 {
        padding: .875rem 1.25rem .5rem;
        font-size: 22px;
        margin-bottom: 0;

        &:before {
            @include th-card;

            position: absolute;
            content: '';
            left: 0; // SKIP-RTL
            right: 0; // SKIP-RTL
            top: 0;
            bottom: 0;
            z-index: -1;
        }
    }

    address {
        @include th-reset-address;

        padding: .25rem 1.25rem 1rem;
        font-size: 14px;
        color: #6c757d;

        p {
            margin-top: .5rem;
            margin-bottom: 0;
        }
        p + p {
            margin-top: 0;
        }
    }
}

@include media-breakpoint-down(sm) {
    .woocommerce-customer-details {
        .woocommerce-columns {
            --th-grid-columns: 1;
        }
    }
}
