/*
// .th-block-teammates
*/

.th-block-teammates {}


.th-block-teammates__header {
    margin-bottom: 40px;
}
.th-block-teammates__title {
    font-size: 32px;
    font-weight: $font-weight-bold;
    text-align: center;
}
.th-block-teammates__subtitle {
    color: map_get($card-scheme, opposite-alt);
    text-align: center;
    font-size: 16px;
    margin-top: 4px;
}
.th-block-teammates__list {
    display: flex;

    @include th-owl-carousel;

    .owl-dots {
        margin-top: 32px;
    }
    .owl-stage {
        display: flex;
    }
    .owl-item {
        flex-shrink: 0;
        display: flex;
    }
}
.th-block-teammates__item {
    flex-shrink: 0;
    margin: 0 auto;
    flex-grow: 1;
    width: 100%;
}

@include media-breakpoint-down(sm) {
    .th-block-teammates__list {
        padding: 0 10px;
    }
}
