/*
// .comments-list
*/

.comment-list {
    &,
    .children {
        list-style: none;
        padding: 0;
        margin: 0;
    }

    & > li,
    .children > li {
        margin: 0;
    }

    & > li + li,
    .children > li + li,
    .children {
        margin-top: 28px;
    }

    .depth-1 {
        & > .children {
            margin-top: 14px;
            padding-top: 18px;
            margin-bottom: -12px;
            padding-bottom: 15px;

            @include direction {
                #{$border-inline-start}: 1px solid map_get($card-scheme, divider);
                #{$padding-inline-start}: ($comment-avatar-size / 2 + $comment-avatar-margin - 2px);
                #{$margin-inline-start}: ($comment-avatar-size / 2 - 1px);
            }
        }
    }

    @include media-breakpoint-up(md) {
        .depth-2,
        .depth-3 {
            & > .children {
                margin-top: 14px;
                padding-top: 18px;
                margin-bottom: -12px;
                padding-bottom: 15px;

                @include direction {
                    #{$border-inline-start}: 1px solid map_get($card-scheme, divider);
                    #{$padding-inline-start}: ($comment-avatar-size / 2 + $comment-avatar-margin - 2px);
                    #{$margin-inline-start}: ($comment-avatar-size / 2 - 1px);
                }
            }
        }
    }

    .comment:last-child > .children {
        padding-bottom: 0;
        margin-bottom: 0;
    }

    @include media-breakpoint-down(sm) {
        & > li + li,
        .children > li + li,
        .children {
            margin-top: 24px;
        }
        .depth-1 > .children {
            padding-top: 14px;
            margin-bottom: -8px;
        }
        .comment:last-child > .children {
            padding-bottom: 0;
        }
    }
    @media (max-width: 419px) {
        & > li + li,
        .children > li + li,
        .children {
            margin-top: 20px;
        }
        .depth-1 > .children {
            padding-top: 12px;

            @include direction {
                #{$padding-inline-start}: 20px;
                #{$margin-inline-start}: 12px;
            }
        }
    }

    .comment-awaiting-moderation {
        display: block;
        margin-top: 8px;
        border-radius: 3px;
        line-height: 1.375;
        background: #f2e291;
        color: #4d472e;
        padding: 12px 17px;
        font-size: 14px;

        @include direction {
            #{$margin-inline-end}: -64px;
        }
    }

    .comment-body {
        position: relative;
    }
    .comment-meta {
        position: relative;

        @include direction {
            #{$margin-inline-end}: 64px;
        }
    }

    $local-arrow-size: 10px;

    .comment-content {
        background: map_get($child-card-scheme, main);
        padding: 12px 17px;
        border-radius: 5px;
        font-size: 15px;
        line-height: 1.625;
        width: 100%;
        position: relative;
        margin-top: 12px;

        &:before {
            position: absolute;
            content: '';
            display: block;
            top: -$local-arrow-size;
            border: ($local-arrow-size / 2) solid transparent;
            border-bottom-color: map_get($child-card-scheme, main);
            pointer-events: none;

            @include direction {
                #{$inset-inline-start}: ($comment-avatar-size / 2 - $local-arrow-size / 2);
            }
        }

        & > :first-child {
            margin-top: 0;
        }
        & > :last-child {
            margin-bottom: 0;
        }
    }
    .avatar {
        overflow: hidden;
        border-radius: 50%;
        width: $comment-avatar-size;
        height: $comment-avatar-size;

        @include direction {
            #{$margin-inline-end}: $comment-avatar-margin;
        }
    }

    .bypostauthor {
        $local-border-size: 2px;

        .comment-content {
            border: $local-border-size solid var(--th-theme-scheme-main);

            &:before {
                top: -($local-arrow-size + $local-border-size);
                border-bottom-color: var(--th-theme-scheme-main);

                @include direction {
                    #{$inset-inline-start}: ($comment-avatar-size / 2 - $local-arrow-size / 2 - $local-border-size);
                }
            }
        }
    }

    .comment-author {
        display: flex;

        a {
            color: inherit;
        }
        a:hover {
            text-decoration: underline;
        }

        .fn {
            font-size: 15px;
            line-height: 19px;
            font-weight: $font-weight-medium;
            margin-top: 2px;
        }

        .says {
            display: none;
        }
    }
    .comment-metadata {
        font-size: 13px;
        color: map_get($card-scheme, muted);
        position: relative;
        display: flex;
        flex-wrap: wrap;
        overflow: hidden;

        @include direction {
            #{$margin-inline-end}: -21px;
        }

        a {
            color: inherit;
        }
        a:hover {
            text-decoration: underline;
        }

        & > a {
            @include direction {
                #{$margin-inline-end}: 21px;
            }
        }

        .edit-link {
            display: flex;

            @include direction {
                #{$margin-inline-start}: -21px;
                #{$margin-inline-end}: 21px;
            }

            &:before {
                margin: 0 10px;
                display: block;
                content: '';
                align-self: center;
                height: calc(100% - 6px);
                width: 1px;
                background: currentColor;
                opacity: .4;
            }
        }
    }

    .comment-reply-title a,
    .comment-reply-link {
        font-size: 13px;
        font-weight: 500;
        line-height: 15px;
        padding: 4.5px 9px;
        border-radius: 2px;
        transition:
            color .15s ease-in-out,
            background-color .15s ease-in-out,
            border-color .15s ease-in-out,
            box-shadow .15s ease-in-out;

        background: map-get($btn-light-scheme, normal-bg-color);
        color: map-get($btn-light-scheme, normal-font-color);

        &:hover {
            background: map-get($btn-light-scheme, hover-bg-color);
            color: map-get($btn-light-scheme, hover-font-color);
        }
        &:active {
            background: map-get($btn-light-scheme, active-bg-color);
            color: map-get($btn-light-scheme, active-font-color);
        }
    }

    .comment-reply-link {
        position: absolute;
        top: (38px / 2) - (24px / 2);

        @include direction {
            #{$inset-inline-end}: 0;
        }
    }

    @media (min-width: 576px) {
        .comment-metadata {
            margin-top: -19px;
            max-width: calc(100% - #{$comment-avatar-size + $comment-avatar-margin - 21px});

            @include direction {
                #{$inset-inline-start}: $comment-avatar-size + $comment-avatar-margin;
            }
        }

        .pingback,
        .trackback {
            .comment-metadata {
                margin-top: 0;

                @include direction {
                    #{$inset-inline-start}: 0;
                }
            }
        }
    }
    @media (max-width: 575px) {
        .comment-content {
            font-size: 14px;
        }
        .avatar {
            display: none;
        }
    }

    & + .comments-pagination {
        margin-top: 36px;
        display: flex;
        justify-content: center;

        &:empty {
            display: none;
        }

        @include wordpress-pagination;

        @include media-breakpoint-down(md) {
            margin-top: 32px;
        }
        @include media-breakpoint-down(xs) {
            margin-top: 24px;
        }
    }
}
