.th-product--layout--full {
    .th-product__body {
        display: grid;
        grid-template-columns: auto 1fr 370px;
        grid-template-rows: max-content auto auto 1fr;
    }
    .th-product__card--one {
        grid-column: 1 / 3;
        grid-row: 1 / 4;
    }
    .th-product__gallery {
        grid-column: 1;
        grid-row: 1 / 4;
    }
    .th-product__header {
        grid-column: 2;
        grid-row: 1;
    }
    .th-product__intro {
        grid-column: 2;
        grid-row: 2;
    }
    .th-product__sidebar {
        grid-column: 3;
        grid-row: 1 / 5;
    }
    .woocommerce-tabs {
        grid-column: 1 / 3;
        grid-row: 4;
    }


    --th-product-gutter: 30px;


    .th-product__card {
        @include th-card;
    }
    .th-product__card--two {
        display: none;
    }
    .th-product__gallery {
        --th-product-gallery-image-max-width: 400px;

        $local-padding-start: 40px;

        padding-top: 36px;
        padding-bottom: 40px;
        width: 400px + $local-padding-start;

        @include direction {
            #{$padding-inline-start}: $local-padding-start;
            #{$padding-inline-end}: 0;
        }

        .flex-viewport,
        .woocommerce-product-gallery__wrapper {
            max-width: var(--th-product-gallery-image-max-width);
            margin: 0 auto;
        }
        .flex-viewport .woocommerce-product-gallery__wrapper {
            max-width: none;
            margin: 0;
        }
        .woocommerce-product-gallery {
            --th-gallery-thumbnail-gutter: 10px;
            --th-gallery-thumbnail-columns: 6;

            @include media-breakpoint-down(xl) {
                --th-gallery-thumbnail-columns: 5;
            }
            @include media-breakpoint-down(lg) {
                --th-gallery-thumbnail-columns: 7;
            }
            @include media-breakpoint-down(md) {
                --th-gallery-thumbnail-columns: 8;
            }
            @include media-breakpoint-down(sm) {
                --th-gallery-thumbnail-columns: 6;
            }
            @media (max-width: 575px) {
                --th-gallery-thumbnail-columns: 5;
                --th-gallery-thumbnail-gutter: 8px;
            }
            @media (max-width: 419px) {
                --th-gallery-thumbnail-columns: 4;
            }
        }
    }
    .th-product__header {
        padding-top: 36px;
        padding-bottom: 16px;

        @include direction {
            #{$padding-inline-start}: 36px;
            #{$padding-inline-end}: 40px;
        }
    }
    .th-product__intro {
        padding-top: 0;
        padding-bottom: 40px;

        @include direction {
            #{$padding-inline-start}: 36px;
            #{$padding-inline-end}: 40px;
        }
    }
    .th-product__sidebar {
        @include direction {
            #{$margin-inline-start}: var(--th-product-gutter);
        }
    }
    .woocommerce-tabs {
        margin-top: var(--th-product-gutter);
        align-self: flex-start;
    }
    .woocommerce-tabs {
        --th-woocommerce-tabs-padding: 48px;

        @include media-breakpoint-down(xl) {
            --th-woocommerce-tabs-padding: 36px;
        }
        @include media-breakpoint-down(sm) {
            --th-woocommerce-tabs-padding: 28px;
        }
        @media (max-width: 419px) {
            --th-woocommerce-tabs-padding: 24px;
        }
    }







    .th-product__subtitle {
        padding-top: 8px;
    }
    .th-product__gallery-intro {
        display: flex;
    }
    //.th-product__excerpt
    .woocommerce-product-details__short-description {
        font-size: 15px;
    }
    .th-product__features {
        margin: 20px 0 0;
        font-size: 14px;
        border-radius: 3px;

        $local-gutter: 12px;
        $local-columns: 1;

        ul {
            list-style: none;
            padding: 0;
            color: map_get($card-scheme, opposite-alt);
            margin: 0 -($local-gutter / 2);
            display: flex;
            flex-wrap: wrap;
        }
        li {
            padding-top: 3px;
            padding-bottom: 2px;
            position: relative;
            margin: 0 ($local-gutter / 2);
            width: calc(100% / #{$local-columns} - #{$local-gutter});

            @include direction {
                #{$padding-inline-start}: 11px;
                #{$padding-inline-end}: 0;
            }

            &:before {
                $local-size: 3px;

                position: absolute;
                display: block;
                content: '';
                width: $local-size;
                height: $local-size;
                border-radius: $local-size / 2;
                background: currentColor;
                top: 12px;
                opacity: .85;

                @include direction {
                    #{$inset-inline-start}: 0;
                }
            }
        }
    }
    .th-product__features-title {
        font-weight: $font-weight-medium;
        padding-bottom: 12px;
    }
    .th-product__features-link {
        $local-expand: 2px;

        margin: 20px (-$local-expand) 0;
        padding: 20px $local-expand 0;
        font-size: 14px;
        border-top: 1px solid map_get($card-scheme, divider);

        a:hover {
            text-decoration: underline;
        }
    }


    @include media-breakpoint-up(xl) {
        // styles
        .th-product__subtitle {
            flex-direction: column;
            align-items: flex-start;
        }
        .th-product__fit {
            order: -1;

            .th-status-badge {
                margin-bottom: 11px;

                @include direction {
                    #{$margin-inline-start}: -4px;
                }
            }
        }
    }
    @include media-breakpoint-up(lg) {
        // styles
        .th-product__summary {
            @include th-card;
        }
    }
    @include media-breakpoint-only(md) {
        // styles
        .th-product__gallery {
            @include th-card;
        }
    }
    @include media-breakpoint-down(xs) {
        // styles
        .th-product__subtitle {
            flex-direction: column;
            align-items: flex-start;
        }
        .th-product__fit {
            order: -1;

            .th-status-badge {
                margin-bottom: 11px;

                @include direction {
                    #{$margin-inline-start}: -4px;
                }
            }
        }
    }
    @include media-breakpoint-only(md) {
        // actions
        $local-size: 40px;
        $local-icon-size: 16px;

        .th-product__header h1 {
            @include direction {
                #{$padding-inline-end}: (52px + 12px + 16px);
            }
        }
    }


    @include media-breakpoint-between(sm, md) {
        // styles
        .th-product__meta {
            $local-gutter: 20px;

            table,
            th,
            td {
                display: block;
            }
            tbody {
                display: flex;
                flex-wrap: wrap;
            }
            tr,
            th {
                width: calc(50% - #{$local-gutter / 2});
            }
            tr {
                display: flex;

                &:nth-child(2n) {
                    @include direction {
                        #{$margin-inline-start}: $local-gutter;
                    }
                }
            }
        }
    }


    @include media-breakpoint-down(xl) {
        // variables
        --th-product-gutter: 24px;

        // grid
        .th-product__body {
            grid-template-columns: auto 1fr (312px + 24px);
        }

        // styles
        .th-product__gallery {
            $local-padding-start: 28px;

            padding-top: 24px;
            padding-bottom: 36px;
            width: 320px + $local-padding-start;

            @include direction {
                #{$padding-inline-start}: $local-padding-start;
                #{$padding-inline-end}: 0;
            }
        }
        .th-product__header {
            padding-top: 24px;
            padding-bottom: 16px;

            @include direction {
                #{$padding-inline-start}: 28px;
                #{$padding-inline-end}: 28px;
            }
        }
        .th-product__intro {
            padding-top: 0;
            padding-bottom: 36px;

            @include direction {
                #{$padding-inline-start}: 28px;
                #{$padding-inline-end}: 28px;
            }
        }
    }

    @include media-breakpoint-down(lg) {
        // grid
        .th-product__body {
            grid-template-columns: auto 1fr (312px + 24px);
            grid-template-rows: max-content auto auto 1fr;
        }
        .th-product__card {
            grid-column: 1 / 3;
            grid-row: 1 / 4;
        }
        .th-product__gallery {
            grid-column: 1 / 3;
            grid-row: 3;
        }
        .th-product__header {
            grid-column: 1 / 3;
            grid-row: 1;
        }
        .th-product__intro {
            grid-column: 1 / 3;
            grid-row: 2;
        }
        .th-product__sidebar {
            grid-column: 3;
            grid-row: 1 / 4;
        }
        .woocommerce-tabs {
            grid-column: 1 / 4;
            grid-row: 4;
        }

        // styles
        .th-product__features {
            display: none;
        }
        .th-product__intro {
            padding-bottom: 16px;
        }
        //.th-product__excerpt
        .woocommerce-product-details__short-description {
            border-bottom: 1px solid map_get($card-scheme, divider);
            padding-bottom: 14px;
        }
        .th-product__gallery {
            --th-product-gallery-image-max-width: 380px;
            padding-top: 0;
            width: 100%;

            @include direction {
                #{$padding-inline-end}: 28px;
            }
        }
        .th-product__header {
            padding-bottom: 10px;
        }
        .th-product__sidebar {
            display: flex;
        }
        // .th-product__shop-features
        .th-product__widgets {
            display: none;
        }
    }

    @include media-breakpoint-down(md) {
        @include th-product--mobile;

        // variables
        --th-product-gutter: 16px;

        // grid
        .th-product__body {
            grid-template-columns: 100%;
            grid-template-rows: auto;
        }
        .th-product__header {
            grid-column: 1;
            grid-row: 1;
        }
        .th-product__gallery {
            grid-column: 1;
            grid-row: 2;
        }
        .th-product__card--two {
            grid-column: 1;
            grid-row: 3 / 5;
        }
        .th-product__intro {
            grid-column: 1;
            grid-row: 3;
        }
        .th-product__sidebar {
            grid-column: 1;
            grid-row: 4;
        }
        .woocommerce-tabs {
            grid-column: 1;
            grid-row: 5;
        }

        // styles
        .th-product__card--one {
            display: none;
        }
        .th-product__card--two {
            display: block;
        }
        .th-product__header {
            @include th-card;

            padding-top: 24px;
            padding-bottom: 22px;

            @include direction {
                #{$padding-inline-start}: 24px;
                #{$padding-inline-end}: 24px;
            }
        }
        .th-product__header h1 {
            font-size: 22px;
            line-height: 28px;
        }
        .th-product__subtitle {
            padding-top: 8px;
        }
        .th-product__body {
            width: 100%;
        }
        .th-product__gallery {
            @include th-card;

            --th-product-gallery-image-max-width: 360px;
            margin-top: var(--th-product-gutter);
            width: 100%;
            padding-top: 24px;
            padding-bottom: 28px;

            @include direction {
                #{$padding-inline-start}: 24px;
                #{$padding-inline-end}: 24px;
            }
        }
        .th-product__sidebar {
            width: 100%;

            @include direction {
                #{$margin-inline-start}: 0;
            }
        }
        .th-product__intro,
        .th-product__card--two {
            margin-top: var(--th-product-gutter);
        }
        .th-product__intro {
            padding-top: 24px;
            padding-bottom: 18px;

            &:empty {
                padding-bottom: 0;
            }

            @include direction {
                #{$padding-inline-start}: 24px;
                #{$padding-inline-end}: 24px;
            }
        }
        //.th-product__excerpt
        .woocommerce-product-details__short-description {
            border: none;
            padding-bottom: 0;
        }
        .th-product__summary {
            --th-product__summary--padding-x: 24px;

            position: static;
            margin-top: 0;
            padding-top: 0;
            padding-bottom: 28px;
        }
        .th-product__meta {
            border-top: 1px solid map_get($card-scheme, divider);
            border-bottom: 1px solid map_get($card-scheme, divider);
            padding: 10px 0;
            margin: 8px 0;

            th,
            td {
                padding-top: 1px;
                padding-bottom: 1px;
            }
            tr + tr > * {
                border: none;
            }
        }
        .th-product-gallery[data-layout="product-full"] .th-product-gallery__zoom {
            top: -9px - 6px;

            @include direction {
                #{$inset-inline-end}: -9px - 6px;
            }
        }
    }
}
