/*
// .widget_rss
*/

.widget_rss {
    $local-gutter: 16px;

    .th-widget__title {
        img {
            display: none;
        }

        a {
            color: inherit;
        }
    }

    ul {
        @include th-reset-list;

        list-style: none;
        padding: 0;
        font-size: 15px;
        line-height: (20 / 15);
        margin: 24px 0 0;
    }
    li {
        @include th-reset-list-item;
    }
    li + li {
        margin-top: $local-gutter;
        padding-top: $local-gutter;
        border-top: 1px solid #ebebeb;
    }

    li  a {
        & {
            color: inherit;
            transition: color .2s;
        }
        &:hover {
            text-decoration: underline;
        }
    }

    .th-widget__title a {
        & {
            color: inherit;
            transition: color .2s;
        }
        &:hover {
            color: $link-hover-color;
        }
    }
    .th-widget__title a:first-child {
        display: none;
    }

    .rss-date {
        margin-top: 5px;
        display: block;
        color: #999;
        font-size: 13px;

        @include th-decor-line(16px);
    }

    .rssSummary {
        $local-line-height: 20px;
        $local-max-lines: 4;

        position: relative;
        margin-top: 8px;
        font-size: 14px;
        line-height: $local-line-height;
        overflow: hidden;
        max-height: $local-line-height * $local-max-lines;
        color: #6c757d;
    }


    .th-site-footer__widget-title a {
        color: #fff;

        &:hover {
            text-decoration: underline;
        }
        &:first-child {
            display: none;
        }
    }

    .th-site-footer & {
        li a {
            color: #fff;

            &:hover {
                text-decoration: underline;
            }
        }
        li + li {
            border-top: 1px solid rgba(#fff, .08);
        }
        .rss-date {
            margin-top: 3px;
            color: #9e9e9e;
        }
        .rssSummary {
            color: #9e9e9e;
            margin-top: 12px;
        }
    }
}
