select,
.th-select {
    @include th-select;
}
.th-select--size--small {
    @include th-select--size--small;
}

.th-site-footer {
    select {
        color: #fff;
        background-color: #4c4c4c;
        border: 1px solid #4c4c4c;
        background-image: url(svg-select-arrow(rgba(#fff, .55)));

        &:focus {
            outline: none;
        }
        &:hover,
        &:focus {
            background-color: #555;
            border-color: #555;
        }
    }
}
