/*
// .woocommerce-account
*/

.woocommerce-account {
    .woocommerce {
        max-width: 1110px;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
    }
    .woocommerce-pagination {
        display: flex;
        justify-content: center;
        margin-top: 1.5rem;
        margin-left: -6px; // SKIP-RTL

        .woocommerce-button {
            @include th-button--size--small;
            @include th-button--style--secondary;

            margin-left: 6px; // SKIP-RTL
        }
    }
}
.woocommerce-MyAccount-navigation {
    @include th-card;

    flex-shrink: 0;
    font-size: 15px;
    line-height: 20px;

    @include direction {
        #{$margin-inline-end}: 30px;
    }

    ul {
        list-style: none;
        padding: 1rem 0;
        margin: 0;
    }

    li {
        @include th-reset-list-item;
    }

    a {
        cursor: pointer;
        display: block;
        color: map_get($card-scheme, opposite-alt);
        padding: 8px 1.5rem;
    }
    a:hover {
        background: map_get($card-scheme, hover);
    }

    .is-active a {
        color: inherit;
        font-weight: $font-weight-medium;
    }

    // responsive
}
.woocommerce-MyAccount-content {
    flex-grow: 1;
    flex-basis: 0;

    & > p {
        @include th-card;

        margin: 0;
        padding: 1rem 1.25rem;
    }
    & > p:not(:last-child),
    & > section:not(:last-child) {
        margin-bottom: 1.5rem;
    }
}

@include media-breakpoint-up(lg) {
    .woocommerce-MyAccount-navigation {
        width: 255px;

        .is-active a {
            //noinspection CssOverwrittenProperties
            @include direction {
                box-shadow: #{3px * $transform-direction} 0 $account-nav-border-color inset;
                box-shadow: #{3px * $transform-direction} 0 var(--th-theme-scheme-main) inset;
            }
        }
    }
}
@include media-breakpoint-only(lg) {
    .woocommerce-MyAccount-navigation {
        width: 210px;
    }
}
@include media-breakpoint-down(md) {
    .woocommerce-account {
        .woocommerce {
            display: block;
        }
    }
    .woocommerce-MyAccount-navigation {
        margin-bottom: 1.5rem;
        max-width: 100%;
        overflow-x: auto;

        @include direction {
            #{$margin-inline-end}: 0;
        }

        ul {
            display: flex;
            white-space: nowrap;
            padding: 0 8px;
        }

        a {
            padding: 14px 20px;
        }

        //noinspection CssOverwrittenProperties
        .is-active a {
            box-shadow: 0 -3px $account-nav-border-color inset;
            box-shadow: 0 -3px var(--th-theme-scheme-main) inset;
        }
    }
}
