/*
// .th-view-options
*/

.th-view-options {
    font-size: .9375rem;
    display: flex;
    flex-wrap: wrap;
}
.th-view-options__body {
    padding: .75rem 1rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    margin-top: -.5rem;
    flex-grow: 1;

    @include direction {
        #{$margin-inline-start}: -1.25rem;
    }

    & + & {
        border-top: 1px solid map_get($card-scheme, divider);
    }
}
.th-view-options__body--filters {
    padding: .875rem 1rem;
    width: 100%;
    margin-top: 0;

    @include direction {
        #{$margin-inline-start}: 0;
    }
}
.th-view-options__label {
    position: absolute;
    background: map_get($card-scheme, main);
    padding: 0 8px;
    top: -5px;
    font-size: 10px;
    text-transform: uppercase;
    line-height: 1;
    color: map_get($card-scheme, muted);

    @include direction {
        #{$inset-inline-start}: 50%;
        transform: translateX(#{-50% * $transform-direction}); // SKIP-RTL
    }
}
.th-view-options__filters-button,
.th-view-options__layout,
.th-view-options__legend,
.th-view-options__select {
    margin-top: .5rem;

    @include direction {
        #{$margin-inline-start}: 1.25rem;
    }
}
.th-view-options__legend {
    white-space: nowrap;

    p {
        margin: 0;
    }
}
.th-view-options__spring {
    flex-grow: 1;
}
.th-view-options__select {
    display: flex;
    align-items: center;

    & > label {
        margin-bottom: 0;
        white-space: nowrap;

        @include direction {
            #{$margin-inline-end}: 10px;
        }
    }
}


.th-view-options--offcanvas--mobile {
    @include media-breakpoint-up(lg) {
        .th-view-options__filters-button {
            display: none;
        }
    }
}


@include media-breakpoint-down(md) {
    .th-view-options__legend,
    .th-view-options__select {
        @include direction {
            #{$padding-inline-start}: 1.25rem;
            #{$margin-inline-start}: auto;
        }
    }
    .th-view-options__spring {
        width: 100%;
        height: 0;
    }
    .th-view-options__select + .th-view-options__select {
        @include direction {
            #{$margin-inline-start}: 0;
        }
    }
}
@include media-breakpoint-down(xs) {
    .th-view-options__legend {
        width: 100%;
        padding-top: 6px;
    }
    .th-view-options__select {
        flex-direction: column;
        align-items: flex-start;

        @include direction {
            #{$margin-inline-start}: 0;
        }

        & > label {
            margin: 0 0 2px;
        }
    }
    .th-view-options__layout {
        @include direction {
            #{$margin-inline-start}: auto;
            #{$margin-inline-end}: 0;
        }
    }
}
