/*
// .th-contact-form
*/

.th-contact-form {
    input[type=email],
    input[type=password],
    input[type=tel],
    input[type=text],
    input[type=url] {
        height: 40px;
    }

    input[type=email],
    input[type=number],
    input[type=password],
    input[type=tel],
    input[type=text],
    input[type=url],
    textarea {
        padding: 9.5px 14px;
    }

    input[type=submit] {
        height: 40px;
        display: block;
        width: 100%;
    }

    .ajax-loader:not(.is-active) {
        display: none;
    }

    .wpcf7-not-valid-tip {
        font-size: 14px;
        margin-top: 4px;
    }
    form.invalid .wpcf7-response-output {
        margin: 1.25rem 0 0;
        padding: .5rem 1rem;
        border-radius: 2px;
        font-size: 15px;
    }
}
