/*
// .woocommerce-checkout
*/

.woocommerce-checkout {
    .woocommerce {
        max-width: 1110px;
        margin: 0 auto;
    }

    .woocommerce-account-fields {
        & > p:first-child {
            margin-bottom: 0;
        }
        .form-row {
            width: 100%;
        }
    }

    .woocommerce-form__label-for-checkbox {
        display: block;
        margin-bottom: 0;

        input {
            position: relative;
            top: -2px;
        }
        input + span {
            @include direction {
                #{$margin-inline-start}: 4px;
            }
        }
    }

    .woocommerce-account-fields,
    .woocommerce-shipping-fields {
        & > div {
            &:before,
            &:after {
                width: 100%;
                content: '';
                display: block;
            }
            &:before {
                height: .5rem;
            }
            &:after {
                height: 1.25rem;
            }
        }

        &:nth-last-child(2) {
            & > div {
                &:after {
                    height: .5rem;
                }
            }
        }
    }
    .woocommerce-account-fields,
    .woocommerce-shipping-fields {
        margin-top: 1.5rem;
    }
    .woocommerce-account-fields + .woocommerce-shipping-fields {
        margin-top: .25rem;
    }
    .woocommerce-additional-fields {
        margin-top: 1rem;
    }

    .place-order.form-row {
        margin: 0;
    }
}
#ship-to-different-address {
    font-weight: $font-weight-normal;
    margin-bottom: 0;
}
form.woocommerce-checkout {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;

    .woocommerce-NoticeGroup {
        width: 100%;
    }
}
#customer_details {
    @include th-card;

    padding: 2rem;
    flex-basis: 0;
    flex-grow: 1;
    display: block;

    .col-1,
    .col-2 {
        flex-basis: 100%;
        max-width: 100%;
        padding: 0;
        margin: 0;
    }
}
.woocommerce-billing-fields,
.woocommerce-checkout-summary {
    & > h3 {
        font-size: 24px;
        font-weight: 500;
        line-height: 1.2;
        padding-bottom: 8px;
        border-bottom: 1px solid #ebebeb;
        margin-bottom: 1.75rem;
        margin-top: -4px;
    }
}
.woocommerce-checkout-summary {
    @include th-card;

    width: 450px;
    padding: 2rem;

    @include direction {
        #{$margin-inline-start}: 32px;
    }
}
.woocommerce-additional-fields__field-wrapper {
    .form-row {
        width: 100%;
    }
}

.wc_payment_methods {
    padding: 0;
    list-style: none;

    &,
    &:first-child {
        margin: 28px 0 0;
    }

    li {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        padding: 8px 14px 2px;
        position: relative;
        margin-bottom: 0;
        z-index: 0;

        &:after {
            width: 100%;
            display: block;
            content: '';
            height: 6px;
        }
    }
    li + li {
        margin-top: 6px;
    }

    label {
        margin-bottom: 0;

        img {
            display: none;
        }

        &:before {
            position: absolute;
            display: block;
            z-index: -1;
            content: '';
            left: 0; // SKIP-RTL
            right: 0; // SKIP-RTL
            top: 0;
            bottom: 0;
            border-radius: 2px;
            border: 1px solid #ebebeb;
            transition:
                background .12s,
                border-color .12s;
        }
        &:hover:before {
            background: #f5f5f5;
            border-color: #e5e5e5;
        }
    }
    input:checked ~ label:before {
        background: #f5f5f5;
        border-color: #e5e5e5;
    }

    input[type=radio] + label {
        @include direction {
            #{$margin-inline-start}: 8px;
        }
    }

    .payment_box {
        width: 100%;
        font-size: 14px;
        line-height: 22px;
        color: #6c757d;

        p {
            margin: 0;
        }

        &:before {
            display: block;
            content: '';
            height: 6px;
        }
    }
}

.woocommerce-checkout {
    .woocommerce-terms-and-conditions {
        margin: 0 -2rem 1rem;
        padding: 0 2rem;
        background: rgba(#000, .03);
        font-size: 15px;

        h1, h2, h3, h4, h5, h6 {
            line-height: 1.2;
            margin-top: 1em;
            margin-bottom: .5em;
            font-weight: $font-weight-medium;

            & + {
                h1, h2, h3, h4, h5, h6 {
                    margin-top: 0;
                }
            }
        }
        h1 { font-size: 1.625em; }
        h2 { font-size: 1.5em; }
        h3 { font-size: 1.375em; }
        h4 { font-size: 1.25em; }
        h5 { font-size: 1.125em; }
        h6 {
            text-transform: uppercase;
            font-size: 1em;
            font-weight: $font-weight-medium;
        }

        & > :first-child {
            margin-top: 0;
        }
        & > :last-child {
            margin-bottom: 0;
        }

        &:before,
        &:after {
            display: block;
            content: '';
            height: 1.5rem;
        }


    }
    .woocommerce-privacy-policy-text {
        margin: 16px 0;
        font-size: 15px;

        & > :last-child {
            margin-bottom: 0;
        }
    }
    #place_order {
        @include th-button--size--extra-large;

        margin-top: 32px;
        width: 100%;
        display: block;
    }
}

.woocommerce-checkout-review-order-table {
    @include th-reset-table;

    line-height: 1.2;
    font-size: 15px;
    padding-top: 4px;

    &,
    thead,
    tbody,
    tfoot {
        display: block;
    }

    tr {
        display: flex;
        justify-content: space-between;

        & > :last-child {
            @include direction {
                #{$margin-inline-start}: 16px;
                text-align: $inline-end;
            }
        }
    }

    th,
    td {
        @include th-reset-table-cell;

        padding: 0;
    }

    thead {
        tr {
            padding-bottom: 4px;
            border-bottom: 1px solid #ebebeb;
        }
        th {
            font-size: 13px;
            text-transform: uppercase;
        }
    }

    tbody {
        strong {
            font-weight: $font-weight-normal;
        }
    }

    tbody,
    tfoot {
        tr + tr {
            padding-top: 8px;
        }
        tr:first-child {
            padding-top: 16px;
        }
        tr:last-child {
            padding-bottom: 16px;
        }
    }

    tfoot {
        tr:first-child {
            border-top: 1px solid #ebebeb;
        }
        th {
            font-weight: $font-weight-medium;
        }
        tr:nth-last-child(2) {
            padding-bottom: 16px;
        }
        tr:last-child {
            font-size: 24px;
            border-top: 1px solid #ebebeb;
            padding-top: 16px;
            padding-bottom: 0;

            strong {
                font-weight: $font-weight-normal;
            }
        }
    }

    .woocommerce-shipping-methods {
        list-style: none;
        padding: 0;
        margin: 0;

        li {
            @include th-reset-list-item;

            display: flex;
            align-items: center;
            justify-content: flex-end;
        }
        li + li {
            padding-top: 6px;
        }
        input {
            order: 1;

            @include direction {
                #{$margin-inline-start}: 6px;
            }
        }
        label {
            margin-bottom: 0;

            @include direction {
                #{$margin-inline-start}: 0;
            }
        }
    }
}

.woocommerce-checkout .woocommerce-form-login,
.woocommerce-form-coupon {
    --th-card-padding: 2rem;

    margin: 0 -.5rem;
    padding: 0 calc(.5rem + var(--th-card-padding));
    position: relative;
    z-index: 0;
    display: flex;
    flex-wrap: wrap;

    &:before {
        display: block;
        content: '';
        width: 100%;
        height: var(--th-card-padding);
    }
    &:after {
        display: block;
        content: '';
        width: 100%;
        height: calc(1.5rem + var(--th-card-padding));
    }

    & > :last-child:after {
        @include th-card;

        position: absolute;
        content: '';
        left: .5rem; // SKIP-RTL
        right: .5rem; // SKIP-RTL
        top: 0;
        bottom: 1.5rem;
        z-index: -1;
    }

    p:not([class]) {
        width: 100%;
        font-size: 15px;
        margin-top: -4px;
    }
}

.woocommerce-checkout .woocommerce-form-login {
    .clear,
    .lost_password {
        width: 100%;
    }
    .clear {
        margin: 0;
    }
    .lost_password {
        font-size: 15px;
        margin-top: 1.25rem;
        margin-bottom: -.24rem;

        a {
            color: #6c757d;
        }
        a:hover {
            text-decoration: underline;
        }
    }
    .form-row-first,
    .form-row-last {
        max-width: 280px;
    }

    .clear + .form-row {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }

    button[type="submit"] {
        order: -1;
        padding-left: 24px; // SKIP-RTL
        padding-right: 24px; // SKIP-RTL

        @include direction {
            #{$margin-inline-end}: 24px;
        }
    }

    button[type="submit"],
    .woocommerce-form-login__rememberme {
        margin-top: .5rem;
    }
}

.woocommerce-form-coupon {
    p:not([class]) {
        margin-bottom: 16px;
    }

    p {
        margin-bottom: 0;
    }

    .form-row-first {
        flex-grow: 1;
        flex-basis: 0;
        width: 0;
        max-width: 360px;

        input {
            width: 100%;
        }
    }
    .form-row-last {
        @include direction {
            #{$margin-inline-start}: 8px;
        }
    }
    .form-row {
        width: auto;
    }
}



@include media-breakpoint-down(md) {
    form.woocommerce-checkout {
        display: block;
    }
    .woocommerce-checkout-summary {
        margin-top: 1.5rem;
        width: auto;

        @include direction {
            #{$margin-inline-start}: 0;
        }
    }
}
@include media-breakpoint-down(xs) {
    #customer_details,
    .woocommerce-checkout-summary {
        padding: 1.5rem;
    }
    .woocommerce-checkout .woocommerce-terms-and-conditions {
        margin-left: -1.5rem; // SKIP-RTL
        margin-right: -1.5rem; // SKIP-RTL
        padding: 0 1.5rem;
    }
    .woocommerce-checkout .woocommerce-form-login,
    .woocommerce-form-coupon {
        --th-card-padding: 1.5rem;
    }
}
@media (max-width: 459px) {
    .woocommerce-checkout .woocommerce-form-login,
    .woocommerce-billing-fields__field-wrapper {
        .form-row-first,
        .form-row-last {
            width: 100%;
            max-width: none;
        }
        .form-row-last {
            @include direction {
                #{$margin-inline-start}: 0;
            }
        }
        .form-row-first:first-child + .form-row-last {
            margin-top: 1rem;
        }
    }
}
