/*
// .th-button
*/

.th-button {
    @include th-button-base;
}

.th-button--style--primary {
    @include th-button--style--primary;
}
.th-button--style--secondary {
    @include th-button--style--secondary;
}
.th-button--style--muted {
    @include th-button--style--muted;
}

.th-button--size--extra-small {
    @include th-button--size--extra-small;
}
.th-button--size--small {
    @include th-button--size--small;
}
.th-button--size--normal {
    @include th-button--size--normal;
}

.th-button--icon {
    padding: 0;

    svg {
        display: block;
        margin: auto;
    }

    &.th-button--size--extra-small {
        $local-size: (15px + 4.5px * 2);

        width: $local-size;
        height: $local-size;
    }
    &.th-button--size--small {
        $local-size: (17px + 6.5px * 2);

        width: $local-size;
        height: $local-size;
    }
    &.th-button--size--normal {
        $local-size: (19px + 8.5px * 2);

        width: $local-size;
        height: $local-size;
    }
}

.th-button--loading {
    &,
    &:hover,
    &:focus,
    &:active,
    &:disabled {
        color: transparent;
        cursor: default;
    }

    &.th-button--style--primary {
        &,
        &:hover,
        &:focus,
        &:active,
        &:disabled {
            @include th-var(background, --th-btn-primary-normal-bg-color, #e52727);
        }
    }
    &.th-button--style--secondary {
        &,
        &:hover,
        &:focus,
        &:active,
        &:disabled {
            @include th-var(background, --th-btn-secondary-normal-bg-color, #f0f0f0);
        }
    }

    &:after {
        @include th-loader();
        @include th-loader-thickness(2px);
        @include th-loader-color(#fff);
    }
    &.th-button--size--extra-small:after {
        @include th-loader-size(16px);
    }
    &.th-button--size--small:after {
        @include th-loader-size(20px);
    }
    &.th-button--size--normal:after {
        @include th-loader-size(24px);
    }
}

input[type="submit"],
.button,
.added_to_cart,
.woocommerce-Address-title .edit {
    @include th-button-base;
    @include th-button--size--normal;
    @include th-button--style--primary;
}
