/*
// .th-button-toggle
*/

$local-border-radius: 2px;

.th-button-toggle {}
.th-button-toggle__list {
    display: flex;
}
.th-button-toggle__item {
    margin: 0;
}
.th-button-toggle__button {
    margin: 0;
    font-weight: $font-weight-medium;
    font-size: 13px;
    line-height: 14px;
    padding: 5px 9px;
    cursor: pointer;
    user-select: none;
    transition:
        background .12s,
        color .12s;

    @include th-var(background, --th-btn-secondary-normal-bg-color, map_get($btn-secondary-scheme, normal-bg-color));
    @include th-var(color, --th-btn-secondary-normal-font-color, map_get($btn-secondary-scheme, normal-font-color));

    &:hover {
        @include th-var(background, --th-btn-secondary-hover-bg-color, map_get($btn-secondary-scheme, hover-bg-color));
        @include th-var(color, --th-btn-secondary-hover-font-color, map_get($btn-secondary-scheme, hover-font-color));
    }
    &:active {
        @include th-var(background, --th-btn-secondary-active-bg-color, map_get($btn-secondary-scheme, active-bg-color));
        @include th-var(color, --th-btn-secondary-active-font-color, map_get($btn-secondary-scheme, active-font-color));
    }
}
.th-button-toggle__item:first-child .th-button-toggle__button {
    @include direction {
        #{$border-start-start-radius}: $local-border-radius;
        #{$border-end-start-radius}: $local-border-radius;
    }
}
.th-button-toggle__item:last-child .th-button-toggle__button {
    @include direction {
        #{$border-start-end-radius}: $local-border-radius;
        #{$border-end-end-radius}: $local-border-radius;
    }
}
.th-button-toggle__input {
    position: absolute;
    clip: rect(0,0,0,0);
    pointer-events: none;
}
.th-button-toggle__input:checked ~ .th-button-toggle__button {
    cursor: default;

    @include th-var(background, --th-btn-primary-normal-bg-color, map_get($btn-primary-scheme, normal-bg-color));
    @include th-var(color, --th-btn-primary-normal-font-color, map_get($btn-primary-scheme, normal-font-color));
}
