/*
// .woocommerce-order-updates
*/

.woocommerce-order-updates {
    @include th-card;

    h2 {
        padding: .875rem 1.25rem .5rem;
        font-size: 22px;
        margin-bottom: 0;
    }

    ol {
        @include th-reset-list;

        list-style: none;
        margin: 0;
        padding: .375rem 1.25rem 1rem;
        font-size: 15px;
        line-height: 1.2;
    }

    li {
        @include th-reset-list-item;
    }

    li + li {
        border-top: 1px solid map-get($card-scheme, divider);
        padding-top: .875rem;
        margin-top: .875rem;
    }

    p {
        margin: 0;
    }

    .meta {
        font-size: 14px;
        color: #6c757d;
    }
}
