/*
// .th-no-results
*/

.th-no-results {}
.th-no-results__body {
    @include th-card;

    padding: 3rem;
    text-align: center;
}
.th-no-results__message + .th-no-results__actions {
    margin-top: 1.25rem;
}
.th-no-results {
    .th-search__body {
        display: flex;
        max-width: 400px;
        margin: 0 auto;
        padding-bottom: .25rem;
    }
    .th-search__input {
        @include direction {
            #{$margin-inline-end}: .5rem;
        }
    }
    .th-search__button--search {
        @include th-button-base;
        @include th-button--size--small;
        @include th-button--style--primary;
    }
    .th-search__button-icon {
        display: none;
    }
}

@include media-breakpoint-down(sm) {
    .th-no-results__body {
        padding: 1.5rem;
    }
    .th-no-results__message + .th-no-results__actions {
        margin-top: 1rem;
    }
}
