/*
// .th-footer-links
*/

.th-footer-links {}
.th-footer-links__title {
    font-size: 20px;
    color: map_get($footer-scheme, opposite);
    margin-bottom: 22px;
    line-height: 1.2;

    @include scheme-font-weight($footer-scheme, opposite, $font-weight-medium);
}
.th-footer-links__list {
    font-size: 15px;
    line-height: 20px;

    ul {
        list-style: none;
        padding: 0;
        margin: 0;
    }
    li {
        @include th-reset-list-item;

        position: relative;
        padding: 5px 0;

        @include direction {
            #{$padding-inline-start}: 15px;
        }

        &:last-child {
            padding-bottom: 0;
        }
        &:before {
            position: absolute;
            display: block;
            content: '';
            width: 4px;
            height: 4px;
            background: rgba(#fff, .2);
            top: 13px;

            @include direction {
                #{$inline-start}: 2px;
            }
        }
    }

    a {
        color: inherit;
        transition: .15s;
    }
    a:hover {
        color: map_get($footer-scheme, opposite);
    }
}

@include media-breakpoint-down(sm) {
    .th-footer-links {
        text-align: center;
    }
    .th-footer-links__list {
        li {
            @include direction {
                #{$padding-inline-start}: 0;
            }
            &:before {
                display: none;
            }
        }
    }
    .th-footer-links__title {
        margin-bottom: 12px;
    }
}
