/*
// .forms
*/

.woocommerce-address-fields,
.edit-account {
    padding: 2rem;
    display: flex;
    flex-wrap: wrap;

    label {
        margin-bottom: .25rem;
        font-size: 15px;
    }

    & > * {
        margin-top: 1rem;
    }

    input + span {
        display: block;
        font-size: 14px;
        color: #6c757d;
        margin-top: .25rem;

        em {
            font-style: normal;
        }
    }

    input,
    fieldset {
        width: 100%;
    }

    legend {
        font-size: 22px;
        font-weight: $font-weight-medium;
        margin-bottom: -.25rem;
    }
    fieldset {
        margin-top: 1rem;

        & > * {
            margin-top: 1rem;
        }
    }

    [type="submit"] {
        margin-top: 1rem;
    }
}
.woocommerce-address-fields__field-wrapper {
    display: flex;
    flex-wrap: wrap;

    & > * {
        margin-top: 1rem;
    }
}
.woocommerce-input-wrapper {
    display: block;
    width: 100%;
}
.form-row {
    margin: 0 0 1rem;
}
.form-row-first,
.form-row-last {
    width: calc(100% / 2 - 1rem / 2);
}
.form-row-first:first-child {
    &,
    & + .form-row-last {
        margin-top: 0;
    }
}
.form-row-last {
    @include direction {
        #{$margin-inline-start}: 1rem;
    }
}
.form-row-wide {
    width: 100%;
}


.woocommerce-MyAccount-content {
    & > form {
        @include th-card;

        h3 {
            padding: 1.75rem 2rem 0;
            font-size: 22px;
            margin-bottom: -2rem;
        }
    }
}


.woocommerce-form-login,
.woocommerce-form-register,
.woocommerce-ResetPassword,
.woocommerce-form-track-order,
.woocommerce-billing-fields__field-wrapper,
.woocommerce-shipping-fields__field-wrapper,
.woocommerce-additional-fields__field-wrapper,
.woocommerce-account-fields > div {
    display: flex;
    flex-wrap: wrap;

    label {
        margin-bottom: .25rem;
        font-size: 15px;
    }

    & > * {
        margin-top: 1rem;
    }

    & > :first-child {
        margin-top: 0;
    }

    p {
        margin-bottom: 0;
    }

    input:not([type="checkbox"]):not([type="radio"]),
    textarea,
    .form-row-wide {
        width: 100%;
    }

    .form-row-first:first-child {
        &,
        & + .form-row-last {
            margin-top: 0;
        }
    }
}


.woocommerce-form-track-order {
    @include th-card;

    margin: 0 auto;
    padding: 2rem;

    p:not([class]) {
        font-size: 15px;
    }

    button[type="submit"] {
        margin-top: 1rem;
    }

    @include media-breakpoint-up(xl) {
        max-width: 410px;

        .form-row-first,
        .form-row-last {
            width: 100%;
        }
        .form-row-last {
            @include direction {
                #{$margin-inline-start}: 0;
            }
        }
    }
    @include media-breakpoint-down(sm) {
        .form-row-first,
        .form-row-last {
            width: 100%;
        }
        .form-row-last {
            @include direction {
                #{$margin-inline-start}: 0;
            }
        }
    }
    @media (max-width: 419px) {
        padding: 1.5rem;
    }
}

.th-order-tracking {
    max-width: 930px;
    margin: 0 auto;

    .order-info {
        @include th-card;

        padding: 1rem 1.25rem;
    }

    & > p:not(:last-child),
    & > section:not(:last-child) {
        margin-bottom: 1.5rem;
    }
}

.woocommerce-notices-wrapper {
    width: 100%;
}

.required {
    color: #cb1818;
    text-decoration: none;
}

.woocommerce-password-strength {
    margin-top: .5rem;
    border-radius: 2px;
    padding: 4px 12px;
    font-size: 15px;
    font-weight: 500;

    &,

    &.strong {
        background-color: #c1e1b9;
    }

    &.short {
        background-color: #f1adad;
    }

    &.bad {
        background-color: #fbc5a9;
    }

    &.good {
        background-color: #ffe399;
    }
}
.woocommerce-password-hint {
    margin: .625rem 0 0;
    color: #6c757d;
    font-size: 14px;
    line-height: 1.375;
    display: block;
}
