/*
// .th-layout-switcher
*/

.th-layout-switcher {}
.th-layout-switcher__list {
    display: flex;
}
.th-layout-switcher__button {
    padding: 7px;
    margin: 0;
    border: none;
    display: block;
    border-radius: 2px;

    @include define-button-state($layout-switcher-scheme, normal);

    &:focus {
        outline: none;
    }

    svg {
        display: block;
        fill: currentColor;
    }
}
.th-layout-switcher__button:hover {
    @include define-button-state($layout-switcher-scheme, hover);
}
.th-layout-switcher__button--active {
    background-color: transparent;
    cursor: default;

    @include th-var(color, --th-theme-scheme-main, map_get($layout-switcher-scheme, active-bg-color));

    &:hover {
        background-color: transparent;
    }
}
