// stylelint-disable declaration-no-important

// Margin and Padding

@each $breakpoint in map-keys($grid-breakpoints) {
    @include media-breakpoint-up($breakpoint) {
        $infix: breakpoint-infix($breakpoint, $grid-breakpoints);

        @each $prop, $abbrev in (margin: m, padding: p) {
            @each $size, $length in $spacers {
                .th-#{$abbrev}#{$infix}-#{$size} { #{$prop}: $length !important; }
                .th-#{$abbrev}t#{$infix}-#{$size},
                .th-#{$abbrev}y#{$infix}-#{$size} {
                    #{$prop}-top: $length !important;
                }
                .th-#{$abbrev}e#{$infix}-#{$size},
                .th-#{$abbrev}x#{$infix}-#{$size} {
                    @include direction {
                        #{$prop}-#{$inline-end}: $length !important;
                    }
                }
                .th-#{$abbrev}b#{$infix}-#{$size},
                .th-#{$abbrev}y#{$infix}-#{$size} {
                    #{$prop}-bottom: $length !important;
                }
                .th-#{$abbrev}s#{$infix}-#{$size},
                .th-#{$abbrev}x#{$infix}-#{$size} {
                    @include direction {
                        #{$prop}-#{$inline-start}: $length !important;
                    }
                }
            }
        }

        // Negative margins (e.g., where `.mb-n1` is negative version of `.mb-1`)
        @each $size, $length in $spacers {
            @if $size != 0 {
                .th-m#{$infix}-n#{$size} { margin: -$length !important; }
                .th-mt#{$infix}-n#{$size},
                .th-my#{$infix}-n#{$size} {
                    margin-top: -$length !important;
                }
                .th-me#{$infix}-n#{$size},
                .th-mx#{$infix}-n#{$size} {
                    @include direction {
                        #{$margin-inline-end}: -$length !important;
                    }
                }
                .th-mb#{$infix}-n#{$size},
                .th-my#{$infix}-n#{$size} {
                    margin-bottom: -$length !important;
                }
                .th-ms#{$infix}-n#{$size},
                .th-mx#{$infix}-n#{$size} {
                    @include direction {
                        #{$margin-inline-start}: -$length !important;
                    }
                }
            }
        }

        // Some special margin utils
        .th-m#{$infix}-auto { margin: auto !important; }
        .th-mt#{$infix}-auto,
        .th-my#{$infix}-auto {
            margin-top: auto !important;
        }
        .th-me#{$infix}-auto,
        .th-mx#{$infix}-auto {
            @include direction {
                #{$margin-inline-end}: auto !important;
            }
        }
        .th-mb#{$infix}-auto,
        .th-my#{$infix}-auto {
            margin-bottom: auto !important;
        }
        .th-ms#{$infix}-auto,
        .th-mx#{$infix}-auto {
            @include direction {
                #{$margin-inline-start}: auto !important;
            }
        }
    }
}
