/*
// .woocommerce-order-details
*/

.woocommerce-order-details {
    @include th-card;

    .woocommerce-order-details__title {
        padding: .875rem 1.25rem .5rem;
        font-size: 22px;
        margin-bottom: 0;
    }

    table {
        @include th-reset-table;

        margin: 0;
        width: 100%;
        font-size: 15px;
    }

    .button {
        @include th-button--size--extra-small;
    }

    th,
    td {
        @include th-reset-table-cell;

        padding: .875rem .75rem;

        &:first-child {
            @include direction {
                #{$padding-inline-start}: 1.25rem;
            }
        }
        &:last-child {
            @include direction {
                #{$padding-inline-end}: 1.25rem;
            }
        }
    }
    th {
        text-transform: uppercase;
        font-size: 13px;
    }
    thead th {
        padding-top: .75rem;
        padding-bottom: .75rem;
    }

    thead + tbody tr:first-child,
    tbody + tfoot tr:first-child {
        th,
        td {
            border-top: 1px solid map-get($card-scheme, divider);
        }
    }

    tbody,
    tfoot {
        th,
        td {
            padding-top: .25rem;
            padding-bottom: .25rem;
        }

        tr:first-child {
            th,
            td {
                padding-top: .875rem;
            }
        }
        tr:last-child {
            th,
            td {
                padding-bottom: .875rem;
            }
        }
    }

    .product-quantity {
        font-weight: $font-weight-normal;
    }
    .order-again {
        border-top: 1px solid map-get($card-scheme, divider);
        margin: 0;
        padding: 1.25rem;
    }

    ul {
        @include th-reset-list;

        list-style: none;
        padding: 0;
        font-size: 13px;
        color: #6c757d;

        li {
            @include th-reset-list-item;

            margin: -2px 0 0;
        }

        strong {
            font-weight: $font-weight-normal;
        }

        p {
            display: inline;
            margin: 0;
        }
    }
}

@include media-breakpoint-down(sm) {
    .woocommerce-order-details {
        .woocommerce-order-details__title {
            padding-bottom: 0;
        }
    }
}
