/*
// .th-post-header
*/

.th-post-header {
    position: relative;
    display: flex;
    align-items: center;
    z-index: 0;
    overflow: hidden;
}
.th-post-header__decor {
    position: absolute;
    bottom: -1px;

    @include th-decor-style(map_get($body-scheme, main));
}
.th-post-header__image {
    img {
        position: absolute;
        left: 0; // SKIP-RTL
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        opacity: .22;
        object-fit: cover;
    }
}
.th-post-header__body {
    max-width: 620px;
    margin: 0 auto;
    text-align: center;
    padding: 52px 30px;
    backface-visibility: hidden;
}
.th-post-header__title {
    margin: 0;
    font-size: 36px;
    word-wrap: break-word;
    font-weight: $font-weight-bold;
}

// categories
$local-categories-gutter: 6px;

.th-post-header__categories {
    margin-bottom: 28px;
}
.th-post-header__categories-list {
    list-style: none;
    margin: -($local-categories-gutter / 2);
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    a {
        margin: ($local-categories-gutter / 2);
        display: block;
        font-size: 10px;
        letter-spacing: .02em;
        text-transform: uppercase;
        padding: 3px 9px;
        border-radius: 1.5px;

        @include th-var(background, --th-theme-scheme-main, map_get($post-header-category-link-scheme, main));

        @include scheme-font-weight($post-header-category-link-scheme, opposite, $font-weight-medium);

        &,
        &:hover {
            @include th-var(color, --th-theme-scheme-opposite, map_get($post-header-category-link-scheme, opposite));
        }
    }
}

// meta
.th-post-header__meta {
    font-size: 14px;
    margin-top: 24px;
    color: map_get($body-scheme, muted);

    a {
        color: inherit;
        transition: color .1s;
    }
    a:hover {
        @include th-var(color, --th-link-color, $link-color);
    }
}
.th-post-header__meta-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.th-post-header__meta-item {
    @include th-reset-list-item;

    & + & {
        $local-margin: 24px;
        $local-size: 4px;

        position: relative;

        @include direction {
            #{$margin-inline-start}: $local-margin;
        }

        &:before {
            display: block;
            position: absolute;
            content: '';
            width: $local-size;
            height: $local-size;
            border-radius: $local-size / 2;
            background: currentColor;
            top: 9px;

            @include direction {
                #{$inset-inline-start}: -($local-margin / 2 + $local-size / 2);
            }
        }
    }
}


.th-post-header--has-image {
    background: $post-header-overlay-bg-color;

    @mixin local-height($header-height, $image-height) {
        margin-bottom: -($image-height - $header-height);
        padding-bottom: $image-height - $header-height;
    }

    @include local-height(350px, 500px);

    .th-post-header__body {
        color: $post-header-overlay-font-color;
        padding: 60px 30px;
    }
    .th-post-header__title,
    .th-post-header__meta {
        text-shadow: $post-header-overlay-font-shadow;
    }
    .th-post-header__meta {
        color: inherit;

        a:hover {
            @include th-var(color, --th-accent-scheme-main, $post-header-overlay-link-hover-color);
        }
    }
}


@include media-breakpoint-down(lg) {
    .th-post-header {
        padding-top: 0;
    }
}
@include media-breakpoint-down(xs) {
    .th-post-header__body {
        padding: 36px 30px;
    }
    .th-post-header__categories {
        margin-bottom: 20px;
    }
    .th-post-header__title {
        font-size: 28px;
    }
    .th-post-header__meta {
        margin-top: 16px;
    }

    .th-post-header--has-image {
        .th-post-header__body {
            padding: 48px 30px;
        }
    }
}
@media (max-width: 419px) {
    .th-post-header--has-image {
        padding-bottom: 0;
        margin-bottom: 0;
    }
}
