/*
// .th-quickview
*/

$local-transition-duration: .3s;
$local-transition-timing-function: ease-out;

.th-quickview {
    display: none;
}
.th-quickview--display {
    display: block;
}
.th-quickview--open {
    .th-quickview__backdrop {
        opacity: 1;
    }
    .th-quickview__window {
        opacity: 1;
        transform: none;
    }
}
.th-quickview__backdrop {
    z-index: 1040;
    position: fixed;
    background: rgba(#333, .8);
    top: 0;
    left: 0; // SKIP-RTL
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity $local-transition-duration $local-transition-timing-function;
}
.th-quickview__container {
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 1050;
    position: fixed;
    top: 0;
    left: 0; // SKIP-RTL
    width: 100%;
    height: 100%;
}
.th-quickview__wrapper {
    --th-quickview-margin-t: calc(1.75rem + var(--th-admin-bar-height));
    --th-quickview-margin-b: 1.75rem;

    max-width: 800px;
    padding: 0 10px;
    min-height: calc(100% - var(--th-quickview-margin-t) - var(--th-quickview-margin-b));
    margin: var(--th-quickview-margin-t) auto var(--th-quickview-margin-b);
    display: flex;
    align-items: center;
}
.th-quickview__window {
    max-width: 100%;
    position: relative;
    background: #fff;
    border-radius: 2px;
    box-shadow: 0 1px 10px rgba(#000, .1);
    flex-grow: 1;
    opacity: 0;
    transform: translateY(-100px);
    transition:
        opacity $local-transition-duration $local-transition-timing-function,
        transform $local-transition-duration $local-transition-timing-function;
}
.th-quickview__close {
    @include th-modal-close-button;
}
.th-quickview__gallery {
    width: 320px;
    flex-shrink: 0;

    @include direction {
        #{$margin-inline-end}: 28px;
    }
}
.th-quickview__product {
    flex-grow: 1;
}
.th-quickview__see-details {
    $local-height: 52px;

    border-radius: 0 0 2.5px 2.5px;
    border-top: 1px solid map_get($card-scheme, divider);
    display: block;
    text-align: center;
    color: map_get($card-scheme, opposite-alt);
    font-size: 15px;
    height: $local-height;
    line-height: ($local-height - 2px);
    transition:
        background .15s,
        border-color .15s;

    &:hover {
        color: map_get($card-scheme, opposite-alt);
        background: $quickview-see-details-hover-bg-color;
    }
    &:active {
        background: $quickview-see-details-active-bg-color;
        transition-duration: 0s;
    }
}


@media (max-width: 840px) {
    .th-quickview__wrapper {
        max-width: 520px;
    }
    .th-quickview__gallery {
        width: 100%;
        margin-bottom: 28px;

        @include direction {
            #{$margin-inline-end}: 0;
        }
    }
}
@include media-breakpoint-down(xs) {
    .th-quickview__wrapper {
        max-width: 100%;
    }
}
