/*
// .star-rating
*/

.star-rating {
    width: 77px;
    height: 14px;
    overflow: hidden;
    box-sizing: content-box;
    position: relative;

    &::before {
        display: block;
        content: '';
        position: absolute;
        width: inherit;
        height: inherit;

        @include start-13x12($rating-star-default-bg-color, $rating-star-default-border-color);
    }

    span {
        display: block;
        position: relative;
        color: transparent;
        background-color: #fff;
        text-indent: 1000px;
        white-space: nowrap;
        height: inherit;

        @include start-13x12($rating-star-active-bg-color, $rating-star-active-border-color);
    }
}
