@mixin th-status-badge-hide-text() {
    .th-status-badge__text {
        display: none;
    }
    .th-status-badge__tooltip {
        display: block;
    }
}
@mixin th-status-badge-show-text() {
    .th-status-badge__text {
        display: block;
    }
    .th-status-badge__tooltip {
        display: none;
    }
}
