@mixin th-overlay-image($overlay-background) {
    position: absolute;
    left: 0; // SKIP-RTL
    right: 0; // SKIP-RTL
    top: 0;
    bottom: 0;
    background-size: cover;
    background-position: center center;
    z-index: -1;

    &:before {
        display: block;
        content: '';
        width: 100%;
        height: 100%;
        background: $overlay-background;
    }
}

@mixin th-overlay-image-link() {
    &:before {
        position: absolute;
        display: block;
        content: '';
        left: 0; // SKIP-RTL
        right: 0; // SKIP-RTL
        top: 0;
        bottom: 0;
        background: $image-overlay-color;
        border-radius: 1.5px;
        opacity: 0;
        transition: opacity .2s;
        pointer-events: none;
    }
    &:hover:before {
        opacity: 1;
    }
}
