/*
// .th-product-gallery
*/

.th-product-gallery {}
.th-product-gallery__body {
    position: relative;
}
.th-product-gallery__trigger {
    $local-padding: 9px;
    $local-icon-size: 24px;

    top: -4px;
    position: absolute;
    padding: $local-padding;
    margin: 0;
    border: none;
    border-radius: 2px;
    fill: currentColor;
    z-index: 2;
    transition:
        background .15s,
        color .15s;

    @include define-button-scheme($btn-muted-light-scheme);
    @include direction {
        #{$inset-inline-end}: -4px;
    }

    &:focus {
        outline: none;
    }

    svg {
        display: block;
    }
}

.woocommerce-product-gallery {
    --th-gallery-thumbnail-gutter: 8px;
    --th-gallery-thumbnail-columns: 6;

    .flex-control-thumbs {
        margin: calc(var(--th-gallery-thumbnail-gutter) / 2 * -1);
        padding: 0;
        list-style: none;
        display: flex;
        flex-wrap: wrap;

        &:before {
            content: '';
            width: 100%;
            display: block;
            height: var(--th-gallery-thumbnail-gutter);
        }

        li {
            margin: calc(var(--th-gallery-thumbnail-gutter) / 2);
            flex-basis: calc(100% / var(--th-gallery-thumbnail-columns) - var(--th-gallery-thumbnail-gutter));
        }

        img {
            padding: 5px;
            border-radius: 1.5px;
            box-shadow: 0 0 0 2px $product-gallery-thumbnail-normal-border-color inset;
            transition: box-shadow .12s;
            cursor: pointer;
            width: 100%;
            height: auto;

            &:hover {
                box-shadow: 0 0 0 2px $product-gallery-thumbnail-hover-border-color inset;
            }
        }

        //noinspection CssOverwrittenProperties
        .flex-active,
        .flex-active:hover {
            box-shadow: 0 0 0 2px $product-gallery-thumbnail-current-border-color inset;
            box-shadow: 0 0 0 2px var(--th-theme-scheme-main) inset;
        }
    }
}
.woocommerce-product-gallery__trigger {
    display: none;
}
.woocommerce-product-gallery__wrapper {
    margin: 0;

    .flex-viewport & {
        max-width: 100%;
        display: flex;
    }
}
.woocommerce-product-gallery__image {
    border-radius: 2px;
    flex-shrink: 0;
    width: 100%;

    & + & {
        display: none;
    }
}
.woocommerce-product-gallery__image--placeholder img,
.woocommerce-product-gallery__wrapper a > img {
    max-width: 100%;
    height: auto;
}
