/*
// .th-categories-list
*/

@mixin local-layout($columns) {
    .th-categories-list__item {
        width: calc((100% - #{($columns - 1) * 1px}) / #{$columns});
    }
    .th-categories-list__divider {
        &:nth-child(#{$columns * 2}n) {
            width: 100%;
            height: 1px;
        }

        &:last-child:nth-child(#{$columns * 2}n) {
            display: none;
        }
    }
}

.th-categories-list {}
.th-categories-list__body {
    @include th-card;

    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
}
.th-categories-list__item {
    @include th-reset-list-item;

    padding: 1.125rem;
    text-align: center;
    position: relative;

    a {
        display: block;
        color: inherit;
    }

    img {
        width: 100%;
        max-width: 200px;
        height: auto;
        margin: 0 auto;
    }

    &:before {
        position: absolute;
        display: block;
        content: '';
        left: 0; // SKIP-RTL
        right: 0; // SKIP-RTL
        top: 0;
        bottom: 0;
        background: $categories-list-hover-overlay-bg-color;
        pointer-events: none;
        opacity: 0;
        transition: opacity .12s;
        z-index: 1;
    }

    &:hover:before {
        opacity: 1;
    }
}
.th-categories-list__item-title {
    font-size: 15px;
    line-height: 1.5;
    font-weight: $font-weight-medium;
    margin: 8px 0 0;
}
.th-categories-list__item-count {
    margin-top: 4px;
    font-size: 14px;
    line-height: 1;
    color: map_get($card-scheme, opposite-alt);
}
.th-categories-list__divider {
    @include th-reset-list-item;

    width: 1px;
    background-color: map_get($card-scheme, divider);
}


.th-categories-list--layout--columns-3-sidebar {
    @include media-breakpoint-up(sm) {
        @include local-layout(3);
    }
    @include media-breakpoint-down(xs) {
        @include local-layout(2);
    }
}


.th-categories-list--layout--columns-4-sidebar {
    @include media-breakpoint-up(xl) {
        @include local-layout(4);
    }
    @include media-breakpoint-between(sm, lg) {
        @include local-layout(3);
    }
    @include media-breakpoint-down(xs) {
        @include local-layout(2);
    }
}


