/*
// .th-compare-table
*/

.th-compare-table {
    @include th-reset-table;

    width: 100%;
    font-size: 15px;
    border-spacing: 0;

    tr:not(:last-child) {
        border-bottom: 1px solid map_get($card-scheme, divider);
    }

    .button,
    .added_to_cart {
        @include th-button--size--small;

        svg {
            display: none;
        }
    }
    .button.added {
        display: none;
    }
    .button.loading {
        position: relative;
        color: transparent;
        cursor: default;

        @include th-var(background, --th-btn-primary-normal-bg-color, map_get($addtocart-button-scheme, normal-bg-color));

        &:after {
            @include th-loader(18px, 2px, map_get($addtocart-button-scheme, normal-font-color));
        }
    }
}
.th-compare-table--only-different {
    .th-compare-table__row--identical {
        display: none;
    }
}
.th-compare-table__column {
    @include th-reset-table-cell;

    vertical-align: center;

    .th-compare-table__row:first-child & {
        vertical-align: top;
    }
}
.th-compare-table__column--header,
.th-compare-table__column--product {
    padding: 14px 16px;
}
.th-compare-table__column--product,
.th-compare-table__column--fake {
    @include direction {
        #{$border-inline-start}: 1px solid map_get($card-scheme, divider);
    }
}
.th-compare-table__column--header {
    width: 15%;
    background: $table-header-bg-color;
    font-weight: $font-weight-medium;
    min-width: 140px;

    @include direction {
        text-align: $inline-end;
    }
}
.th-compare-table__column--product {
    width: 17%;
    text-align: center;
    min-width: 180px;
}
.th-compare-table__column--fake {
    padding: 0;
}

.th-compare-table__column--product:nth-child(6) + .th-compare-table__column--fake {
    display: none;
}

.th-compare-table__product {
    display: block;
    line-height: 20px;
    color: inherit;
    transition: color .12s;

    &:hover {
        @include th-var(color, --th-link-color, $link-color);
    }
}
.th-compare-table__product-image {
    width: 150px;
    margin: 0 auto;

    img {
        max-width: 100%;
        height: auto;
    }
}
.th-compare-table__product-name {
    color: inherit;
}

.th-compare-table__rating-stars {
    display: flex;
    justify-content: center;
    padding-top: 2px;
    margin-bottom: 2px;
}
.th-compare-table__rating-title {
    font-size: 13px;
    color: map_get($card-scheme, opposite-alt);
}
.th-compare-table__dash {
    display: block;
    height: 1px;
    width: 16px;
    margin: 0 auto;
    background: #999;
    opacity: .5;
}
