/*
// .widget_recent_comments
// .widget_recent_entries
*/

.widget_recent_comments,
.widget_recent_entries {
    $local-gutter: 14px;

    ul {
        color: #6c757d;
        list-style: none;
        margin: 0;
        padding: 0;
        font-size: 15px;
        line-height: (20 / 15);
    }
    li + li {
        margin-top: $local-gutter;
        padding-top: $local-gutter;
        border-top: 1px solid #ebebeb;
    }

    li a {
        & {
            color: #262626;
            transition: color .2s;
        }
        &:hover {
            text-decoration: underline;
        }
    }

    .comment-author-link {
        font-style: italic;
    }

    .post-date {
        display: block;
        color: #999;
        font-size: 13px;
        margin-top: 3px;

        @include th-decor-line(16px);
    }

    .th-site-footer & {
        ul {
            color: #9e9e9e;
        }
        li + li {
            border-top: 1px solid rgba(#fff, .08);
        }
        li a {
            color: #fff;
        }
        .post-date {
            color: #9e9e9e;
        }
    }
}
