/*
// .th-decor
*/

.th-decor {
    display: block;
}
.th-decor--type--center {
    .th-decor__body {
        overflow: hidden;
        position: relative;
    }
    .th-decor__start,
    .th-decor__end {
        position: absolute;
    }
    .th-decor__start {
        transform-origin: right top; // SKIP-RTL
    }
    .th-decor__end {
        transform-origin: left top; // SKIP-RTL
    }
    .th-decor__center {
        position: absolute;
        left: 50%; // SKIP-RTL
        transform: translateX(-50%); // SKIP-RTL
    }

    @include th-decor-center-size(21px, 10px, 30deg)
}
.th-decor--type--top,
.th-decor--type--bottom {
    width: 100%;
    position: relative;
    pointer-events: none;
    overflow: hidden;

    .th-decor__start,
    .th-decor__end {
        position: absolute;
        width: calc((100% - 1350px - 160px) / 2);
    }
    .th-decor__start {
        left: 0; // SKIP-RTL
        transform-origin: right top; // SKIP-RTL
    }
    .th-decor__end {
        right: 0; // SKIP-RTL
        transform-origin: left top; // SKIP-RTL
    }

    @include th-decor-bottom-height(21px, 10px);
    @include th-decor-bottom-radius(2px);
}
.th-decor--type--top {
    .th-decor__start,
    .th-decor__end {
        top: 0;
    }
    .th-decor__start {
        transform-origin: right top; // SKIP-RTL
    }
    .th-decor__end {
        transform-origin: left top; // SKIP-RTL
    }

    @include th-decor-bottom-skew(-30deg);
}
.th-decor--type--bottom {
    .th-decor__start,
    .th-decor__end {
        bottom: 0;
    }
    .th-decor__start {
        transform-origin: right bottom; // SKIP-RTL
    }
    .th-decor__end {
        transform-origin: left bottom; // SKIP-RTL
    }

    @include th-decor-bottom-skew(30deg);
}
