/*
// .th-category-card
*/

.th-category-card {}
.th-category-card__body {
    position: relative;
}
.th-category-card__overlay-image {
    display: none;
}

@mixin local-th-category-card--horizontal {
    .th-category-card__image {
        margin-bottom: 0;

        @include direction {
            #{$margin-inline-end}: var(--th-category-card--gutter);
        }
    }
}
@mixin local-th-category-card--vertical {
    .th-category-card__image {
        margin-bottom: var(--th-category-card--gutter);

        @include direction {
            #{$margin-inline-end}: 0;
        }
    }
}

.th-category-card--layout--classic {
    --th-category-card--padding: 24px;
    --th-category-card--gutter: var(--th-category-card--padding);

    .th-category-card__body {
        @include th-card;

        box-shadow: $category-card-shadow;
        flex-grow: 1;
        width: 100%;
    }
    .th-category-card__content {
        display: flex;
        padding: var(--th-category-card--padding);
    }
    .th-category-card__image {
        width: 160px;
        margin: 0;
        flex-shrink: 0;
    }
    .th-category-card__info {
        flex-grow: 1;
    }
    .th-category-card__name,
    .th-category-card__children {
        a {
            transition: color .12s;
        }
        a:hover {
            @include th-var(color, --th-link-color, $link-color);
        }
    }
    .th-category-card__name {
        font-weight: $font-weight-medium;
        font-size: 18px;
        line-height: 24px;

        a {
            color: inherit;
        }
    }
    .th-category-card__children {
        list-style: none;
        margin: 10px 0 0;
        padding: 0;
        font-size: 15px;

        li {
            @include th-reset-list-item;

            position: relative;
            color: map_get($card-scheme, opposite-alt);
            padding-top: 1px;
            padding-bottom: 1px;

            @include direction {
                #{$padding-inline-start}: 11px;
                #{$padding-inline-end}: 0;
            }

            &:before {
                display: block;
                position: absolute;
                content: '';
                width: 3px;
                height: 3px;
                background: currentColor;
                opacity: .7;
                top: 10px;

                @include direction {
                    #{$inset-inline-start}: 0;
                }
            }
        }

        a {
            color: inherit;
        }
    }
    .th-category-card__actions {
        margin-top: 8px;
        margin-bottom: -2px;
        line-height: 1.25;
    }
    .th-category-card__link {
        font-size: 14px;
        font-weight: $font-weight-medium;
        @include th-var(color, --th-link-color, $link-color);

        &:hover {
            text-decoration: underline;
        }
    }




    @include media-breakpoint-up(lg) {
        @include local-th-category-card--horizontal;
    }
    @include media-breakpoint-down(md) {
        @include local-th-category-card--vertical;
    }

    @include media-breakpoint-only(xl) {
        .th-category-card__image {
            width: 120px;
        }
    }
    @media (max-width: 991px) and (min-width: 475px) {
        --th-category-card--padding: 20px;

        .th-category-card__image {
            width: 132px;
            align-self: center;
        }
        .th-category-card__content {
            flex-direction: column;
        }
    }
    @media (max-width: 474px) and (min-width: 360px) {
        @include local-th-category-card--horizontal;

        .th-category-card__image {
            width: 132px;
        }

        @media (max-width: 420px) {
            --th-category-card--padding: 24px;

            .th-category-card__image {
                width: 100px;
            }
        }
    }
    @media (max-width: 359px) {
        --th-category-card--padding: 20px;

        @include local-th-category-card--vertical;

        .th-category-card__image {
            width: 132px;
            align-self: center;
        }
        .th-category-card__content {
            flex-direction: column;
        }
    }
}


.th-category-card--layout--overlay {
    .th-category-card__body {
        flex-grow: 1;
        z-index: 0;
        overflow: hidden;
        border-radius: 2.5px;
        padding: 32px 20px 40px;

        &:before {
            position: absolute;
            display: block;
            content: '';
            left: 0; // SKIP-RTL
            right: 0; // SKIP-RTL
            top: 0;
            bottom: 0;
            background-color: $category-card-overlay-color;
            background-image: $category-card-overlay-gradient;
            z-index: -1;
            transition: background-color .2s;
        }
    }

    .th-category-card__overlay-image {
        display: block;
        position: absolute;
        z-index: -3;
        left: -10px; // SKIP-RTL
        right: -10px; // SKIP-RTL
        top: -10px;
        bottom: -10px;
        transition: transform .3s ease-out;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    .th-category-card__overlay-image--blur {
        z-index: -2;
        opacity: 0;
        transition:
            transform .3s ease-out,
            opacity .3s ease-out;

        img {
            filter: blur(3px);
        }
    }

    .th-category-card__content,
    .th-category-card__info {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-height: 100%;
        flex-grow: 1;
        text-align: center;
    }

    .th-category-card__content {
        color: $category-card-overlay-font-color;
    }

    .th-category-card__name,
    .th-category-card__children {
        text-shadow: $category-card-overlay-font-shadow;

        a {
            color: inherit;
            transition: color .12s;
        }
        a:hover {
            @include th-var(color, --th-accent-scheme-main, $category-card-overlay-link-hover-color);
        }
    }

    .th-category-card__name {
        font-size: 24px;
        margin-bottom: 20px;
    }

    .th-category-card__children {
        flex-grow: 1;
        list-style: none;
        padding: 0;
        font-size: 16px;
        margin: 0 0 28px;
        opacity: 1;

        li {
            margin: 0;
            padding: 5px 0 6px;
        }
    }

    // hover
    &:hover {
        .th-category-card__overlay-image {
            transform: scale(1.03);
        }
        .th-category-card__overlay-image--blur {
            opacity: 1;
        }

        .th-category-card__body:before {
            background-color: $category-card-hover-overlay-bg-color;
        }
    }


    @include media-breakpoint-down(sm) {
        .th-category-card__body {
            padding: 22px 28px 28px;
        }
        .th-category-card__content {
            align-items: flex-start;
        }
        .th-category-card__info {
            align-items: flex-start;
            align-content: flex-start;
        }
        .th-category-card__name {
            margin-bottom: 10px;
            font-size: 22px;
        }
        .th-category-card__children {
            display: flex;
            flex-wrap: wrap;
            font-size: 15px;
            margin-bottom: 24px;

            @include direction {
                text-align: $inline-start;
            }

            li {
                padding: 1px 0;

                @include direction {
                    #{$margin-inline-end}: 16px;
                }
            }
        }
    }
}
