/*
// .th-block-posts-carousel
*/

.th-block-posts-carousel {}
.th-block-posts-carousel__carousel {
    .owl-stage-outer {
        margin: -10px;
        padding: 10px;
        position: relative;
    }
    .owl-stage {
        display: flex;
    }
    .owl-item {
        display: flex;
        flex-grow: 0;
        flex-shrink: 0;
    }
}
.th-block-posts-carousel__item {
    display: flex;
    width: 100%;

    .th-post-card {
        width: 100%;
    }
}

.th-block-posts-carousel--layout--grid {
    .th-post-card {
        @include post-card-grid-base;
        @include post-card-grid-xs;
    }
}

.th-block-posts-carousel--layout--list {
    .th-post-card {
        @include media-breakpoint-up(sm) {
            @include post-card-list-base;
        }
        @include media-breakpoint-only(xxl) {
            @include post-card-list-sm;
        }
        @include media-breakpoint-between(lg, xl) {
            @include post-card-list-xs;
        }
        @include media-breakpoint-only(lg) {
            .th-post-card__image {
                width: 210px;
            }
            .th-post-card__title {
                margin-bottom: 0;
            }
            .th-post-card__excerpt {
                display: none;
            }
            .th-post-card__meta {
                margin-bottom: 0;
            }
            .th-post-card__meta-list {
                margin-top: -4px;
            }
            .th-post-card__meta-item {
                margin-top: 4px;
            }
        }
        @include media-breakpoint-only(md) {
            @include post-card-list-sm;
        }
        @include media-breakpoint-only(sm) {
            @include post-card-list-xs;
        }
        @include media-breakpoint-down(xs) {
            @include post-card-grid-base;
            @include post-card-grid-xs;
        }
    }
}
