/*
// .th-post-view
*/

.th-post-view {}
.th-post-view__card {
    @include th-card;

    --th-post-card-padding: 48px;

    position: relative;
    padding: var(--th-post-card-padding);

    &.th-post {
        padding: 0;
    }
}
.th-post-view__card + .th-post-view__card,
.th-post-view__alert {
    margin-top: 24px;
}
.th-post-view__card-title {
    padding: 0 0 8px;
    margin-top: -4px;
    margin-bottom: 40px;
    font-size: 28px;
    line-height: 1.2;
    font-weight: $font-weight-bold;
    border-bottom: 1px solid map_get($card-scheme, divider);
}


@include media-breakpoint-down(sm) {
    .th-post-view__card {
        --th-post-card-padding: 32px;
    }
    .th-post-view__card-title {
        margin-bottom: 28px;
    }
}
@include media-breakpoint-down(xs) {
    .th-post-view__card {
        --th-post-card-padding: 24px;
    }
    .th-post-view__card-title {
        padding: 0 0 4px;
        margin-top: -2px;
        margin-bottom: 24px;
        font-size: 24px;
    }
}
@media (max-width: 419px) {
    .th-post-view__card {
        margin-left: -15px; // SKIP-RTL
        margin-right: -15px; // SKIP-RTL
    }
}
