/*
// .woocommerce-Addresses
*/

.woocommerce-Addresses {
    --th-grid-gutter: 1.5rem;
    --th-grid-columns: 2;

    display: flex;
    flex-wrap: wrap;
    margin-top: calc(var(--th-grid-gutter) * -1);
    margin-left: calc(var(--th-grid-gutter) * -1); // SKIP-RTL

    .woocommerce-Address {
        @include th-card;

        flex: 0 0 calc(100% / var(--th-grid-columns) - var(--th-grid-gutter));
        max-width: calc(100% / var(--th-grid-columns) - var(--th-grid-gutter));
        padding: 0;
        position: relative;
        margin-top: var(--th-grid-gutter);
        margin-left: var(--th-grid-gutter); // SKIP-RTL
    }

    .woocommerce-Address-title {
        padding: .875rem 1.25rem .5rem;
        display: flex;
        justify-content: space-between;
        align-items: center;

        a {
            @include th-button-base;
            @include th-button--style--secondary;
            @include th-button--size--extra-small;
        }
    }

    h3 {
        font-size: 22px;
        margin-bottom: 0;
    }

    address {
        @include th-reset-address;

        padding: .25rem 1.25rem 1rem;
        font-size: 14px;
        color: #6c757d;

        p {
            margin-top: .5rem;
            margin-bottom: 0;
        }
        p + p {
            margin-top: 0;
        }
    }
}

@include media-breakpoint-down(sm) {
    .woocommerce-Addresses {
        --th-grid-columns: 1;
    }
}

