/*
// .th-block-empty
*/

.th-block-empty {}
.th-block-empty__body {
    text-align: center;
}
.th-block-empty__title {
    margin-top: 12px;
    font-size: 36px;
    font-weight: $font-weight-bold;
}
.th-block-empty__message {
    margin-top: 16px;
}
.th-block-empty__action {
    margin-top: 32px;
}

.th-block-empty--after-header {
    .th-block-empty__message {
        margin-top: -20px;
    }

    @include media-breakpoint-down(lg) {
        .th-block-empty__body {
            @include direction {
                text-align: $inline-start;
            }
            .th-block-empty__message {
                margin-top: -8px;
            }
            .th-block-empty__action {
                margin-top: 24px;
            }
        }
    }
}
