/*
// .th-product--layout--quickview
*/

.th-product--layout--quickview {
    display: flex;
    padding: 32px;

    .th-product__gallery {
        width: 320px;
        flex-shrink: 0;
        margin: -4px;

        @include direction {
            #{$margin-inline-end}: 28px;
        }
    }
    .th-product__summary {
        flex-grow: 1;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        align-content: flex-start;
        align-items: center;
    }
    .product_title {
        font-size: 24px;
        line-height: 28px;
        font-weight: $font-weight-bold;
        margin-top: -2px;
        margin-bottom: 0;
        width: 100%;
    }
    .woocommerce-product-rating {
        display: flex;
        margin-top: 6px;
        width: 100%;
    }
    .woocommerce-product-details__short-description {
        margin-top: 10px;
        font-size: 15px;
        line-height: 22px;
        width: 100%;
        margin-bottom: -4px;

        & > :last-child {
            margin-bottom: 0;
        }
    }
    .th-product__meta {
        @include th-reset-table;

        $local-gutter: 6px;

        margin: 12px 0 0;
        width: 100%;

        table {
            display: block;
            margin: -($local-gutter / 2);
        }
        tbody {
            display: flex;
            flex-wrap: wrap;
        }
        th,
        td {
            @include th-reset-table-cell;

            padding: 1px 0;
        }
        tr,
        th,
        td {
            display: block;
        }
        tr {
            background: $quickview-meta-bg-color;
            border-radius: 1.5px;
            margin: ($local-gutter / 2);
            padding-top: 5px;
            padding-bottom: 5px;

            @include direction {
                #{$padding-inline-start}: 7px;
                #{$padding-inline-end}: 14px;
            }
        }
        th {
            font-size: 11px;
            font-weight: $font-weight-normal;
            color: $quickview-meta-title-color;
            line-height: 1;
        }
        td {
            font-size: 13px;
            line-height: 1;
            font-weight: $font-weight-medium;
        }
        a {
            color: inherit;
        }
    }

    .th-product__summary {
        --th-product__summary--padding-x: 0;
    }
    .th-product__summary > .price {
        font-size: 24px;
        padding: 6px 0;
        margin: 12px 0;

        del {
            font-size: 14px;
            margin-bottom: 3px;
        }
    }
    .th-product__stock,
    .woocommerce-variation-availability {
        margin-bottom: 2px;

        @include direction {
            #{$margin-inline-start}: 16px;
        }
    }


    .th-product__summary .variations {
        width: 100%;
        margin-left: 0; // SKIP-RTL
        margin-right: 0; // SKIP-RTL
        padding: 0;

        background: transparent;
        border-top: none;
        border-bottom: none;
        margin-bottom: 20px;
    }
    .woocommerce-variation-price {
        font-size: 24px;

        @include direction {
            #{$margin-inline-end}: 0;
        }

        del {
            font-size: 14px;
            margin-bottom: 3px;
        }
    }
    .woocommerce-variation-availability {}

    .th-product__summary .woocommerce-variation-add-to-cart,
    .th-product__summary .cart {
        .th-quantity {
            @include direction {
                #{$margin-inline-end}: 8px;
            }

            input {
                @include input-nl;
            }
        }
        button[type="submit"] {
            @include th-button--size--normal;

            flex-grow: 0;

            @include direction {
                #{$margin-inline-end}: 8px;
            }
        }
    }


    .th-product__summary .th-wishlist-add,
    .th-product__summary .th-compare-add{
        height: 36px;
        width: 38px;
        display: flex;
        justify-content: center;
        margin: 0;

        @include direction {
            #{$margin-inline-end}: 8px;
        }

        svg {
            margin: 0;
        }

        &__label {
            display: none;
        }
    }

    @media (max-width: 840px) {
        display: block;
        padding: 28px;

        .th-product__gallery {
            width: 100%;
            margin: 0 0 24px;

            .flex-viewport,
            .woocommerce-product-gallery__wrapper {
                max-width: 360px;
                margin: 0 auto;
            }
            .flex-viewport .woocommerce-product-gallery__wrapper {
                max-width: none;
                margin: 0;
            }
        }
    }
    @media (max-width: 474px) {
        padding: 20px;

        .th-product__gallery {
            margin: 0 0 16px;
        }
    }
}
