/*
// .th-document
*/

.th-document {
    max-width: 760px;
    margin: 0 auto;
}
.th-document__header {
    $local-padding: 52px;

    text-align: center;
    padding: #{$local-padding - 8px} 0 #{$local-padding};
}
.th-document__title {
    font-weight: $font-weight-bold;
    line-height: 1.2;
    letter-spacing: .01em;
    margin: 0;
}
.th-document__subtitle {
    font-size: 15px;
    color: map_get($body-scheme, muted);
    margin-top: 8px;
    margin-bottom: -4px;
}
.th-document__content {
    @include th-card;

    padding: 50px;
}

@include media-breakpoint-down(lg) {
    .th-document__header {
        $local-padding: 48px;

        padding: #{$local-padding - 4px} 0 #{$local-padding};
    }
    .th-document__subtitle {
        margin-top: 4px;
    }
}
@include media-breakpoint-down(md) {
    .th-document__content {
        padding: 40px;
    }
}
@include media-breakpoint-down(sm) {
    .th-document__header {
        $local-padding: 40px;

        padding: #{$local-padding - 4px} 0 #{$local-padding};
    }
    .th-document__title {
        font-size: 36px;
    }
    .th-document__content {
        padding: 28px;
    }
}
@include media-breakpoint-down(xs) {
    .th-document__title {
        font-size: 32px;
    }
    .th-document__subtitle {
        font-size: 14px;
    }
    .th-document__content {
        padding: 24px;
    }
}
