/*
// .th-timer
*/

.th-timer {
    display: flex;
}
.th-timer__dots {
    width: 24px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &:before,
    &:after {
        display: block;
        content: '';
        width: 4px;
        height: 4px;
        background: $timer-dots-color;
        border-radius: 2px;
        margin: 4px 0;
    }
}
.th-timer__part {
    border-radius: 2px;
    width: 50px;
    line-height: 1;
    padding: 8px 0;
    text-align: center;

    @include th-var(background, --th-accent-scheme-main, map_get($timer-scheme, main));
    @include th-var(color, --th-accent-scheme-opposite, map_get($timer-scheme, opposite));
}
.th-timer__part-value {
    font-size: 24px;
    font-weight: $font-weight-bold;
    letter-spacing: .04em;
}
.th-timer__part-label {
    font-size: 10px;
    text-transform: uppercase;
    margin-top: 4px;

    @include th-var(color, --th-accent-scheme-muted, map_get($timer-scheme, muted));
}


@include media-breakpoint-down(sm) {
    .th-timer__dots {
        width: 18px;

        &:before,
        &:after {
            width: 3px;
            height: 3px;
            border-radius: 1.5px;
        }
    }
    .th-timer__part {
        width: 40px;
        padding: 5px 0 6px;
    }
    .th-timer__part-value {
        font-size: 20px;
    }
    .th-timer__part-label {
        font-size: 9px;
        margin-top: 3px;
    }
}
