/*
// .woocommerce-order
*/

.woocommerce-order {
    max-width: 930px;
    margin: 0 auto;

    & > * {
        margin-bottom: 24px;
    }
    & > :last-child {
        margin-bottom: 0;
    }

    .woocommerce-thankyou-order-received {
        font-size: 18px;
    }
}
.woocommerce-order-overview {
    @include th-card;

    padding: 16px 0;
    text-align: center;
    display: flex;
    list-style: none;

    li {
        @include th-reset-list-item;

        flex-basis: 25%;
        line-height: 1.25;
        position: relative;
        padding: 2px 12px;
        flex-direction: column;
        font-size: 13px;
        color: #6c757d;

        &:not(:last-child):before {
            position: absolute;
            display: block;
            content: "";
            border-left: 2px dashed rgba(0, 0, 0, .1); // SKIP-RTL
            width: 0;
            height: 100%;
            top: 0;

            @include direction {
                #{$inset-inline-end}: -1px;
            }
        }
    }

    strong {
        display: block;
        font-size: 14px;
        line-height: 16px;
        font-weight: $font-weight-medium;
        margin-top: 4px;
        color: #262626
    }
}
.woocommerce-order-overview__email {
    display: none;
}

@include media-breakpoint-up(xl) {
    .woocommerce-order {
        .woocommerce-thankyou-order-received {
            text-align: center;
            margin-top: -32px;
            margin-bottom: 40px;
        }
    }
}
@include media-breakpoint-down(lg) {
    .woocommerce-order {
        .woocommerce-thankyou-order-received {
            margin-top: -12px;
            margin-bottom: 32px;
        }
    }
}
@include media-breakpoint-down(md) {
    .woocommerce-order-overview {
        flex-wrap: wrap;

        li {
            flex-basis: 50%;

            &:nth-child(2):before {
                display: none;
            }
            &:nth-child(n + 3) {
                margin-top: 16px;
            }
        }
    }
}
