.th-product--layout--sidebar {
    // variables
    --th-product-gutter: 30px;
    --th-product-gallery-image-max-width: 400px;

    // grid
    .th-product__body {
        display: grid;
        grid-template-columns: calc(100% - 370px) 370px;
        grid-template-rows: auto auto 1fr;
    }
    .th-product__card--one {
        grid-column: 1;
        grid-row: 1 / 3;
    }
    .th-product__header {
        grid-column: 1;
        grid-row: 1;
    }
    .th-product__gallery {
        grid-column: 1;
        grid-row: 2;
    }
    .woocommerce-tabs {
        grid-column: 1;
        grid-row: 3;
    }
    .th-product__sidebar {
        grid-column: 2;
        grid-row: 1 / 4;
    }

    // styles
    .th-product__card {
        @include th-card;
    }
    .th-product__card--two {
        display: none;
    }
    .th-product__gallery {
        padding: 0 40px 40px;

        .flex-viewport,
        .woocommerce-product-gallery__wrapper {
            max-width: var(--th-product-gallery-image-max-width);
            margin: 0 auto;
        }
        .flex-viewport .woocommerce-product-gallery__wrapper {
            max-width: none;
            margin: 0;
        }
        .woocommerce-product-gallery {
            --th-gallery-thumbnail-gutter: 10px;
            --th-gallery-thumbnail-columns: 8;

            @include media-breakpoint-down(xl) {
                --th-gallery-thumbnail-columns: 6;
            }
            @include media-breakpoint-down(lg) {
                --th-gallery-thumbnail-columns: 8;
            }
            @include media-breakpoint-down(sm) {
                --th-gallery-thumbnail-columns: 6;
            }
            @media (max-width: 575px) {
                --th-gallery-thumbnail-columns: 5;
                --th-gallery-thumbnail-gutter: 8px;
            }
            @media (max-width: 419px) {
                --th-gallery-thumbnail-columns: 4;
            }
        }
    }
    .th-product__intro {
        display: none;
    }
    .th-product__header {
        padding: 36px 40px 16px;
    }
    .th-product__sidebar {
        @include direction {
            #{$margin-inline-start}: var(--th-product-gutter);
        }
    }
    .th-product__summary {
        @include th-card;
    }
    .woocommerce-tabs {
        margin-top: var(--th-product-gutter);
    }
    .woocommerce-tabs {
        --th-woocommerce-tabs-padding: 36px;

        @include media-breakpoint-down(sm) {
            --th-woocommerce-tabs-padding: 28px;
        }
        @media (max-width: 419px) {
            --th-woocommerce-tabs-padding: 24px;
        }
    }

    @include media-breakpoint-down(xl) {
        .th-product__widgets {
            display: none;
        }
    }


    @include media-breakpoint-between(md, lg) {
        // actions
        $local-size: 40px;
        $local-icon-size: 16px;

        .th-product__header h1 {
            @include direction {
                #{$padding-inline-end}: (52px + 12px + 16px);
            }
        }
    }


    @include media-breakpoint-between(sm, lg) {
        // styles
        .th-product__meta {
            $local-gutter: 20px;

            table,
            th,
            td {
                display: block;
            }
            tbody {
                display: flex;
                flex-wrap: wrap;
            }
            tr,
            th {
                width: calc(50% - #{$local-gutter / 2});
            }
            tr {
                display: flex;

                &:nth-child(2n) {
                    @include direction {
                        #{$margin-inline-start}: $local-gutter;
                    }
                }
            }
        }
    }


    @include media-breakpoint-down(lg) {
        @include th-product--mobile;

        .th-product__summary {
            --th-product__summary--padding-x: 24px;

            padding-top: 24px;
            padding-bottom: 28px;
        }
        .th-product__meta {
            border-top: 1px solid map_get($card-scheme, divider);
            border-bottom: 1px solid map_get($card-scheme, divider);
            padding: 10px 0;
            margin: 0 0 8px;

            th,
            td {
                padding-top: 1px;
                padding-bottom: 1px;
            }
            tr + tr > * {
                border: none;
            }
        }
    }

    @include media-breakpoint-down(xs) {
        // styles
        .th-product__subtitle {
            flex-direction: column;
            align-items: flex-start;
        }
        .th-product__fit {
            order: -1;

            .th-status-badge {
                margin-bottom: 11px;

                @include direction {
                    #{$margin-inline-start}: -4px;
                }
            }
        }
    }


    @include media-breakpoint-down(xl) {
        // variables
        $product-gutter: 20px;
        --th-product-gutter: #{$product-gutter};
        --th-product-gallery-image-max-width: 360px;

        // grid
        .th-product__body {
            grid-template-rows: max-content;
            grid-template-columns: calc(100% - #{312px + $product-gutter}) (312px + $product-gutter);
        }
        .th-product__sidebar {
            grid-column: 2;
            grid-row: 1 / 3;
        }

        // styles
        .th-product__gallery {
            padding-top: 0;
            padding-bottom: 28px;

            @include direction {
                #{$padding-inline-start}: 28px;
                #{$padding-inline-end}: 28px;
            }
        }
        .th-product__header {
            padding-top: 24px;
            padding-bottom: 16px;

            @include direction {
                #{$padding-inline-start}: 28px;
                #{$padding-inline-end}: 28px;
            }
        }
        .woocommerce-tabs {
            grid-column: 1 / 3;
        }
    }


    @include media-breakpoint-down(lg) {
        // variables
        --th-product-gutter: 16px;

        // grid
        .th-product__body {
            grid-template-columns: 100%;
        }
        .th-product__sidebar {
            grid-column: 1;
            grid-row: 3;
        }
        .woocommerce-tabs {
            grid-column: 1;
            grid-row: 4;
        }

        // styles
        .th-product__card--one {
            display: none;
        }
        .th-product__header {
            @include th-card;

            padding-bottom: 24px;
        }
        .th-product__gallery {
            @include th-card;

            width: 100%;
            margin-top: var(--th-product-gutter);
            padding-top: 24px;
            padding-bottom: 28px;

            @include direction {
                #{$padding-inline-start}: 24px;
                #{$padding-inline-end}: 24px;
            }
        }
        .th-product__sidebar {
            margin-top: var(--th-product-gutter);

            @include direction {
                #{$margin-inline-start}: 0;
            }
        }
        .th-product__summary {
            position: static;
        }
    }


    @include media-breakpoint-down(sm) {
        // styles
        .th-product__header {
            @include th-card;

            padding-top: 24px;
            padding-bottom: 22px;

            @include direction {
                #{$padding-inline-start}: 24px;
                #{$padding-inline-end}: 24px;
            }
        }
        .th-product__header h1 {
            font-size: 22px;
            line-height: 28px;
        }
    }
}
