/*
// .th-block-finder
*/

$local-control-gutter: 14px;

.th-block-finder {
    position: relative;
    height: 500px;
    overflow: hidden;
    display: flex;
    align-items: center;
    background: $block-finder-bg-color;

    .th-var-header-layout--spaceship & {
        @include media-breakpoint-up(xl) {
            padding-top: 24px;
        }
    }
}
.th-block-finder__decor {
    position: absolute;
    bottom: -1px;
    z-index: 2;

    @include th-decor-style(map_get($body-scheme, main));
}
.th-block-finder__image {
    position: absolute;
    left: 0; // SKIP-RTL
    right: 0; // SKIP-RTL
    top: 0;
    bottom: 0;
    background-size: cover;
    background-position: center center;
    z-index: 1;
}
.th-block-finder__body {
    color: $block-finder-font-color;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    z-index: 2;
}
.th-block-finder__header {
    margin-bottom: 64px;
}
.th-block-finder__title {
    font-size: 52px;
    font-weight: $font-weight-medium;
    text-shadow: $block-finder-font-shadow;
    margin: 0;
}
.th-block-finder__subtitle {
    margin-top: 4px;
    font-size: 22px;
    text-shadow: $block-finder-font-shadow;
}
.th-block-finder__form {
    background: #fff;
    border-radius: 2px;

    --th-block-finder__form--gutter: 14px;

    display: flex;
    align-self: stretch;
}
.th-block-finder__vehicle-select,
.th-block-finder__button {
    &:focus {
        outline: none;
    }
}
.th-block-finder__vehicle-select {
    --th-vehicle-select--columns: 4;
    --th-vehicle-select--gutter: 0px;

    flex-grow: 1;
}
.th-block-finder__button {
    border-radius: 2px;
    border: none;
    padding: 0 40px;
    font-size: 17px;
    transition:
        background .2s,
        color .2s;

    @include th-var(background-color, --th-btn-context-normal-bg-color, map_get($btn-dark-scheme, normal-bg-color));
    @include th-var(color, --th-btn-context-normal-font-color, map_get($btn-dark-scheme, normal-font-color));

    &:hover {
        @include th-var(background-color, --th-btn-context-hover-bg-color, map_get($btn-dark-scheme, hover-bg-color));
        @include th-var(color, --th-btn-context-hover-font-color, map_get($btn-dark-scheme, hover-font-color));
    }
    &:active {
        @include th-var(background-color, --th-btn-context-active-bg-color, map_get($btn-dark-scheme, active-bg-color));
        @include th-var(color, --th-btn-context-active-font-color, map_get($btn-dark-scheme, active-font-color));
    }

    &:active {
        transition-duration: .1s,  .1s;
    }
    &:focus {
        outline: none;
    }
}


@include media-breakpoint-up(xl) {
    .th-block-finder__form {
        @include direction {
            #{$border-start-end-radius}: 4px;
            #{$border-end-end-radius}: 4px;
        }
    }
    .th-block-finder__button {
        min-width: 15%;

        @include direction {
            #{$border-start-start-radius}: 0;
            #{$border-end-start-radius}: 0;
        }
    }
}
@include media-breakpoint-down(lg) {
    $local-control-width: 260px;

    .th-block-finder__header {
        margin-bottom: 52px;
    }
    .th-block-finder__title {
        font-size: 44px;
        margin-top: -20px;
    }
    .th-block-finder__subtitle {
        font-size: 17px;
    }
    .th-block-finder__form {
        align-self: center;
        flex-wrap: wrap;
        width: ($local-control-width * 2 + $local-control-gutter * 2);
        max-width: 100%;
        justify-content: center;
    }
    .th-block-finder__vehicle-select {
        --th-vehicle-select--columns: 2;
    }
    .th-block-finder__button {
        width: 100%;
        margin: 12px;
        min-height: 48px;
    }
}
@include media-breakpoint-down(sm) {
    .th-block-finder {
        height: auto;
        padding: 48px 8px;
    }
    .th-block-finder__header {
        margin-bottom: 32px;
    }
    .th-block-finder__title {
        margin-top: -12px;
        font-size: 36px;
        line-height: 44px;
    }
    .th-block-finder__subtitle {
        font-size: 16px;
        margin-top: 8px;
    }
    .th-block-finder__form {
        width: 380px;
        max-width: 100%;
    }
    .th-block-finder__vehicle-select {
        --th-vehicle-select--columns: 1;
    }
    .th-block-finder__button {
        margin: 8px;
        font-size: 16px;
    }
}
@media (max-width: 399px) {
    .th-block-finder {
        padding-top: 44px;
        padding-bottom: 26px;
    }
    .th-block-finder__title {
        font-size: 26px;
        line-height: 34px;
    }
    .th-block-finder__subtitle {
        font-size: 15px;
    }
    .th-block-finder__button {
        font-size: 15px;
        min-height: 44px;
    }
}
