/*
// .th-posts-view
*/

.th-posts-view {
    --th-posts-view--gutter: 0;
    --th-posts-view--columns: 1;
}
.th-posts-view__list {
    display: flex;
    flex-wrap: wrap;
    margin-top: calc(var(--th-posts-view--gutter) * -1);
    margin-left: calc(var(--th-posts-view--gutter) * -1); // SKIP-RTL
}
.th-posts-view__item {
    margin-top: var(--th-posts-view--gutter);
    margin-left: var(--th-posts-view--gutter); // SKIP-RTL
    width: calc((100% / var(--th-posts-view--columns)) - var(--th-posts-view--gutter));
}
.th-posts-view__pagination {
    margin-top: 48px;
    display: flex;
    justify-content: center;

    &:empty {
        display: none;
    }

    @include wordpress-pagination;
}
