/*
// .cart_totals
*/

.cart_totals {
    @include th-card;

    padding: 2rem;
    font-size: 15px;
    line-height: 1.2;

    h2 {
        font-size: 24px;
        font-weight: 500;
        padding-bottom: 8px;
        border-bottom: 1px solid #ebebeb;
        margin-bottom: 2rem;
        margin-top: -4px;
        line-height: 1.2;
    }

    table {
        @include th-reset-table;

        width: 100%;
    }

    th,
    td {
        @include th-reset-table-cell;
    }

    table,
    tbody,
    tr {
        display: block;
    }
    th {
        display: none;
    }

    td {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 0;

        @include direction {
            text-align: $inline-end;
        }

        &:before {
            content: attr(data-title);
            display: block;
            font-weight: $font-weight-medium;

            @include direction {
                #{$padding-inline-end}: 1rem;

                text-align: $inline-start;
            }
        }
    }

    tr:not(:last-child):not(:first-child) {
        th,
        td {
            padding-top: .5rem;
        }
    }

    .order-total {
        &:before {
            content: '';
            display: block;
            width: 100%;
            padding-top: 12px;
            margin-top: 12px;
            border-top: 1px solid #ebebeb;
        }

        td {
            font-size: 20px;
        }
        strong {
            font-weight: $font-weight-normal;
        }
    }

    .wc-proceed-to-checkout {
        margin-top: 2rem;

        a {
            display: block;
        }
    }

    .woocommerce-shipping-methods {
        list-style: none;
        padding: 0;
        margin: 0;

        li {
            display: flex;
            align-items: center;
            justify-content: flex-end;
        }

        li + li {
            padding-top: 6px;
        }

        label {
            margin: 0;
        }

        input {
            order: 1;

            @include direction {
                #{$margin-inline-start}: 6px;
            }
        }
    }
    .woocommerce-shipping-destination {
        margin-top: 8px;
        margin-bottom: 0;
        font-size: 14px;
        line-height: inherit;
        color: #6c757d;
        width: 100%;

        strong {
            font-weight: $font-weight-normal;
        }
    }
    .woocommerce-shipping-calculator {
        margin-top: 4px;
        width: 100%;
    }
    .shipping-calculator-button {
        font-size: 14px;

        &:hover {
            text-decoration: underline;
        }
    }
    .shipping-calculator-form {
        @include direction {
            text-align: $inline-start;
        }

        &:before,
        &:after {
            content: '';
            display: block;
        }
        &:before {
            height: .75rem;
        }
        &:after {
            height: 1rem;
        }

        p {
            margin: 0 0 .75rem;
        }

        p:last-of-type {
            margin: 0;
        }

        input,
        button {
            width: 100%;
        }
    }

    @include media-breakpoint-up(sm) {
        .wc-proceed-to-checkout a {
            @include th-button--size--extra-large;
        }
    }

    @include media-breakpoint-down(xs) {
        padding: 1.5rem;

        .wc-proceed-to-checkout a {
            @include th-button--size--large;
        }
    }
}
