/*
// .th-widget-filters
*/

@mixin local-offcanvas() {
    .th-widget-filters__header {
        display: none;
    }
    .th-widget-filters__item {
        padding: 8px 20px;

        &:first-child {
            border-top: none;
        }
    }
}

.th-widget-filters__list {
    margin: -.125rem (-$widget-padding) (-$widget-padding);
}
.th-widget-filters__item {
    padding: 8px $widget-padding;
    margin: 0;
    border-top: 1px solid map_get($card-scheme, divider);
}
.th-widget-filters__actions {
    padding: 20px $widget-padding $widget-padding;

    & > * + * {
        @include direction {
            #{$margin-inline-start}: 8px;
        }
    }
}


.th-widget-filters--offcanvas--none {
    @include th-card;
}
.th-widget-filters--offcanvas--mobile {
    @include media-breakpoint-up(lg) {
        @include th-card;
    }
    @include media-breakpoint-down(md) {
        @include local-offcanvas;
    }
}
.th-widget-filters--offcanvas--always {
    @include local-offcanvas;
}
