/*
// .th-vehicle-picker-modal
*/

.th-vehicle-picker-modal {}
.th-vehicle-picker-modal__panel {
    display: none;
    padding: 32px 32px 36px;
}
.th-vehicle-picker-modal__panel--active {
    display: block;
}
.th-vehicle-picker-modal__header {
    font-size: 24px;
    font-weight: 500;
    padding-bottom: .25rem;
    border-bottom: 1px solid #ebebeb;
    margin-bottom: 1.75rem;
    margin-top: -.5rem;
}
.th-vehicle-picker-modal__footer {
    margin-top: 2.5rem;
    display: flex;
    justify-content: flex-end;

    > *:not(:last-child) {
        @include direction {
            #{$margin-inline-end}: 12px;
        }
    }
}
.th-vehicle-picker-modal__add-button {
    min-height: 44px;
    display: block;
    width: 100%;
}
.th-vehicle-picker-modal__back-to-list {
    .th-garage-empty & {
        display: none;
    }
}
