/*
// .th-vehicle-form
*/

.th-vehicle-form {}
.th-vehicle-form__divider {
    width: 100%;
    line-height: 1;
    font-size: 13px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    color: map_get($card-scheme, muted);
    margin: 12px 0;

    &:before,
    &:after {
        display: block;
        content: '';
        height: 1px;
        flex-grow: 1;
        background: map_get($card-scheme, divider);
    }
    &:before {
        @include direction {
            #{$margin-inline-end}: 6px;
        }
    }
    &:after {
        @include direction {
            #{$margin-inline-start}: 6px;
        }
    }
}


.th-vehicle-form--location--header {
    .th-vehicle-form__vehicle-select {
        --th-vehicle-select--columns: 2;
        --th-vehicle-select--gutter: 12px;
    }
}
.th-vehicle-form--location--account {
    .th-vehicle-form__vehicle-select {
        --th-vehicle-select--columns: 4;
        --th-vehicle-select--gutter: 12px;
    }

    @include media-breakpoint-down(sm) {
        .th-vehicle-form__vehicle-select {
            --th-vehicle-select--columns: 2;
        }
    }

    @media (max-width: 459px) {
        .th-vehicle-form__vehicle-select {
            --th-vehicle-select--columns: 1;
        }
    }
}
.th-vehicle-form--location--modal {
    .th-vehicle-form__vehicle-select {
        --th-vehicle-select--columns: 2;
        --th-vehicle-select--gutter: 12px;
    }

    @media (max-width: 399px) {
        .th-vehicle-form__vehicle-select {
            --th-vehicle-select--columns: 1;
        }
    }
}
