/*
// .th-compare
*/

.th-compare {
    @include th-card;
}
.th-compare__options-list {
    padding: 16px 20px;
    border-bottom: 1px solid map_get($card-scheme, divider);
    font-size: 15px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.th-compare__option {
    display: flex;
    align-items: center;

    & + & {
        @include direction {
            #{$margin-inline-start}: 16px;
        }
    }
}
.th-compare__option-label {
    @include direction {
        #{$margin-inline-end}: 8px;
    }
}
