/*
// .th-block-features
*/

.th-block-features__list {
    @include th-reset-list;
}
.th-block-features__item,
.th-block-features__divider {
    @include th-reset-list-item;
}

.th-block-features--layout--top-strip,
.th-block-features--layout--bottom-strip {
    .th-block-features {}
    .th-block-features__list {
        display: flex;
        list-style: none;
        margin: 0;
        padding: 8px 40px;
    }
    .th-block-features__item {
        display: flex;
        width: calc((100% - 3px) / 4);
        padding: 1.5rem 2.5rem;
        justify-content: center;
    }
    .th-block-features__item-icon {
        @include th-var(fill, --th-theme-scheme-main, $block-features-icon-color);

        @include direction {
            #{$margin-inline-end}: 1.375rem;
        }
    }
    .th-block-features__item-title {
        font-size: 18px;
        font-weight: $font-weight-medium;
    }
    .th-block-features__item-subtitle {
        color: map_get($body-scheme, opposite-alt);
        font-size: 15px;
    }

    @include media-breakpoint-down(xl) {
        .th-block-features__list {
            padding: 0;
        }
        .th-block-features__item {
            padding: 1.5rem 1.5rem;
            justify-content: flex-start;
        }
        .th-block-features__item-title {
            font-size: 16px;
        }
        .th-block-features__item-subtitle {
            font-size: 14px;
        }
    }
    @include media-breakpoint-down(lg) {
        .th-block-features__item {
            padding: 1.375rem .75rem;
        }
        .th-block-features__item-icon {
            @include direction {
                #{$margin-inline-end}: 1rem;
            }
        }
    }
    @include media-breakpoint-only(md) {
        .th-block-features__list {
            padding: 4px 0;
        }
        .th-block-features__item {
            flex-direction: column;
            text-align: center;
            justify-content: center;
        }
        .th-block-features__item-icon {
            margin-bottom: 12px;

            @include direction {
                #{$margin-inline-end}: 0;
            }
        }
    }
    @include media-breakpoint-down(sm) {
        .th-block-features__list {
            flex-wrap: wrap;
            padding: 16px 0;
        }
        .th-block-features__item {
            width: 50%;
            padding: .75rem 1.25rem;
        }
    }
    @media (max-width: 575px) and (min-width: 400px) {
        .th-block-features__item {
            flex-direction: column;
            text-align: center;
            justify-content: center;
        }
        .th-block-features__item-icon {
            margin-bottom: 12px;

            @include direction {
                #{$margin-inline-end}: 0;
            }
        }
    }
    @media (max-width: 399px) {
        .th-block-features__item {
            width: 100%;
            padding: .675rem 1.25rem;
        }
        .th-block-features__item-icon {
            @include direction {
                #{$margin-inline-end}: 18px;
            }
        }
    }
}

.th-block-features--layout--top-strip {
    .th-block-features__list {
        border-bottom: 2px solid map_get($body-scheme, divider);
    }
}
.th-block-features--layout--bottom-strip {
    .th-block-features__list {
        border-top: 2px solid map_get($body-scheme, divider);
    }
}

@mixin local-layout($columns) {
    .th-block-features__item {
        width: calc((100% - #{($columns - 1) * 1px}) / #{$columns});
    }
    .th-block-features__divider {
        width: 1px;

        &:nth-child(#{$columns * 2}n) {
            width: 100%;
            height: 1px;
        }
        &:nth-child(#{$columns * 2}n):last-child {
            display: none;
        }
    }
}

.th-block-features--layout--widget {
    .th-block-features__list {
        display: flex;
        flex-wrap: wrap;
        margin: -10px 0;
        list-style: none;
        padding: 0;
    }
    .th-block-features__divider {}
    .th-block-features__item {
        padding: 10px 4px;
        display: flex;
        align-items: center;
    }
    .th-block-features__item-icon {
        @include th-var(color, --th-theme-scheme-main, $shop-features-icon-color);

        @include direction {
            #{$margin-inline-end}: 18px;
        }

        svg {
            $local-size: 38px;

            width: $local-size;
            height: $local-size;
            fill: currentColor;
            vertical-align: middle;
        }
    }
    .th-block-features__item-title {
        font-size: 14px;
        font-weight: $font-weight-medium;
    }
    .th-block-features__item-subtitle {
        font-size: 14px;
        color: map_get($card-scheme, opposite-alt);
        margin-top: -2px;
    }

    @include local-layout(1);
}


