/*
// .th-alert
*/

@mixin th-alert {
    .woocommerce-error,
    .woocommerce-message,
    .woocommerce-info,
    .th-alert {
        @content;
    }
}
@mixin th-alert--style--primary {
    .woocommerce-message,
    .th-alert--style--primary {
        @content;
    }
}
@mixin th-alert--style--info {
    .woocommerce-info,
    .th-alert--style--info {
        @content;
    }
}
@mixin th-alert--style--danger {
    .woocommerce-error,
    .th-alert--style--danger {
        @content;
    }
}

@include th-alert {
    --th-alert-padding-x: 1.25rem;
    --th-alert-padding-y: 1rem;
    --th-alert-padding-button: 1rem;

    width: 100%;
    padding: var(--th-alert-padding-y) var(--th-alert-padding-x);
    border-radius: 2px;
    font-size: 15px;
    line-height: 20px;
    margin: 0 0 1.5rem;
    list-style: none;

    .button {
        @include th-button--size--small;
        @include th-button--style--ghost-dark;

        &:first-child {
            @include direction {
                #{$margin-inline-end}: .5rem;
            }
        }
    }

    a {
        box-decoration-break: clone;
        color: currentColor;
        background: rgba(#fff, .5);
        border-radius: 1.5px;
        padding: 0 3px;
        transition: background-color .12s ease-in-out;

        &:hover {
            background: rgba(#fff, .85);
        }
        &:active {
            background: rgba(#fff, .5);
            transition-duration: 0s;
        }
    }
}
@include th-alert--style--primary {
    background: #b2d9ff;
    color: #36414d;
}
@include th-alert--style--info {
    background: #b8e2e5;
    color: #2d3e40;
}
@include th-alert--style--danger {
    background: #ffb8b8;
    color: #473333;
}

.th-alert--size--sm {
    --th-alert-padding-x: .75rem;
    --th-alert-padding-y: .5rem;

    font-size: 15px;
}

@media (max-width: 419px) {
    @include th-alert {
        --th-alert-padding-x: 1rem;
        --th-alert-padding-y: .75rem;
        margin-bottom: 1rem;

        .button {
            @include th-button--size--extra-small;
        }
    }
}
