/*
// .widget_layered_nav_filters
*/

$local-gutter: 4px;

.widget_layered_nav_filters {
    ul {
        list-style: none;
        padding: 0;
        margin: -($local-gutter / 2);
        display: flex;
        flex-wrap: wrap;
    }
    li {
        margin: ($local-gutter / 2);
    }
    a {
        padding: 4px 11px;
        display: block;
        height: 21px;
        font-size: 13px;
        line-height: 1;
        color: inherit;
        background-color: $applied-filters-normal-bg-color;
        border-radius: (23px / 2);
        position: relative;
        border: none;
        transition:
            background .12s,
            color .12s;

        @include direction {
            #{$padding-inline-end}: (10px + 11px + 9px + 3px);
        }

        &:focus {
            outline: none;
        }
        &:hover {
            background-color: $applied-filters-hover-bg-color;
            color: inherit;
        }

        &:before {
            content: '';
            display: block;
            position: absolute;
            width: 1px;
            top: 4px;
            bottom: 4px;
            background-color: $applied-filters-divider-color;

            @include direction {
                #{$inset-inline-end}: (10px + 9px + 6px);
            }
        }

        &:after {
            @include cross-9-light($applied-filters-cross-color);

            position: absolute;
            content: '';
            width: 9px;
            height: 9px;
            top: 6px;

            @include direction {
                #{$inset-inline-end}: 10px;
            }
        }
    }
}
