/*
// .woocommerce-cart-form
*/

.woocommerce-cart-form {
    a {
        color: inherit;
        transition: color .15s;
    }
    a:hover {
        @include th-var(color, --th-link-color, $link-color);
    }

    .remove {
        font-size: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        color: transparent;
        position: relative;

        &:after {
            @include cross-12(#000);

            position: relative;
            display: block;
            content: '';
            width: 12px;
            height: 12px;
            opacity: .25;
            z-index: 1;
            transition: opacity .12s ease-in-out;
        }
    }

    table {
        @include th-reset-table;
    }

    th,
    td {
        @include th-reset-table-cell;
    }

    @include media-breakpoint-up(lg) {
        --th-table-padding-x: 24px;
        --th-cell-padding-x: 12px;
        --th-cell-padding-y: 14px;

        @include th-card;

        line-height: 1.25;

        table {
            width: 100%;
        }

        thead {
            font-size: 13px;
            text-transform: uppercase;
        }

        th,
        td {
            padding: var(--th-cell-padding-y) var(--th-cell-padding-x);

            &:first-child {
                @include direction {
                    #{$padding-inline-start}: var(--th-table-padding-x);
                }
            }
            &:last-child {
                @include direction {
                    #{$padding-inline-end}: var(--th-table-padding-x);
                }
            }
        }

        tbody {
            th,
            td {
                border-top: 1px solid #ebebeb;
            }
        }

        .product-remove {
            width: 1px;
        }

        .product-thumbnail {
            width: 1px;

            img {
                max-width: 80px;
                height: auto;
                display: block;
            }
        }
        .product-price,
        .product-subtotal {
            @include direction {
                text-align: $inline-end;
            }
        }
        .product-quantity {
            text-align: center;
            width: 150px;

            @include direction {
                #{$padding-inline-start}: 36px;
            }
        }

        .actions {
            --th-cell-padding-y: 20px;

            text-align: justify;
        }

        .coupon {
            display: inline-flex;
            vertical-align: middle;
            width: 360px;

            label {
                @include sr-only;
            }
            input {
                width: 0;
                flex-grow: 1;

                @include direction {
                    #{$margin-inline-end}: 8px;
                }
            }
        }

        .actions > button[type="submit"] {
            @include direction {
                float: $inline-end;
            }
        }

        .remove {
            $local-size: 24px;
            $local-expand: 4px;

            width: $local-size;
            height: $local-size;

            @include direction {
                #{$margin-inline-end}: -8px;
            }

            &:before {
                position: absolute;
                content: '';
                left: -$local-expand; // SKIP-RTL
                right: -$local-expand; // SKIP-RTL
                top: -$local-expand;
                bottom: -$local-expand;
                border-radius: 1000px;
                transition: background-color .12s ease-in-out;
            }

            &:hover:before {
                background: rgba(#000, .08);
            }
            &:hover:after {
                opacity: .4;
            }
            &:active:before {
                background: rgba(#000, .14);
                transition-duration: 0s;
            }
        }
    }

    @include media-breakpoint-down(md) {
        --th-cart-columns: 3;
        --th-cart-gutter: 16px;

        thead {
            display: none;
        }

        table {
            display: block;
        }

        tbody {
            display: flex;
            flex-wrap: wrap;
            margin: calc(var(--th-cart-gutter) / 2 * -1);
        }

        tr {
            width: 100%;
            position: relative;
        }
        td {
            padding: 0;
        }

        .woocommerce-cart-form__cart-item {
            @include th-card;

            display: flex;
            flex-direction: column;
            flex-shrink: 0;
            flex-basis: calc((100% / var(--th-cart-columns)) - var(--th-cart-gutter));
            margin: calc(var(--th-cart-gutter) / 2);
            max-width: calc((100% / var(--th-cart-columns)) - var(--th-cart-gutter));
        }

        .product-thumbnail {
            padding: 16px 14px;

            img {
                max-width: 100%;
                height: auto;
                display: block;
                margin: 0 auto;
            }
        }
        .product-name {
            padding: 0 14px 14px;
            flex-grow: 1;
            text-align: center;
            line-height: 1.375;
        }

        .product-price,
        .product-quantity,
        .product-subtotal {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 10px 14px;
            border-top: 1px solid #ebebeb;
            font-size: 15px;

            @include direction {
                text-align: $inline-end;
            }

            &:before {
                content: attr(data-title);
                font-weight: $font-weight-medium;

                @include direction {
                    #{$margin-inline-end}: 16px;
                    text-align: $inline-start;
                }
            }
        }

        .product-quantity {
            .th-quantity {
                max-width: 120px;
            }
        }

        .remove {
            width: 32px;
            height: 32px;
            background-color: #fff;
            position: absolute;
            top: 0;
            transition: background-color .12s ease-in-out;

            @include direction {
                #{$inset-inline-end}: 0;
                #{$border-end-start-radius}: 2px;
            }

            &:hover {
                background: #ebebeb;
            }
            &:hover:after {
                opacity: .4;
            }

            &:active {
                background: #dbdbdb;
                transition-duration: 0s;
            }
        }

        .actions {
            @include th-card;

            display: flex;
            justify-content: space-between;
            margin: calc(var(--th-cart-gutter) / 2);
            margin-top: calc(1.25rem - var(--th-cart-gutter) / 2);
            padding: 1rem;
        }

        .coupon {
            display: flex;
            max-width: 360px;
            flex-grow: 1;

            @include direction {
                #{$margin-inline-end}: 8px;
            }

            label {
                @include sr-only;
            }
            input {
                flex-grow: 1;
                flex-shrink: 1;
                flex-basis: 0;
                width: 0;

                @include direction {
                    #{$margin-inline-end}: 8px;
                }
            }
        }

        .actions > button[type="submit"] {
            @include direction {
                float: $inline-end;
            }
        }
    }
    @include media-breakpoint-down(sm) {
        --th-cart-columns: 2;
    }
    @media (max-width: 519px) {
        .actions {
            flex-wrap: wrap;

            &:before {
                display: block;
                content: '';
                border-bottom: 1px solid #ebebeb;
                width: calc(100% + 2rem);
                margin: 1rem -1rem;
            }
        }
        .coupon {
            order: -1;
            width: 100%;
            max-width: none;
        }
    }
    @media (max-width: 474px) {
        --th-cart-columns: 1;
    }
}
.cart-collaterals {
    display: none;
}
