/*
// .th-sidebar
*/

$local-transition-duration: .25s;
$local-transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);

@mixin local-offcanvas() {
    visibility: hidden;
    transition: visibility 0s $local-transition-duration;

    .th-sidebar__backdrop {
        @include th-modal-backdrop;
    }
    .th-sidebar__body {
        @include th-modal-sidebar-body;

        height: 100%;
        display: flex;
        flex-direction: column;
    }
    .th-sidebar__content {
        flex-grow: 1;
        overflow-y: auto;
        overflow-x: hidden;
    }
    &.th-sidebar--open {
        visibility: visible;
        transition-delay: 0s;

        .th-sidebar__backdrop {
            opacity: 1;
        }
        .th-sidebar__body {
            transform: translateX(0); // SKIP-RTL
        }
    }
    .th-sidebar__header {
        display: flex;
        align-items: center;
        border-bottom: 1px solid map_get($card-scheme, divider);
        flex-shrink: 0;
    }
    .th-sidebar__title {
        flex-grow: 1;
        font-size: 15px;
        font-weight: $font-weight-medium;
        padding: 0 20px;
    }
    .th-sidebar__close {
        width: 48px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: none;
        fill: currentColor;
        padding: 0 0 2px;
        flex-shrink: 0;
        transition:
            background-color .15s,
            color .15s;

        @include define-button-scheme($btn-muted-scheme);

        &:focus {
            outline: none;
        }
    }
    .th-sidebar__widget:not(.widget_redparts_sputnik_filters) {
        display: none;
    }
    .th-widget__title {
        display: none;
    }
    .th-sidebar__widget {
        padding-top: 0;
        box-shadow: none;
    }
    .th-widget-filters__list {
        margin-top: 0;
    }
    .th-widget-filters__item:first-child {
        border-top: none;
    }
}

.th-sidebar {}
.th-sidebar__widget + .th-sidebar__widget {
    margin-top: 20px;
}
.th-sidebar__widget {
    @include th-card;

    padding: $widget-padding;
}
.th-sidebar__widget-title {
    font-size: 20px;
    line-height: 1.2;
    font-weight: $font-weight-medium;
    margin-top: -.25rem;
    margin-bottom: ($widget-padding - .125rem);
}


.th-sidebar__header {
    display: none;
}

.th-sidebar--offcanvas--mobile {
    @include media-breakpoint-down(md) {
        @include local-offcanvas;
    }
}
.th-sidebar--offcanvas--always {
    @include local-offcanvas;
}
