@mixin el-product-card__addtocart($states...) {
    @if (length($states) == 0) {
        $states: ('',);
    }

    @for $i from 0 to length($states) {
        $state: nth($states, $i + 1);

        @if ($state == normal) {
            $state: '';
        }

        @if ($state != '') {
            $state: ':' + $state;
        }

        .th-product-card__addtocart#{$state} .button {
            @content;
        }
    }
}
@mixin el-product-card__addtocart--loading($states...) {
    @if (length($states) == 0) {
        $states: ('',);
    }

    @for $i from 0 to length($states) {
        $state: nth($states, $i + 1);

        @if ($state == normal) {
            $state: '';
        }

        @if ($state != '') {
            $state: ':' + $state;
        }

        .th-product-card__addtocart#{$state} .button.loading {
            @content;
        }
    }
}

@mixin product-card__addtocart-full-base {
    @include el-product-card__addtocart {
        background: map_get($btn-primary-scheme, normal-bg-color);
        color: map_get($btn-primary-scheme, normal-font-color);
        border: none;
        border-radius: 2px;
        font-weight: $font-weight-medium;
        transition:
            background-color .15s,
            color .15s;

        &:focus {
            outline: none;
        }
        &:hover {
            background: map_get($btn-primary-scheme, hover-bg-color);
            color: map_get($btn-primary-scheme, hover-font-color);
        }
        &:active {
            background: map_get($btn-primary-scheme, active-bg-color);
            color: map_get($btn-primary-scheme, active-font-color);
        }
    }
}

@mixin product-card__addtocart-icon-base {
    $local-expand: 4px;

    .th-product-card__addtocart {
        position: relative;

        .added_to_cart {
            position: absolute;
            left: -($local-expand / 2); // SKIP-RTL
            right: -($local-expand / 2); // SKIP-RTL
            top: -($local-expand / 2);
            bottom: -($local-expand / 2);
            opacity: 0;
        }
    }

    @include el-product-card__addtocart {
        position: relative;
        border: none;
        padding: 8px;
        border-radius: 2px;
        background: transparent;
        color: map_get($addtocart-button-scheme, default-font-color);
        z-index: 0;
        transition: color .2s;

        span {
            display: none;
        }

        svg {
            display: block;
            fill: currentColor;
        }

        &:before {
            top: -($local-expand / 2);
            left: -($local-expand / 2); // SKIP-RTL
            display: block;
            content: '';
            position: absolute;
            width: calc(100% + #{$local-expand});
            height: calc(100% + #{$local-expand});
            background: map_get($addtocart-button-scheme, default-bg-color);
            z-index: -1;
            border-radius: 50%;
            transform: scale(0);
            transition:
                transform .2s,
                background .2s;
        }
        &:focus {
            outline: none;
        }

        &:not(.ajax_add_to_cart) {
            .th-icon-cart {
                display: none;
            }
        }

        &.ajax_add_to_cart {
            &:not(.added) {
                .th-icon-cart-added {
                    display: none;
                }
            }
            &.added {
                .th-icon-cart {
                    display: none;
                }
            }
        }
    }

    &:hover,
    &:active {
        @include el-product-card__addtocart {
            @include th-var(color, --th-btn-primary-normal-font-color, map_get($addtocart-button-scheme, normal-font-color));

            &:before {
                @include th-var(background, --th-btn-primary-normal-bg-color, map_get($addtocart-button-scheme, normal-bg-color));

                transform: scale(1);
            }
        }
        @include el-product-card__addtocart(hover) {
            @include th-var(color, --th-btn-primary-hover-font-color, map_get($addtocart-button-scheme, hover-font-color));

            &:before {
                @include th-var(background, --th-btn-primary-hover-bg-color, map_get($addtocart-button-scheme, hover-bg-color));
            }
        }
        @include el-product-card__addtocart(active) {
            @include th-var(color, --th-btn-primary-active-font-color, map_get($addtocart-button-scheme, active-font-color));

            &:before {
                @include th-var(background, --th-btn-primary-active-bg-color, map_get($addtocart-button-scheme, active-bg-color));

                transition-duration: .05s;
            }
        }
    }

    @include el-product-card__addtocart--loading {
        cursor: default;

        &:after {
            @include th-loader(22px, 2px, map_get($addtocart-button-scheme, normal-font-color));
        }
    }
    &,
    &:hover,
    &:active {
        @include el-product-card__addtocart--loading(normal, hover, active) {
            color: transparent;

            &:before {
                @include th-var(background, --th-btn-primary-normal-bg-color, map_get($addtocart-button-scheme, normal-bg-color));

                transform: scale(1);
            }
        }
    }
}

@mixin product-card-base() {
    @include th-card;

    @include el-product-card-action {
        position: relative;
        display: block;
        padding: 8px;
        margin: 0;
        border: none;
        transition:
            color .08s,
            background .08s;

        @include define-button-scheme($btn-muted-scheme);

        svg {
            fill: currentColor;
            display: block;
        }

        &:focus {
            outline: none;
        }
        &:active {
            transition-duration: 0s;
        }
        &:last-child {
            @include direction {
                #{$border-end-start-radius}: 2px;
            }
        }
    }
    @include el-product-card-action--loading {
        &,
        &:hover {
            cursor: default;
            background: map_get($btn-muted-scheme, normal-bg-color);
            color: transparent;
            transition-duration: 0s;
        }

        &:before {
            @include th-loader(16px, 2px, map_get($btn-muted-scheme, loader-color));
        }
    }
    @include el-product-card-action__label {
        position: absolute;
        pointer-events: none;
        opacity: 0;
    }
    .th-product-card__image {
        display: block;

        img {
            max-width: 100%;
            height: auto;
        }
    }
    .th-product-card__name h2 {
        font-size: inherit;
        font-weight: inherit;
        line-height: inherit;
        margin: 0;
    }
    .th-product-card__rating {
        color: map_get($card-scheme, opposite-alt);
        font-size: .8125rem;

        a,
        a:hover {
            color: inherit;
        }
        a:hover {
            text-decoration: underline;
        }
    }
    .th-product-card__features {
        color: map_get($card-scheme, opposite-alt);
        font-size: 13px;
        line-height: 21px;
        list-style: none;
        padding: 0;
        margin: 0;

        li {
            margin: 0;
            position: relative;

            @include direction {
                #{$padding-inline-start}: 12px;
            }

            &:before {
                display: block;
                position: absolute;
                content: '';
                width: 3px;
                height: 3px;
                border-radius: 1.5px;
                background: currentColor;
                top: 9px;
                opacity: .7;

                @include direction {
                    #{$inset-inline-start}: 3px;
                }
            }
        }
    }
    .th-product-card__name {
        a {
            color: inherit;
        }
        a:hover {
            text-decoration: none;
        }
    }
    .th-product-card__meta {
        font-size: 12px;
        line-height: 1;
        color: map_get($card-scheme, muted);
    }
    .th-product-card__prices {
        font-weight: $font-weight-medium;
        letter-spacing: -.04em;
        line-height: 1;
    }
    @include el-product-card__price--new {
        text-decoration: none;
    }
    @include el-product-card__price--old {
        font-weight: $font-weight-normal;
        text-decoration: line-through;
    }
    @include el-product-card__wishlist-and-compare {
        position: relative;
        border: none;
        background: map_get($wishlist-button-scheme, normal-bg-color);
        color: map_get($wishlist-button-scheme, normal-font-color);
        fill: map_get($wishlist-button-scheme, normal-icon-color);
        border-radius: 2px;
        transition:
            background .15s,
            fill .15s;

        &:focus {
            outline: none;
        }
        &:hover {
            background: map_get($wishlist-button-scheme, hover-bg-color);
            fill: map_get($wishlist-button-scheme, hover-icon-color);
        }
        &:active {
            background: map_get($wishlist-button-scheme, active-bg-color);
            fill: map_get($wishlist-button-scheme, active-icon-color);
        }
    }
    @include el-product-card__wishlist-and-compare--loading {
        background: map_get($wishlist-button-scheme, normal-bg-color);
        fill: transparent;
        cursor: default;

        &:after {
            @include th-loader(16px, 2px, map_get($btn-muted-scheme, loader-color));
        }
    }
}


@mixin product-card-grid-base() {
    @include product-card__addtocart-icon-base;

    @include el-product-card__wishlist-and-compare {
        display: none;
    }

    $local-padding: 16px;
    $local-actions-transition-duration: .2s;
    $local-actions-transition-delay: .05s;

    display: flex;
    flex-direction: column;
    position: relative;

    &:after {
        display: block;
        position: static;
        content: '';
        order: 1;
        flex-grow: 1;
    }

    .th-product-card__badges {
        position: absolute;
        top: $local-padding;
        display: flex;
        z-index: 1;
        flex-direction: column;
        align-items: flex-start;

        @include direction {
            #{$inset-inline-start}: $local-padding;
        }

        & > * + * {
            margin-top: 3px;
        }
    }
    .th-product-card__actions-list {
        position: absolute;
        overflow: hidden;
        z-index: 1;

        @include direction {
            #{$inset-inline-end}: 0;
        }
    }
    .th-product-card__image {
        display: block;
        position: relative;
    }
    .th-product-card__fit {
        position: absolute;
        bottom: 8px;

        @include th-status-badge-hide-text;

        @include direction {
            #{$inset-inline-start}: calc(#{$local-padding} - 3px);
        }
    }
    .th-product-card__meta {
        padding: 6px $local-padding 0;

        &:empty {
            display: none;
        }
    }
    .th-product-card__name {
        padding: 6px $local-padding 0;
        line-height: 1.1875;
    }
    .th-product-card__rating {
        margin: -3px 0 -2px 0;
        padding: 8px $local-padding 0;
        display: flex;
        flex-wrap: wrap;
    }
    .th-product-card__rating-stars,
    .th-product-card__rating-label {
        margin: 3px 0 2px 0;
    }
    .th-product-card__rating-stars {
        @include direction {
            #{$margin-inline-end}: 7px;
        }
    }
    .th-product-card__rating-label {
        line-height: 1;
    }
    .th-product-card__features {
        padding: 10px $local-padding 0;
        margin-bottom: -2px;
        display: none;
    }
    .th-product-card__footer {
        padding: $local-padding;
        align-items: center;
        display: flex;
        order: 2;
    }
    .th-product-card__prices {
        flex-grow: 1;
        font-size: 18px;
    }
    @include el-product-card__prices-list {
        display: flex;
        align-items: flex-end;
        flex-wrap: wrap;
    }
    @include el-product-card__price--new {
        @include direction {
            #{$margin-inline-end}: 6px;
        }
    }
    @include el-product-card__price--old {
        font-size: 14px;

        @include direction {
            #{$margin-inline-end}: 6px;
        }
    }
    .th-product-card__addtocart {
        margin: -3px;
    }

    @media (hover: hover) {
        @include el-product-card-action--wishlist-and-compare {
            transition:
                transform $local-actions-transition-duration,
                opacity $local-actions-transition-duration,
                color .12s,
                background .12s;
            opacity: 0;
            will-change: transform;

            @include direction {
                transform: translateX(#{100% * $transform-direction}) translateX(6px); // SKIP-RTL
            }
        }

        &:hover,
        &:active {
            @include el-product-card-action--wishlist-and-compare {
                transform: none;
                opacity: 1;
            }
        }
    }
}


