    .th-product-card__prices {
        padding: 28px 0;
        font-size: 20px;
        display: flex;
        align-items: flex-end;
        flex-wrap: wrap;
        justify-content: center;
    }
    @include el-product-card__price--old {
        font-size: 15px;

        @include direction {
            #{$margin-inline-start}: 6px;
        }
    }
    .th-product-card__addtocart {
        width: 100%;
        margin-bottom: 10px;

        .added_to_cart {
            display: block;
        }
    }
    @include el-product-card__wishlist-and-compare {
        font-size: 13px;
        line-height: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 7px 10px 5px;

        svg {
            position: relative;
            top: -1px;

            @include direction {
                #{$margin-inline-end}: 8px;
            }
        }
    }
    @include el-product-card__wishlist-and-compare__icon {
        order: -1;
    }
    @include el-product-card__wishlist {
        svg {
            @include direction {
                #{$margin-inline-start}: -3px;
                #{$margin-inline-end}: 7px;
            }
        }
    }
    @include el-product-card__compare {
        svg {
            @include direction {
                #{$margin-inline-start}: -2px;
            }
        }
    }
    @include el-product-card__wishlist--loading {
        &:after {
            @include direction {
                #{$inset-inline-start}: 7px;
                #{$inset-inline-end}: auto;
            }
        }
    }
    @include el-product-card__compare--loading {
        &:after {
            @include direction {
                #{$inset-inline-start}: 8px;
                #{$inset-inline-end}: auto;
            }
        }
    }
}
@mixin product-card-list-desktop-sm() {
    $local-image-width: 192px;
    $local-image-padding-x: 16px;
    $local-footer-width: 180px;
    $local-footer-padding-x: 16px;

    .th-product-card__image {
        width: $local-image-width;
        padding-left: $local-image-padding-x; // SKIP-RTL
        padding-right: $local-image-padding-x; // SKIP-RTL
    }
    .th-product-card__fit {
        @include direction {
            #{$inset-inline-start}: $local-image-width - 2px;
        }
    }
    .th-product-card__footer {
        width: $local-footer-width;
        padding: 0 $local-footer-padding-x (39px + 10px);
    }
    .th-product-card__prices {
        padding: 24px 0;
    }
    .th-product-card__meta {
        position: absolute;
        bottom: 0;
        width: ($local-footer-width - $local-footer-padding-x * 2);

        @include direction {
            #{$inset-inline-end}: $local-footer-padding-x;
        }
    }
    @include el-product-card__wishlist-and-compare {
        margin-top: 1px;
    }
}


@mixin product-card-list-mobile-base() {
    flex-direction: column;

    .th-product-card__image {
        padding: 20px;
        width: 100%;
        display: flex;
        position: relative;

        .image {
            width: 180px;
            max-width: 100%;
        }
    }
    .th-product-card__fit {
        position: absolute;
        bottom: 14px;
        display: flex;

        @include direction {
            #{$inset-inline-start}: 15px;
            #{$inset-inline-end}: 15px;
        }
    }
    .th-product-card__badges {
        top: 16px;

        @include direction {
            #{$inset-inline-start}: 16px;
        }
    }
    .th-product-card__info {
        padding: 0 20px 20px;
    }
    .th-product-card__features {
        padding-top: 12px;
    }
    .th-product-card__footer {
        border-top: 1px solid map_get($card-scheme, divider);
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        padding-left: 20px; // SKIP-RTL
        padding-right: 20px; // SKIP-RTL
        padding-bottom: 20px;
    }
    &.th-product-card--has-meta {
        .th-product-card__footer {
            padding-bottom: (38px + 20px);
        }
    }
    .th-product-card__prices {
        width: 100%;
        padding-top: 20px;
        padding-bottom: 16px;
        display: flex;
        align-items: flex-end;
        flex-wrap: wrap;
    }
    @include el-product-card__price--old {
        font-size: 15px;

        @include direction {
            #{$margin-inline-start}: 6px;
        }
    }
    .th-product-card__addtocart {
        @include direction {
            #{$margin-inline-end}: 8px;
        }
    }
    @include el-product-card__addtocart {
        height: 30px;
        padding: 6px 16px;
        font-size: 15px;
    }
    .added_to_cart {
        height: 30px;
        padding: 6px 16px;
        font-size: 15px;
    }
    @include el-product-card__wishlist-and-compare {
        height: 30px;
        width: 32px;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
            @include direction {
                #{$margin-inline-start}: 0;
                #{$margin-inline-end}: 0;
            }
        }
    }
    @include el-product-card__wishlist-and-compare__label {
        display: none;
    }
    .th-product-card__meta {
        position: absolute;
        bottom: 0;
        left: 0; // SKIP-RTL
        right: 0; // SKIP-RTL
        padding: 0 20px;
        height: 38px;
        line-height: 36px;
        border-top: 1px solid map_get($card-scheme, divider);

        &:empty {
            display: none;
        }
    }
}



@mixin product-card-layout-list() {
    @include product-card-list-base;

    @include media-breakpoint-up(md) {
        @include product-card-list-desktop-base;
    }

    @include media-breakpoint-between(md, lg) {
        @include product-card-list-desktop-sm;
    }

    @include media-breakpoint-down(sm) {
        @include product-card-list-mobile-base;
    }
}



@mixin product-card-table-base() {
    .th-product-card__fit,
    .th-product-card__features,
    .th-product-card__actions-list,
    .th-product-card__addtocart-full {
        display: none;
    }

    @include product-card__addtocart-icon-base;

    @include el-product-card__wishlist-and-compare {
        display: none;
    }

    display: flex;
    align-items: center;

    .th-product-card__badges {
        position: relative;
        top: -2px;
        display: inline-block;
        vertical-align: middle;

        & > * {
            display: inline-block;
            vertical-align: middle;

            @include direction {
                #{$margin-inline-end}: 3px;
            }
        }

        @include direction {
            #{$inset-inline-start}: 0;
            #{$margin-inline-end}: 2px;
        }

        &:empty {
            display: none;
        }
    }
    .th-tag-badge {
        padding: 3px 5px;
        height: 15px;
        font-size: 10px;

        &:before {
            transform: none;
            border-radius: 1.5px;

            @include direction {
                #{$inset-inline-start}: 0;
                #{$inset-inline-end}: 0;
            }
        }
    }
}


@mixin product-card-table-desktop-base() {
    @include direction {
        #{$padding-inline-start}: 16px;
        #{$padding-inline-end}: 0;
    }

    .th-product-card__image {
        width: 90px;
        flex-shrink: 0;

        @include direction {
            #{$margin-inline-end}: 16px;
        }
    }
    .th-product-card__info {
        flex-grow: 1;
        display: flex;
        align-self: stretch;
        margin: 16px 0;
    }
    .th-product-card__meta {
        font-size: 13px;

        &:empty:before {
            display: block;
            content: '';
            height: 1px;
            width: 16px;
            background: currentColor;
            opacity: .5;
        }
    }
    .th-product-card__meta-title {
        display: none;
    }
    .th-product-card__name {
        flex-grow: 1;
        display: flex;
        align-items: center;
        font-size: 15px;
        line-height: 18px;
    }
    .th-product-card__rating {
        padding: 0 16px;
        display: flex;
        flex-wrap: wrap;
        margin-top: 4px;
    }
    .th-product-card__rating-label {
        margin-top: 3px;
    }
    .th-product-card__footer {
        display: flex;
        align-items: center;
        align-self: stretch;
        flex-shrink: 0;
        margin: 16px 0;

        @include direction {
            #{$border-inline-start}: 1px solid map_get($card-scheme, divider);
            #{$padding-inline-start}: 16px;
        }
    }
    .th-product-card__prices {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        flex-grow: 1;
        flex-shrink: 0;
        justify-content: center;
        align-self: stretch;
        align-items: flex-end;

        width: 120px;
        font-size: 15px;

        @include direction {
            #{$border-inline-end}: 1px solid map_get($card-scheme, divider);
            #{$padding-inline-end}: 16px;
        }
    }
    @include el-product-card__prices-list {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-end;
        width: 100%;
    }
    @include el-product-card__price--old {
        font-size: 14px;

        @include direction {
            #{$margin-inline-start}: 6px;
        }
    }
    @include el-product-card__price--old {
        display: block;
        margin-top: 4px;

        @include direction {
            #{$margin-inline-start}: 0;
        }
    }
    @include el-product-card__addtocart {
        margin: 0 16px;
    }
    .th-product-card__actions-list {
        padding: 0;
        display: none;
    }
}


@mixin product-card-table-mobile-base() {
    @include th-card;

    flex-wrap: wrap;
    align-items: stretch;
    padding-top: 0;
    padding-bottom: 0;

    @include direction {
        #{$padding-inline-start}: 0;
        #{$padding-inline-end}: 0;
    }

    .th-product-card__image {
        padding: 4px;
        margin: 0 8px;
        width: 60px;
    }
    .th-product-card__info {
        order: 3;
        width: 100%;
        margin: 0;
        padding: 14px;
        border-top: 1px solid map_get($card-scheme, divider);

        @include direction {
            #{$border-inline-start}: none;
        }
    }
    .th-product-card__meta {
        &:not(:empty) {
            margin-bottom: 6px;
        }
    }
    .th-product-card__name {
        line-height: 20px;

        .woocommerce-loop-product__title {
            display: inline;
        }
    }
    .th-product-card__rating {
        margin: -3px 0 -2px 0;
        padding-top: 5px;
        display: flex;
        flex-wrap: wrap;
    }
    .th-product-card__rating-stars,
    .th-product-card__rating-label {
        margin: 3px 0 2px 0;
    }
    .th-product-card__rating-stars {
        @include direction {
            #{$margin-inline-end}: 7px;
        }
    }
    .th-product-card__rating-label {
        line-height: 1;
    }
    .th-product-card__footer {
        display: flex;
        align-items: center;
        flex-grow: 1;
        flex-shrink: 0;
    }
    .th-product-card__prices {
        display: flex;
        flex-direction: column;
        justify-content: center;
        flex-grow: 1;
        padding: 12px 14px;
        height: 100%;

        @include direction {
            #{$border-inline-start}: 1px solid map_get($card-scheme, divider);
            #{$border-inline-end}: 1px solid map_get($card-scheme, divider);
        }
    }
    @include el-product-card__price--old {
        font-size: 14px;
        margin-top: 4px;
    }
    @include el-product-card__addtocart {
        margin: 10px 18px;
        flex-shrink: 0;
        align-self: center;
    }
}



@mixin product-card-layout-table() {
    @include product-card-table-base;

    @include media-breakpoint-up(sm) {
        @include product-card-table-desktop-base;
    }
    @include media-breakpoint-up(xl) {
        .th-product-card__name {
            padding: 0 18px;

            @include direction {
                #{$border-inline-start}: 1px solid map_get($card-scheme, divider);
            }
        }
        .th-product-card__rating {
            width: 140px;
            padding-top: 0;
            flex-shrink: 0;
            margin-top: 0;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            @include direction {
                #{$border-inline-start}: 1px solid map_get($card-scheme, divider);
            }
        }
        .th-product-card__meta {
            width: 140px;
            flex-shrink: 0;
            display: flex;
            align-items: center;
            justify-content: center;

            @include direction {
                #{$border-inline-start}: 1px solid map_get($card-scheme, divider);
            }
        }
    }
    @include media-breakpoint-only(xl) {
        .th-product-card__image {
            width: 60px;

            @include direction {
                #{$margin-inline-end}: 16px;
            }
        }
    }
    @include media-breakpoint-between(sm, lg) {
        .th-product-card__info {
            flex-direction: column;
            justify-content: center;
            padding: 0 16px;

            @include direction {
                #{$border-inline-start}: 1px solid map_get($card-scheme, divider);
            }
        }
        .th-product-card__name {
            padding: 0;
            flex-grow: 0;
        }
        .th-product-card__rating {
            margin: -3px 0 -2px 0;
            padding: 5px 0 0;
            display: flex;
            flex-wrap: wrap;
        }
        .th-product-card__rating-stars,
        .th-product-card__rating-label {
            margin: 3px 0 2px 0;
        }
        .th-product-card__rating-stars {
            @include direction {
                #{$margin-inline-end}: 7px;
            }
        }
        .th-product-card__rating-label {
            line-height: 1;
        }
        .th-product-card__meta {
            padding: 0 0 5px;

            &:empty {
                display: none;
            }
        }
        .th-product-card__meta-title {
            display: inline;
        }
    }
    @include media-breakpoint-only(sm) {
        .th-product-card__image {
            width: 60px;

            @include direction {
                #{$margin-inline-end}: 16px;
            }
        }
        .th-product-card__prices {
            width: 90px;
        }
    }

    @include media-breakpoint-down(xs) {
        @include product-card-table-mobile-base;
    }
}
