/*
// .woocommerce-reviews
*/

.woocommerce-Reviews {
    .commentlist {
        @include th-reset-list;

        list-style: none;
        padding: 0;
        margin: 0;

        li {
            @include th-reset-list-item;
        }
        li + li {
            margin-top: 18px;
            padding-top: 24px;
            border-top: 1px solid map_get($card-scheme, divider);
        }
        li:last-child {
            padding-bottom: 18px;
            border-bottom: 1px solid map_get($card-scheme, divider);
        }
    }
    .comment_container {
        position: relative;

        .avatar {
            position: absolute;
            width: 42px;
            height: 42px;
            border-radius: 2px;
        }

        .comment-text {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;
        }
        .star-rating {
            order: 1;
        }
        .description {
            width: 100%;
            order: 2;
            font-size: 15px;
            line-height: 1.5;
            margin-top: 12px;

            & > :last-child {
                margin-bottom: 0;
            }
        }
        .meta {
            @include th-reset-paragraph;

            display: flex;
            flex-direction: column;
            justify-content: center;
            min-height: 42px;
            margin-bottom: 0;

            @include direction {
                #{$margin-inline-start}: (42px + 12px);
            }
        }
        .woocommerce-review__author {
            font-size: 15px;
            line-height: 18px;
            font-weight: $font-weight-bold;
            margin-top: 2px;
        }
        .woocommerce-review__dash {
            display: none;
        }
        .woocommerce-review__published-date {
            font-size: 13px;
            color: map_get($card-scheme, muted);
        }
    }

    .woocommerce-pagination {
        margin-top: 20px;

        .page-numbers {
            justify-content: flex-start;
        }
    }

    .comment-respond {
        max-width: 580px;
    }
    .comment-reply-title {
        display: block;
        margin-bottom: 20px;
        font-size: 28px;
        font-weight: $font-weight-medium;
        line-height: 1.2;
    }

    #comments + #review_form_wrapper {
        margin-top: 48px;
    }

    .comment-form .form-submit {
        margin-top: 1rem;
    }
    .comment-form .comment-notes,
    .comment-form .logged-in-as {
        margin-top: 0;
    }

    .comment-form-rating {
        $local-gutter: 5px;
        $local-extra-size: 16px;

        .stars {
            display: flex;

            span {
                display: flex;
            }

            a {
                position: relative;
                display: block;
                width: 13px + $local-extra-size;
                height: 12px + $local-extra-size + 1px;
                color: transparent;
                box-shadow: inset 0 0 0 1px #d9d9d9;
                border-radius: 2px;
                transition: background-color .2s;

                &::before {
                    left: ($local-extra-size / 2); // SKIP-RTL
                    top: ($local-extra-size / 2);

                    @include svg-pseudo-element(13px, 12px);
                    @include start-13x12($rating-star-default-bg-color, $rating-star-default-border-color);
                }

                &::after {
                    position: absolute;
                    display: block;
                    content: '';
                    width: 100%;
                    height: 100%;
                    top: 0;

                    @include direction {
                        #{$inset-inline-end}: 0;
                    }
                }

                &:active {
                    transition-duration: 0s;
                    background-color: rgba(#ced4da, .2);
                }
            }

            a + a {
                &::after {
                    width: calc(100% + #{$local-gutter});
                }

                @include direction {
                    #{$margin-inline-start}: $local-gutter;
                }
            }
        }

        .stars.selected a,
        .stars span:hover a {
            &::before {
                @include start-13x12($rating-star-active-bg-color, $rating-star-active-border-color);
            }
        }

        .stars a:hover ~ a,
        .stars.selected span:not(:hover) a.active ~ a {
            &::before {
                @include start-13x12($rating-star-default-bg-color, $rating-star-default-border-color);
            }
        }
    }
}
.woocommerce-noreviews {
    @include th-reset-paragraph;

    margin-bottom: 0;
}
.woocommerce-Reviews-title {
    display: none;
}
