/*
// .th-block-products-carousel
*/

$local-carousel-expand: 10px;

.th-block-products-carousel {}
.th-block-products-carousel__carousel {
    position: relative;
    display: flex;

    .owl-stage-outer {
        margin: -$local-carousel-expand;
        padding: $local-carousel-expand;
        position: relative;
    }
    .owl-stage {
        display: flex;
    }
    .owl-item {
        flex-shrink: 0;
        display: flex;
    }
}
.th-block-products-carousel__carousel-loader {
    @include th-loader-overlay(map_get($body-scheme, main), $local-carousel-expand, $local-carousel-expand, 100px, 2px, $body-loader-color);

    z-index: 2;
    visibility: hidden;
    opacity: 0;
    transition:
        opacity .3s,
        visibility 0s .3s;
}
.th-block-products-carousel__carousel--loading {
    .th-block-products-carousel__carousel-loader {
        visibility: visible;
        transition-delay: 0s;
        opacity: 1;
    }
}
.th-block-products-carousel__column {
    display: flex;
    flex-direction: column;
    width: 100%;
}
.th-block-products-carousel__cell {
    @include th-reset-list;

    display: flex;
    width: 100%;
    flex-basis: 100%;
    flex-grow: 1;
    margin: 0;
    padding: 0;
}
.th-block-products-carousel__item {
    @include th-reset-list-item;

    display: flex;
    width: 100%;
    flex-basis: 100%;
    flex-grow: 1;
}


.th-block-products-carousel[data-layout="grid-4"],
.th-block-products-carousel[data-layout="grid-5"] {
    .th-block-products-carousel__cell + .th-block-products-carousel__cell {
        margin-top: 20px;
    }
}
.th-block-products-carousel[data-layout="grid-6"] {
    .th-block-products-carousel__cell + .th-block-products-carousel__cell {
        margin-top: 16px;
    }
}
.th-block-products-carousel[data-layout|="horizontal"] {
    .th-block-products-carousel__cell + .th-block-products-carousel__cell {
        margin-top: 14px;
    }
}
