/*
// .woocommerce-pagination
*/

.woocommerce-pagination {
    ul {
        display: flex;
        justify-content: center;
        width: 100%;
        margin: -3px;
        list-style: none;
        padding: 0;

        li {
            @include th-reset-list-item;

            display: flex;
            align-items: center;
        }
        .page-numbers {
            display: block;
        }

        @include wordpress-pagination-base;
    }
}
