/*
// .th-cart
*/

.th-cart {
    display: grid;
    grid-template-columns: 1fr 50px 360px;
    grid-template-rows: auto auto;

    .woocommerce-cart-form {
        grid-row: 1;
        grid-column: 1;
    }
    .cross-sells {
        grid-row: 2;
        grid-column: 1;

        margin-top: 2rem;
    }
    .cart_totals {
        grid-row: 1 / 3;
        grid-column: 3;
        align-self: self-start;
    }

    .cross-sells {
        --th-products-list-columns: 4;
        --th-products-list-gutter: 16px;

        h2 {
            font-size: 24px;
            line-height: 1.2;
            margin-bottom: .875rem;
        }

        .th-products-list__head {
            display: none;
        }
        .th-products-list__content {
            margin: calc(var(--th-products-list-gutter) / 2 * -1);
        }
        .th-products-list__item {
            margin: calc(var(--th-products-list-gutter) / 2);
            width: calc((100% - (var(--th-products-list-columns) * var(--th-products-list-gutter))) / var(--th-products-list-columns));
        }
        .th-products-list__item:nth-child(n + 5) {
            display: none;
        }
    }

    @include media-breakpoint-only(xl) {
        grid-template-columns: 1fr 2.5rem 400px;
        grid-template-rows: auto 2.5rem auto;

        .woocommerce-cart-form {
            grid-row: 1;
            grid-column: 1 / 4;
        }
        .cross-sells {
            grid-row: 3;
            grid-column: 1;

            margin-top: 0;
        }
        .cart_totals {
            grid-row: 3;
            grid-column: 3;
        }

        .cross-sells {
            --th-products-list-columns: 3;
            --th-products-list-gutter: 16px;

            .th-products-list__item:nth-child(n + 4) {
                display: none;
            }
        }
    }

    @include media-breakpoint-only(lg) {
        grid-template-columns: 1fr;
        grid-template-rows: auto auto auto;

        .woocommerce-cart-form {
            grid-row: 1;
            grid-column: 1;
        }

        .cross-sells {
            grid-row: 2;
            grid-column: 1;

            margin-top: 2rem;
        }
        .cart_totals {
            grid-row: 3;
            grid-column: 1;

            margin-top: 2.5rem;
            max-width: 400px;
        }

        .cross-sells {
            --th-products-list-columns: 4;
            --th-products-list-gutter: 16px;

            .th-products-list__item:nth-child(n + 5) {
                display: none;
            }
        }
    }

    @include media-breakpoint-down(md) {
        display: block;

        .cross-sells {
            --th-products-list-columns: 3;
            --th-products-list-gutter: 16px;

            .th-products-list__item:nth-child(n + 4) {
                display: none;
            }
        }
        .cart_totals {
            margin-top: 2.5rem;
            max-width: 400px;
        }
    }
    @include media-breakpoint-down(sm) {
        .cross-sells {
            display: none;
        }
        .cart_totals {
            max-width: none;
        }
    }
}
