/*
// .th-vehicle-select
*/

.th-vehicle-select {
    --th-vehicle-select--columns: 1;
    --th-vehicle-select--gutter: 0px;
}
.th-vehicle-select__body {
    display: flex;
    flex-wrap: wrap;
    margin-left: calc(var(--th-vehicle-select--gutter) * -1); // SKIP-RTL
    margin-top: calc(var(--th-vehicle-select--gutter) * -1);
}
.th-vehicle-select__item {
    position: relative;
    flex-grow: 1;
    margin-left: var(--th-vehicle-select--gutter); // SKIP-RTL
    margin-top: var(--th-vehicle-select--gutter);
    flex-basis: calc((100% / var(--th-vehicle-select--columns)) - var(--th-vehicle-select--gutter));
    width: calc((100% / var(--th-vehicle-select--columns)) - var(--th-vehicle-select--gutter));
}
.th-vehicle-select__item-loader {
    @include th-loader-overlay(#ebebeb, 0px, 0px, 20px, 2px, #000);

    pointer-events: none;
    opacity: 0;
    transition: opacity .2s ease-in-out;

    &:before {
        opacity: 1;
        border-radius: 2.5px;
    }
}
.th-vehicle-select__item--loading {
    .th-vehicle-select__item-loader {
        pointer-events: all;
        opacity: 1;
    }
}


.th-vehicle-select--location--block-finder {
    $local-disabled-opacity: .6;

    --th-vehicle-select__item--padding-x: 24px;
    --th-vehicle-select__item--padding-y: 18px;
    --th-vehicle-select__item--gutter: 4px;
    --th-vehicle-select__item-title--font-size: 15px;
    --th-vehicle-select__item-title--line-height: 20px;
    --th-vehicle-select__item-value--font-size: 17px;
    --th-vehicle-select__item-value--line-height: 20px;

    .th-vehicle-select__item {
        min-width: 0;
        position: relative;

        &:before {
            content: attr(data-label);
            display: block;
            color: #6c757d;
            font-size: var(--th-vehicle-select__item-title--font-size);
            line-height: var(--th-vehicle-select__item-title--line-height);
            position: absolute;
            pointer-events: none;
            top: var(--th-vehicle-select__item--padding-y);
            transition:
                opacity .25s ease-in-out,
                color .25s ease-in-out;

            @include direction {
                #{$inset-inline-start}: var(--th-vehicle-select__item--padding-x);
            }
        }

        &--disabled {
            &:before {
                opacity: $local-disabled-opacity;
            }
        }

        select {
            width: 100%;
            height: 100%;
            border: none;
            appearance: none;
            cursor: pointer;
            padding-top: 0;
            padding-bottom: 0;

            &:disabled {
                cursor: default;
                color: #6c757d;
                font-weight: $font-weight-normal;
                opacity: $local-disabled-opacity;
            }
        }
        select,
        .select2-selection--single {
            margin: 0;
            height: auto;
            border-radius: 0;
            background-color: transparent;
            background-repeat: no-repeat;
            background-size: 11px 7px;
            background-image: url(svg-select-alt-arrow(rgba(#6c757d, .5)));
            transition:
                background-color .1s ease-in-out,
                opacity .25s ease-in-out,
                color .25s ease-in-out;

            @include direction {
                background-position: $inline-end calc(var(--th-vehicle-select__item--padding-x) - 4px) center;
                text-align: $inline-start;
            }

            &:focus {
                outline: none;
            }

            &:hover {
                background-color: rgba(#6c757d, .06);
            }
        }
        select,
        .select2-selection--single .select2-selection__rendered {
            color: #262626;
            line-height: var(--th-vehicle-select__item-value--line-height);
            font-size: var(--th-vehicle-select__item-value--font-size);
            font-weight: $font-weight-medium;
            padding:
                calc(
                    var(--th-vehicle-select__item--padding-y) +
                    var(--th-vehicle-select__item--gutter) +
                    var(--th-vehicle-select__item-title--line-height)
                )
                var(--th-vehicle-select__item--padding-x)
                var(--th-vehicle-select__item--padding-y);
            transition: inherit;

            @include direction {
                #{$padding-inline-start}: var(--th-vehicle-select__item--padding-x);
                #{$padding-inline-end}: calc(11px + (var(--th-vehicle-select__item--padding-x) - 4px) * 2);
            }
        }
        .select2-selection--single {
            display: flex;
            align-items: center;
        }
        .select2-selection__arrow {
            display: none;
        }
        .select2-selection--single {
            border: none;
        }
        .select2-container--disabled {
            opacity: $local-disabled-opacity;

            .select2-selection--single {
                background-color: transparent;
            }
            .select2-selection__rendered {
                color: #6c757d;
                font-weight: $font-weight-normal;
            }
        }
        .select2-container {
            width: 100%;
            height: 100%;
        }
    }
    .th-vehicle-select__item-loader {
        &:before {
            background: #fff;
        }
    }

    @include media-breakpoint-up(xl) {
        .th-vehicle-select__item + .th-vehicle-select__item {
            @include direction {
                #{$border-inline-start}: 1px solid rgba(#000, .1);
            }
        }
    }
    @include media-breakpoint-between(md, lg) {
        --th-vehicle-select__item--padding-x: 20px;
        --th-vehicle-select__item--padding-y: 14px;
        --th-vehicle-select__item--gutter: 4px;
        --th-vehicle-select__item-title--font-size: 14px;
        --th-vehicle-select__item-title--line-height: 18px;
        --th-vehicle-select__item-value--font-size: 15px;
        --th-vehicle-select__item-value--line-height: 18px;

        border-bottom: 1px solid rgba(#000, .1);

        .th-vehicle-select__item:nth-child(2n + 2) {
            @include direction {
                #{$border-inline-start}: 1px solid rgba(#000, .1);
            }
        }
        .th-vehicle-select__item:nth-child(n + 3) {
            border-top: 1px solid rgba(#000, .1);
        }
    }

    @include media-breakpoint-down(sm) {
        --th-vehicle-select__item--padding-x: 16px;
        --th-vehicle-select__item--padding-y: 12px;

        .th-vehicle-select__item {
            border-bottom: 1px solid rgba(#000, .1);
        }
    }
}
